import React, { FC } from 'react';

import { browserVendor } from '~/configs/config';
import { useTranslation } from '~/libs/i18n';
import { BrowserVendors } from '~/types/browsers';

import { Alert } from './Alert';

interface AllowAccessAlertProps {
  onRequestGlobalPermissions: () => void;
}

export const AllowAccessAlert: FC<AllowAccessAlertProps> = ({ onRequestGlobalPermissions }) => {
  const { t } = useTranslation();
  const copyProps = {
    globalRequest: {
      title: t('additionalPermissionNeedAcrossTheWeb'),
      description: t('checkBrowserWindowForPrompt'),
    },
    fallbackRequest: {
      title: t('enableAcrossMultipleSites'),
      description: t('allowToucanToWorkOnTopSites'),
    },
  };
  const currentCopyProps =
    browserVendor === BrowserVendors.Safari ? copyProps.fallbackRequest : copyProps.globalRequest;

  return <Alert {...currentCopyProps} cta={t('allowAccess')} onClick={onRequestGlobalPermissions} />;
};
