/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import Config from '~/configs/config';
import { useTranslation } from '~/libs/i18n';

const { ToucanIcon, Link } = ToucanComponents;

interface HeaderProps {
  isUserRegistered: boolean;
}

export const Header: FC<HeaderProps> = ({ isUserRegistered }) => {
  const { t } = useTranslation();
  const theme = useTheme();

  return (
    <SlideHeader
      titleRightContent={
        isUserRegistered && (
          <Link
            variant="xs"
            href={`${Config.siteURL}/dashboard/settings`}
            css={css`
              display: flex;
              align-items: center;
            `}
          >
            {t('editAccount')}
            <ToucanIcon
              icon="link-out"
              css={css`
                color: inherit;
                width: 12px;
                height: 12px;
                margin-left: ${theme.spacing(1)};
              `}
            />
          </Link>
        )
      }
      isBackButtonVisible={false}
    >
      {t('settings')}
    </SlideHeader>
  );
};
