import React from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { omit } from 'lodash';

import { Box, BoxProps } from '~/components/Box';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { Language } from '~/constants/language';

const { useColorScheme, Typography, ProgressBar } = ToucanComponents;

export const HomonymCard = ({
  sourcePhrase,
  targetPhrase,
  progressionScore,
  languageKey,
  ...boxProps
}: {
  sourcePhrase: string;
  targetPhrase: string;
  progressionScore: number;
  languageKey?: Language;
} & BoxProps) => {
  const { isDarkMode } = useColorScheme();
  const isLearned = progressionScore === 100;
  const textColor = isDarkMode ? ToucanColors.white : ToucanColors.gray[600];
  const bgColor = isDarkMode ? ToucanColors.gray[500] : ToucanColors.white;

  const barCompleteTransitionColor = isLearned ? 'linear-gradient(90deg, #85C3D8 0%, #B1D6BF 100%)' : undefined;

  return (
    <Box
      position="relative"
      {...omit(boxProps, 'targetLanguage', 'refetch', 'translationId', 'fluentAt', 'practicedToday')}
      data-testid="word-list-card"
      pt={2}
      px={2}
    >
      <Box bgcolor={bgColor} borderRadius="8px" p={2} display="flex" flexDirection="column">
        <Box display="flex" justifyContent="space-between" width="100%" flex="0">
          {languageKey && (
            <Box pt={1} display="flex" alignItems="flex-start">
              <AudioPlayer
                targetPhrase={targetPhrase}
                language={languageKey}
                onPlay={() => {
                  // TODO progress on play?
                }}
                marginRight={1}
              />
            </Box>
          )}
          <Box flex="1">
            <Typography variant="sm" color={textColor} isBold>
              {targetPhrase}
            </Typography>
            <Typography variant="xs" color={textColor}>
              {sourcePhrase}
            </Typography>
          </Box>
        </Box>

        <Box pt={2} flex="0">
          <ProgressBar
            barCompleteTransitionOptions={{ duration: 0 }}
            beginPercentage={progressionScore}
            endPercentage={progressionScore}
            barColor={isLearned ? ToucanColors.green[300] : ToucanColors.blue[200]}
            barCompleteTransitionColor={barCompleteTransitionColor}
            onAnimationComplete={async () => {}}
          />
        </Box>
      </Box>
    </Box>
  );
};
