/** @jsx jsx */
import React from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme, Box } from '@mui/material';

import { SidebarItemSeparated } from '~/components/Sidebar/SidebarItem';
import { ExtraPracticeExplanationTrigger } from '~/components/Sidebar/content/ExtraPracticeExplanation';
import { PremiumLockedBadge } from '~/components/shared/PremiumLockedBadge';
import { SubscriptionRedirectContainer } from '~/components/shared/SubscriptionRedirectContainer';
import { FeatureName } from '~/constants/features';
import { Pinchpoints } from '~/constants/pinchpoints';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useSubscription } from '~/hooks/useSubscription';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useToggleContextlessLearningMomentMutation } from '~/schema/background';

const { Typography, Switch, useColorScheme, Button, ToucanIcon } = ToucanComponents;

interface ContextlessLearningMomentProps {
  isInPopup: boolean;
}

export const ContextlessLearningMoment = ({ isInPopup }: ContextlessLearningMomentProps) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { isContextlessLearningMomentEnabled } = useUserInfo();
  const { textLevel1, textLevel3 } = useColorScheme();
  const [toggleContextlessLearningMoment] = useToggleContextlessLearningMomentMutation();
  const { openSubscribePage, isPremiumSubscribed } = useSubscription();
  const { getFeaturePaywallStatus } = usePremiumExperiment();
  const isFeatureBehindPaywall =
    !isPremiumSubscribed &&
    getFeaturePaywallStatus(PremiumFeatureName.ExtraPractice) === PremiumFeaturePaywallStatus.Paid;
  const isModalEnabled = useFeatureEnabled(FeatureName.ExtraPracticeExplanationModal);

  const onChangeEnabledState = () => {
    toggleContextlessLearningMoment({ enabled: !isContextlessLearningMomentEnabled });
    ContentMessengers.trackEvent({
      eventName: 'Contextless learning moment settings toggle',
      properties: {
        enabled: !isContextlessLearningMomentEnabled,
      },
    });
  };

  return (
    <SidebarItemSeparated
      leftContent={
        <React.Fragment>
          <Typography
            variant="sm"
            isBold
            css={css`
              color: ${isFeatureBehindPaywall ? textLevel3 : textLevel1};
            `}
          >
            {t('extraPractice')}
          </Typography>
          {isModalEnabled && !isInPopup && !isFeatureBehindPaywall && (
            <ExtraPracticeExplanationTrigger triggerText={t('whatIsThis')} />
          )}
          {isFeatureBehindPaywall && (
            <Box display="flex" alignItems="center" gap={1}>
              <PremiumLockedBadge data-testid="settings-panel-clm-premium-badge" />
              <Typography variant="xs">
                <Button
                  type="text"
                  onClick={() => openSubscribePage({ pinchpoint: Pinchpoints.ExtraPracticeSettings })}
                  css={css`
                    display: flex;
                    align-items: center;
                    gap: ${theme.spacing(0.5)};
                  `}
                >
                  {t('startFreeTrialFirstLetterCapitalized')}
                  <ToucanIcon
                    icon="chevron-right"
                    css={css`
                      width: 10px;
                      color: inherit;
                    `}
                  />
                </Button>
              </Typography>
            </Box>
          )}
        </React.Fragment>
      }
      rightContent={
        <SubscriptionRedirectContainer
          shouldRedirect={isFeatureBehindPaywall}
          pinchpoint={Pinchpoints.ExtraPracticeSettings}
        >
          <Switch
            value="switch"
            isChecked={isFeatureBehindPaywall ? false : isContextlessLearningMomentEnabled}
            isDisabled={isFeatureBehindPaywall}
            onChange={onChangeEnabledState}
            css={css`
              margin-right: 0;
            `}
          />
        </SubscriptionRedirectContainer>
      }
    />
  );
};
