import React, { FC } from 'react';

import { css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { HexagonalImage } from '~/components/shared/HexagonalImage';
import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

import { Box } from '../../../Box';

const { useColorScheme } = ToucanComponents;

interface LearningShortcutBannerProps {
  isShortcutAvailable: boolean;
  heroImage: string;
  badgeImage: string;
}

export const LearningShortcutBanner: FC<LearningShortcutBannerProps> = ({
  isShortcutAvailable,
  heroImage,
  badgeImage,
}) => {
  const { isDarkMode, backgroundBase } = useColorScheme();
  const heroBanner = isShortcutAvailable
    ? heroImage
    : browser.runtime.getURL(`/images/learningShortcuts/no-selection-hero-${isDarkMode ? 'dark' : 'light'}.svg`);
  const { t } = useTranslation();

  return (
    <Box
      display="flex"
      flexDirection="column"
      alignItems="center"
      sx={{
        backgroundImage: `url(${heroBanner})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        height: '162px',
      }}
      justifyContent="center"
      position="relative"
    >
      <Box flex={1} display="flex" justifyContent="center" alignItems="flex-end" zIndex={2}>
        {isShortcutAvailable && (
          <Box marginBottom="-56px">
            <HexagonalImage imageUrl={badgeImage} width={100} height={100} borderColor={backgroundBase} />
          </Box>
        )}
      </Box>
      <Image
        src={`/images/learningShortcuts/arc-overlay-${isDarkMode ? 'dark' : 'light'}.svg`}
        alt={t('arcOverlay')}
        css={css`
          width: 100%;
          z-index: 1;
        `}
      />
    </Box>
  );
};
