/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

const { Typography, Button, useColorScheme } = ToucanComponents;

interface AlertProps {
  onClick?: () => void;
  onSecondaryClick?: (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent> | React.KeyboardEvent<HTMLButtonElement>,
  ) => void;
  title: React.ReactNode;
  description?: React.ReactNode;
  cta?: string;
  secondaryCta?: string;
  testidPrefix?: string;
}

export const Alert: FC<AlertProps> = ({
  onClick = () => {},
  title,
  description,
  cta,
  secondaryCta,
  onSecondaryClick,
  testidPrefix,
}) => {
  const theme = useTheme();
  const { textAlert, uiAlert } = useColorScheme();

  return (
    <Box
      role="button"
      css={css`
        width: 100%;
        background-color: ${uiAlert};
        padding: ${theme.spacing(2)} ${theme.spacing(4)};
        position: relative;
        z-index: 3;
        text-align: left;
        cursor: ${!cta || secondaryCta ? 'default' : 'pointer'};
      `}
      tabIndex={0}
      onKeyDown={() => {}}
      onClick={!secondaryCta ? onClick : () => {}}
    >
      <Typography
        data-testid={testidPrefix ? `${testidPrefix}-title` : null}
        variant="xs"
        isBold
        css={css`
          color: ${textAlert};
        `}
      >
        {title}
      </Typography>
      {description && (
        <Typography
          variant="xs"
          css={css`
            margin-top: ${theme.spacing(0.5)};
            color: ${textAlert};
          `}
        >
          {description}
        </Typography>
      )}
      <Box display="flex" flexDirection="row">
        {cta && (
          <Button
            data-testid={testidPrefix ? `${testidPrefix}-button` : null}
            onClick={e => {
              e.stopPropagation();
              onClick();
            }}
            onKeyDown={onClick}
            type="text"
            css={css`
              cursor: pointer;
              margin-top: ${theme.spacing(1)};
              font-size: 14px;
            `}
          >
            {cta}
          </Button>
        )}
        {onSecondaryClick && secondaryCta && (
          <Button
            data-testid={testidPrefix ? `${testidPrefix}-secondary-button` : null}
            onClick={onSecondaryClick}
            onKeyDown={onSecondaryClick}
            type="text"
            css={css`
              cursor: pointer;
              margin-top: ${theme.spacing(1)};
              margin-left: ${theme.spacing(2)};
              text-decoration: none !important;
              font-size: 14px;
            `}
          >
            {secondaryCta}
          </Button>
        )}
      </Box>
    </Box>
  );
};
