/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { useTheme } from '@mui/material';

interface SectionItemSeparatorProps {
  spacing?: number;
}

export const SectionItemSeparator: FC<SectionItemSeparatorProps> = ({ spacing = 2 }) => {
  const theme = useTheme();
  return (
    <hr
      css={css`
        background-color: transparent;
        width: 100%;
        height: ${theme.spacing(spacing)};
        border: 0;
        margin: 0;
      `}
    />
  );
};
