/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme, Box } from '@mui/material';

import { SidebarItem } from '~/components/Sidebar/SidebarItem';
import { Pinchpoints } from '~/constants/pinchpoints';
import { DensityValues, DensityValuesPrimitive } from '~/constants/settings';
import { useSubscription } from '~/hooks/useSubscription';
import { useTranslation } from '~/libs/i18n';

import { DensityOptionOld } from './DensityOptionOld';

const { Typography, useColorScheme } = ToucanComponents;

interface TranslationDifficultySelectorProps {
  densityLevel: DensityValuesPrimitive;
  onChangeDensityLevel: (level: DensityValuesPrimitive) => Promise<void>;
  isUserAccountPremium: boolean;
}

export const DifficultySelectorOld: FC<TranslationDifficultySelectorProps> = ({
  densityLevel,
  onChangeDensityLevel,
  isUserAccountPremium,
}) => {
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const { openSubscribePage } = useSubscription();

  const densityOptions = [DensityValues.Less, DensityValues.More, DensityValues.Many, DensityValues.Premium];

  const onDensityOptionClick = async (densityValue: DensityValues) => {
    if (densityValue === densityLevel) {
      return;
    }
    if (densityValue === DensityValues.Premium && !isUserAccountPremium) {
      openSubscribePage({ pinchpoint: Pinchpoints.PremiumDensity });
    } else {
      onChangeDensityLevel(densityValue);
    }
  };

  return (
    <SidebarItem>
      <Typography
        variant="sm"
        isBold
        css={css`
          color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[500]};
          margin-bottom: ${theme.spacing(1)};
        `}
      >
        {t('numberOfTranslations')}
      </Typography>
      <Box
        display="flex"
        justifyContent="space-between"
        css={css`
          width: 100%;
        `}
      >
        {densityOptions.map(density => (
          <DensityOptionOld
            key={`${density}-selector`}
            onClick={onDensityOptionClick}
            density={density}
            active={densityLevel === density}
            locked={!isUserAccountPremium && density === DensityValues.Premium}
          />
        ))}
      </Box>
    </SidebarItem>
  );
};
