/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme, CircularProgress } from '@mui/material';

import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { FadeIn } from '~/components/shared/FadeIn';

const { Typography, useColorScheme } = ToucanComponents;

interface InfoMessageProps {
  isVisible?: boolean;
  isError?: boolean;
  text?: string;
  isLoaderVisible?: boolean;
}

export const InfoMessage: FC<InfoMessageProps> = ({
  isVisible = false,
  isError = false,
  text,
  isLoaderVisible = false,
}) => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();

  const getInfoMessageTextColor = () => {
    if (isError) {
      return isDarkMode ? ToucanColors.red[200] : ToucanColors.red[300];
    }

    return isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500];
  };

  if (!isVisible) {
    return null;
  }

  return (
    <Box flex="0" width="100%" zIndex="3" position="sticky" bottom={0}>
      <SidebarSection
        hasBottomLine={false}
        css={css`
          border-top: 1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]};
          background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
        `}
      >
        <FadeIn show={isVisible}>
          <div
            css={css`
              margin-top: ${theme.spacing(1)};
              margin-bottom: ${theme.spacing(1)};
              display: flex;
              justify-content: center;
              align-items: center;
            `}
          >
            {isLoaderVisible && (
              <CircularProgress
                size={theme.spacing(2)}
                thickness={5}
                css={css`
                  margin-right: ${theme.spacing(2)};
                `}
              />
            )}
            <Typography
              variant="xs"
              css={css`
                color: ${getInfoMessageTextColor()};
              `}
            >
              {text}
            </Typography>
          </div>
        </FadeIn>
      </SidebarSection>
    </Box>
  );
};
