/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme, Box } from '@mui/material';

import { PremiumLockedBadge } from '~/components/shared/PremiumLockedBadge';
import { SubscriptionRedirectContainer } from '~/components/shared/SubscriptionRedirectContainer';
import { Pinchpoints } from '~/constants/pinchpoints';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useSubscription } from '~/hooks/useSubscription';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';

import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, Switch, Button, ToucanIcon, useColorScheme } = ToucanComponents;

interface HighlightToTranslateSettingsProps {
  onToggleEnabledState: (enabled: boolean) => void;
  isEnabled: boolean;
}

export const HighlightToTranslateSettings: FC<HighlightToTranslateSettingsProps> = ({
  isEnabled,
  onToggleEnabledState,
}) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { textLevel1, textLevel3 } = useColorScheme();
  const { openSubscribePage, isPremiumSubscribed } = useSubscription();
  const { getFeaturePaywallStatus } = usePremiumExperiment();
  const isFeatureBehindPaywall =
    !isPremiumSubscribed &&
    getFeaturePaywallStatus(PremiumFeatureName.HighlightToTranslate) === PremiumFeaturePaywallStatus.Paid;

  const onChangeEnabledState = () => {
    onToggleEnabledState(!isEnabled);
    ContentMessengers.trackEvent({
      eventName: 'Highlight to translate settings toggle',
      properties: {
        enabled: !isEnabled,
      },
    });
  };

  return (
    <SidebarItemSeparated
      leftContent={
        <React.Fragment>
          <Typography
            variant="sm"
            isBold
            css={css`
              color: ${isFeatureBehindPaywall ? textLevel3 : textLevel1};
            `}
          >
            {t('highlightToTranslate')}
          </Typography>
          {isFeatureBehindPaywall && (
            <Box display="flex" alignItems="center" gap={1}>
              <PremiumLockedBadge data-testid="settings-panel-htt-premium-badge" />
              <Typography variant="xs">
                <Button
                  type="text"
                  onClick={() => openSubscribePage({ pinchpoint: Pinchpoints.HighlightToTranslateSettings })}
                  css={css`
                    display: flex;
                    align-items: center;
                    gap: ${theme.spacing(0.5)};
                  `}
                >
                  {t('startFreeTrialFirstLetterCapitalized')}
                  <ToucanIcon
                    icon="chevron-right"
                    css={css`
                      width: 10px;
                      color: inherit;
                    `}
                  />
                </Button>
              </Typography>
            </Box>
          )}
        </React.Fragment>
      }
      rightContent={
        <SubscriptionRedirectContainer
          shouldRedirect={isFeatureBehindPaywall}
          pinchpoint={Pinchpoints.HighlightToTranslateSettings}
        >
          <Switch
            value="switch"
            isChecked={isEnabled}
            isDisabled={isFeatureBehindPaywall}
            onChange={onChangeEnabledState}
            css={css`
              margin-right: 0;
            `}
          />
        </SubscriptionRedirectContainer>
      }
    />
  );
};
