import React, { useState } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { CircularProgress, css } from '@mui/material';

import { useMountedRef } from '~/hooks/useMountedRef';
import { useTranslation } from '~/libs/i18n';

import { Box } from '../Box';
import { SidebarItemSeparated } from '../Sidebar/SidebarItem';
import { SidebarSection } from '../Sidebar/SidebarSection';
import { useSidebar } from '../Sidebar/useSidebar';
import { Image } from '../shared/Image';

const { Button, Typography, useColorScheme } = ToucanComponents;

export const AccountDeletionConfirmation = ({
  logout,
  refetchAll,
}: {
  logout: () => Promise<void>;
  refetchAll: () => Promise<void>;
}) => {
  const [loading, setLoading] = useState<boolean>(false);
  const { t } = useTranslation();
  const { closeSidebar } = useSidebar();
  const { isDarkMode } = useColorScheme();
  const isMounted = useMountedRef();

  return (
    <>
      <SidebarItemSeparated />
      <SidebarSection
        hasBottomLine={false}
        display="flex"
        flexDirection="column"
        alignItems="center"
        justifyContent="center"
        textAlign="center"
      >
        <Image mt={2} src="/images/toucan-sad.svg" alt={t('toucanLogo')} />
        <Box pt={2} pb={1}>
          <Typography variant="sm" isBold color={isDarkMode ? ToucanColors.white : ToucanColors.gray[500]}>
            {t('sorryToSeeYouGo')}
          </Typography>
        </Box>
        <Typography variant="sm" color={isDarkMode ? ToucanColors.white : ToucanColors.gray[500]}>
          {t('toucanWillDeleteYourAccount')}
        </Typography>

        <Box
          pt={3}
          width="100%"
          display="flex"
          flexDirection="column"
          css={css`
            & svg {
              color: ${ToucanColors.white};
            }
          `}
        >
          <Button
            type="utility"
            onClick={async () => {
              setLoading(true);
              try {
                await logout();
                await refetchAll();
                closeSidebar();
              } catch (e) {
                // Ignore
              } finally {
                if (isMounted()) {
                  setLoading(false);
                }
              }
            }}
          >
            {loading ? <CircularProgress size={16} thickness={3} /> : t('logOut')}
          </Button>
        </Box>
      </SidebarSection>
    </>
  );
};
