import { i18n } from '~/libs/i18n';
import { SnoozeDuration } from '~/schema/background';

export const getDurationOptions = (getMessage: typeof i18n.prototype.getMessage) => {
  const durationOptions: Omit<Record<SnoozeDuration, { label: string; value: string }>, 'FiveMinutes'> = {
    [SnoozeDuration.OneHour]: {
      label: getMessage('oneHour'),
      value: SnoozeDuration.OneHour,
    },
    [SnoozeDuration.Evening]: {
      label: getMessage('until6pm'),
      value: SnoozeDuration.Evening,
    },
    [SnoozeDuration.Tomorrow]: {
      label: getMessage('untilTomorrow'),
      value: SnoozeDuration.Tomorrow,
    },
    [SnoozeDuration.DailyNineToSix]: {
      label: getMessage('daily9amTo6pm'),
      value: SnoozeDuration.DailyNineToSix,
    },
    [SnoozeDuration.Indefinitely]: {
      label: getMessage('indefinitely'),
      value: SnoozeDuration.Indefinitely,
    },
  };

  return durationOptions;
};
