/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { IconName, ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { alpha } from '@mui/material';

import { Box } from '~/components/Box';
import { ProgressionType } from '~/schema/background';

interface ProgressItemProps {
  title: string;
  subTitle: string;
  iconColor?: string;
  iconFillColor?: string;
  icon: string;
  onItemClicked: (progressionType: ProgressionType) => void;
  progressionType: ProgressionType;
}

const { Typography, ToucanIcon } = ToucanComponents;

export const ProgressionItem: FC<ProgressItemProps> = ({
  icon = 'alarm-clock',
  iconColor = ToucanColors.teal[300],
  iconFillColor = ToucanColors.white,
  title,
  subTitle,
  onItemClicked,
  progressionType,
}) => (
  <Box
    mb={1}
    borderRadius="4px"
    bgcolor="rgba(245, 245, 245, 0.1)"
    p={2}
    borderBottom="2px solid rgba(245, 245, 245, 0.2);"
    display="flex"
    alignItems="center"
    css={css`
      cursor: pointer;
      transition: all 0.2s ease;
      box-shadow: inset 0 1px 0 -1px ${alpha(ToucanColors.white, 0.1)};
      border-bottom: 2px solid ${alpha(ToucanColors.white, 0.2)};
      :hover {
        box-shadow: inset 0 0 0 2px ${alpha(ToucanColors.white, 0.1)};
      }
      :focus {
        box-shadow: inset 0 0 0 2px ${alpha(ToucanColors.blue[100], 0.1)};
        outline: none;
      }
    `}
    onClick={() => onItemClicked(progressionType)}
    tabIndex={-1}
  >
    <Box
      width="40px"
      height="40px"
      borderRadius="20px"
      mr={2}
      bgcolor={iconColor}
      color={iconColor}
      display="flex"
      justifyContent="center"
      alignItems="center"
      css={css`
        svg {
          fill: ${iconFillColor};
        }
      `}
    >
      <ToucanIcon icon={icon as IconName} />
    </Box>
    <Box>
      <Typography variant="sm" isBold color={ToucanColors.white}>
        {title}
      </Typography>
      <Typography variant="2xs" color={ToucanColors.white}>
        {subTitle}
      </Typography>
    </Box>
    <Box
      css={css`
        margin-left: auto;
      `}
    >
      <ToucanIcon
        icon="chevron-right"
        css={css`
          width: 16px;
          color: ${ToucanColors.white};
        `}
      />
    </Box>
  </Box>
);
