/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme, FormHelperText, InputAdornment } from '@mui/material';
import { useForm } from 'react-hook-form';

import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { ServerErrorCodes } from '~/helpers/errors/types';
import { useTranslation } from '~/libs/i18n';
import {
  useAddDomainFromBanlistMutation,
  useGetUserBanlistQuery,
  useRemoveDomainFromBanlistMutation,
} from '~/schema/background';

const { Typography, Button, ToucanIcon, TextField, Tooltip, useColorScheme } = ToucanComponents;

interface DomainFormData {
  domain: string;
  server?: string;
}

interface BlockedSitesProps {
  onDomainChange: () => Promise<void>;
  isInPopup: boolean;
}

// TODO: Put somewhere else
export const domainRegex = /^([a-z0-9]+(-[a-z0-9]+)*\.)+[a-z]{2,}$/;

export const BlockedSites: FC<BlockedSitesProps> = ({ onDomainChange, isInPopup }) => {
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const userBanlistQuery = useGetUserBanlistQuery();
  const [removeDomainFromBanlist] = useRemoveDomainFromBanlistMutation();
  const [addDomainToBanlist] = useAddDomainFromBanlistMutation();
  const {
    register,
    handleSubmit,
    setError,
    clearErrors,
    reset,
    formState: { errors },
  } = useForm<DomainFormData>();

  const blockSite = async (formData: DomainFormData) => {
    const { errors: addDomainToBanlistErrors } = await addDomainToBanlist({ domain: formData.domain });

    if (addDomainToBanlistErrors) {
      if (addDomainToBanlistErrors?.[0].extensions?.code === ServerErrorCodes.DOMAIN_ALREADY_BANNED) {
        setError('server', {
          message: t('thatDomainHasAlreadyBeenAdded'),
        });
      } else {
        setError('server', {
          message: t('toucansHavingTroubleAddingThisUrl'),
        });
      }
      return;
    }

    reset({ domain: '' });
    await userBanlistQuery.refetch();
    await onDomainChange();
  };

  const removeSiteFromList = async (domain: string) => {
    await removeDomainFromBanlist({ domain });
    await userBanlistQuery.refetch();
    await onDomainChange();
  };

  return (
    <React.Fragment>
      <SidebarSection>
        <SlideHeader subHeader={t('translationsWontAppearOnBlockedSites')}>{t('blockedSites')}</SlideHeader>
      </SidebarSection>
      <SidebarSection hasBottomLine={false}>
        <TextField
          isDarkMode={isDarkMode}
          label={t('siteUrl')}
          placeholder={t('siteCom')}
          hasError={!!errors.domain}
          helperText={errors.domain && errors.domain.message}
          css={css`
            margin-bottom: ${theme.spacing(1)};
          `}
          {...register('domain', {
            required: t('pleaseEnterAValidDomain'),
            pattern: {
              value: domainRegex,
              message: t('pleaseEnterAValidDomain'),
            },
          })}
          type="url"
          InputProps={{
            endAdornment: (
              <InputAdornment
                css={css`
                  white-space: normal;
                `}
                position="end"
              >
                <Tooltip
                  title={t('blockedSitesInputTooltip')}
                  width={257}
                  placement="top-end"
                  disablePortal={!isInPopup}
                >
                  <Button
                    type="text"
                    css={css`
                      font-size: 12px;
                      &:hover {
                        text-decoration: none;
                      }
                    `}
                  >
                    <ToucanIcon
                      icon="info-circle"
                      css={css`
                        width: 16px;
                        height: 16px;
                      `}
                    />
                  </Button>
                </Tooltip>
              </InputAdornment>
            ),
          }}
        />

        {errors.server && (
          <FormHelperText
            error
            css={css`
              margin-bottom: ${theme.spacing(2)};
            `}
          >
            {errors.server.message}
          </FormHelperText>
        )}

        <Button
          type="utility"
          size="medium"
          onClick={() => {
            clearErrors('server');
            handleSubmit(blockSite)();
          }}
          css={css`
            width: 100%;
            margin-bottom: ${theme.spacing(2)};
          `}
        >
          {t('blockSite')}
        </Button>

        {userBanlistQuery.data?.settings?.userBanlist.map((bannedDomain: string, index: number) => (
          <div
            css={css`
              display: flex;
              align-items: center;
              padding-top: ${theme.spacing(2)};
              padding-bottom: ${theme.spacing(2)};
              ${index !== userBanlistQuery.data.settings.userBanlist.length - 1 &&
              `
                border-bottom: 1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]};
              `}
            `}
          >
            <Typography
              color={isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}
              css={css`
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
              `}
            >
              {bannedDomain}
            </Typography>
            <Button
              type="text"
              onClick={() => removeSiteFromList(bannedDomain)}
              css={css`
                margin-left: auto;
                min-width: 20px;
              `}
            >
              <ToucanIcon
                icon="times"
                css={css`
                  color: ${ToucanColors.red[300]};
                  width: 16px;
                  height: 16px;
                `}
              />
            </Button>
          </div>
        ))}
      </SidebarSection>
      <SidebarSection hasBottomLine={false}>{userBanlistQuery.error?.message}</SidebarSection>
    </React.Fragment>
  );
};
