import React, { FC } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { CircularProgress } from '@mui/material';

import { Box } from '~/components/Box';
import { useTranslation } from '~/libs/i18n';

const { Typography, Button, useColorScheme } = ToucanComponents;

interface VerifyInformationProps {
  onVerify: () => void;
  email: string | undefined;
  name: string | undefined;
  verifying: boolean;
}

export const VerifyInformation: FC<VerifyInformationProps> = ({ onVerify, email, name, verifying }) => {
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();

  const textColor = isDarkMode ? ToucanColors.white : ToucanColors.gray[500];
  const buttonType: 'primary' | 'utility' = isDarkMode ? 'primary' : 'utility';
  const borderColor = isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200];

  return (
    <Box
      p={3}
      css={css`
        h4,
        p {
          color: ${textColor};
        }
      `}
    >
      <Box pb={1}>
        <Typography variant="md" font="secondary">
          {t('verifyYourInformation')}
        </Typography>
      </Box>
      <Box pb={2}>
        <Typography variant="sm">{t('toKeepYourAccountSecure')}</Typography>
      </Box>
      <Box mb={2} borderRadius="4px" border={`1px solid ${borderColor}`} p={2}>
        {email && (
          <Box display="flex" pb={1}>
            <Box pr={1}>
              <Typography variant="sm" isBold>
                {t('email')}
              </Typography>
            </Box>

            <Typography variant="sm">{email}</Typography>
          </Box>
        )}
        {name && (
          <Box display="flex">
            <Box pr={1}>
              <Typography variant="sm" isBold>
                {t('name')}
              </Typography>
            </Box>
            <Typography variant="sm">{name}</Typography>
          </Box>
        )}
      </Box>
      <Box
        display="flex"
        css={css`
          button {
            width: 100%;
          }
          path {
            color: ${ToucanColors.white};
          }
        `}
      >
        <Button type={buttonType} onClick={onVerify}>
          {verifying ? <CircularProgress size="16px" /> : t('confirm')}
        </Button>
      </Box>
    </Box>
  );
};
