/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useToggleDarkModeMutation } from '~/schema/background';

import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, Switch, useColorScheme } = ToucanComponents;

export const DarkMode: FC = () => {
  const { t } = useTranslation();
  const { isDarkMode, textLevel1 } = useColorScheme();
  const [toggleDarkMode] = useToggleDarkModeMutation();

  const onChangeEnabledState = () => {
    toggleDarkMode({ enabled: !isDarkMode });
    ContentMessengers.trackEvent({
      eventName: 'Dark mode settings toggle',
      properties: {
        enabled: !isDarkMode,
      },
    });
  };

  return (
    <SidebarItemSeparated
      leftContent={
        <Typography
          variant="sm"
          isBold
          css={css`
            color: ${textLevel1};
          `}
        >
          {t('darkMode')}
        </Typography>
      }
      rightContent={
        <Switch
          value="switch"
          isChecked={isDarkMode}
          onChange={onChangeEnabledState}
          css={css`
            margin-right: 0;
          `}
        />
      }
    />
  );
};
