import React, { useRef, useEffect } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { AutoSizer, CellMeasurer, CellMeasurerCache, List, ListRowRenderer } from 'react-virtualized';

import { Box } from '~/components/Box';
import { ExtraPracticeExplanationTrigger } from '~/components/Sidebar/content/ExtraPracticeExplanation';
import { WordListPlaceholder } from '~/components/Sidebar/content/Progress/WordList/WordListPlaceholder';
import { FadeIn } from '~/components/shared/FadeIn';
import { SidebarErrorMessage } from '~/components/shared/SidebarErrorMessage';
import { FeatureName } from '~/constants/features';
import { Language } from '~/constants/language';
import { getErrorId } from '~/helpers/errors';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { LearningShortcutHomonym, useGetLearningShortcutWordListQuery } from '~/schema/background';

import { Header } from './Header';
import { HomonymCard } from './HomonymCard';

const { useColorScheme } = ToucanComponents;

interface LearningShortcutWordListProps {
  topicId?: string;
  isInPopup: boolean;
}

const cache = new CellMeasurerCache({
  // 138 card + 16 margin
  defaultHeight: 154,
  defaultWidth: 380,
  fixedWidth: true,
});

export const wordListRenderer: (options: {
  wordList: LearningShortcutHomonym[];
  languageKey: Language | undefined;
}) => ListRowRenderer = ({ wordList, languageKey }) => ({ index, style, parent }) => {
  const rowData = wordList[index];

  return (
    <CellMeasurer cache={cache} columnIndex={0} key={rowData.homonymId} parent={parent} rowIndex={index} style={style}>
      <HomonymCard languageKey={languageKey} style={style} {...rowData} />
    </CellMeasurer>
  );
};

export const TopicWordListContainer = ({ isInPopup, topicId }: LearningShortcutWordListProps) => {
  const { data, error, loading } = useGetLearningShortcutWordListQuery({
    skip: !topicId,
    variables: {
      input: {
        learningShortcutTopicId: topicId ?? '',
      },
    },
  });
  const { targetLanguage } = useUserInfo();
  const listRef = useRef<List>(null);
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const isModalEnabled = useFeatureEnabled(FeatureName.ExtraPracticeExplanationModal);

  const wordList = data?.screens.learningShortcutWordList?.wordList ?? [];
  const topicName = data?.screens.learningShortcutWordList?.name;
  const count = data?.screens.learningShortcutWordList.possibleTopicPhraseCount;

  useEffect(() => {
    if (!topicName || !topicId) {
      return;
    }
    // TODO: update topic id to topic name but that requires updating the graphql schema
    ContentMessengers.trackEvent({
      eventName: 'Shortcut Word List Seen',
      properties: {
        viewedTopicWordList: topicName,
        topic: topicId,
      },
    });
  }, [topicName, topicId]);

  return (
    <Box
      flex="1"
      display="flex"
      flexDirection="column"
      position="relative"
      bgcolor={isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]}
      zIndex={1}
      height="100%"
    >
      <Header
        title={topicName}
        subtitle={t('amountWordsAndPhrases', { count })}
        bottomContent={
          isModalEnabled && !isInPopup ? (
            <Box pt={1}>
              <ExtraPracticeExplanationTrigger triggerText={t('learnAboutExtraPractice')} />
            </Box>
          ) : null
        }
        loading={loading}
        error={!!error}
      />
      {(() => {
        if (loading) {
          return <WordListPlaceholder />;
        }
        if (error) {
          const errorId = getErrorId(error);
          return (
            <Box pt={5} px={3}>
              <SidebarErrorMessage message={error.message} errorId={errorId} />
            </Box>
          );
        }

        return (
          <FadeIn show height="100%">
            <AutoSizer>
              {({ width, height }) => (
                <List
                  ref={listRef}
                  deferredMeasurementCache={cache}
                  height={height || 300}
                  width={width || 380}
                  rowHeight={cache.rowHeight}
                  rowCount={wordList.length}
                  rowRenderer={wordListRenderer({
                    wordList,
                    languageKey: targetLanguage,
                  })}
                />
              )}
            </AutoSizer>
          </FadeIn>
        );
      })()}
    </Box>
  );
};
