/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { Maybe } from '@graphql-tools/utils';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { HorizontalLine } from '~/components/Sidebar/HorizontalLine';
import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

import { Box } from '../../../Box';

const { useColorScheme, Typography, Button } = ToucanComponents;

type WhatYouKnow = {
  id: string;
  name: string;
};

interface LearningShortcutBodyProps {
  onNavigateToDetails: (learningShortcutId: string) => void;
  onNavigateToTopic: (learningShortcutId: string) => void;
  currentFocusLearningShortcutId?: string;
  currentFocusName?: string;
  whatYouKnow: Array<Maybe<WhatYouKnow>>;
}

export const LearningShortcutBody: FC<LearningShortcutBodyProps> = ({
  onNavigateToDetails,
  onNavigateToTopic,
  currentFocusLearningShortcutId,
  currentFocusName,
  whatYouKnow,
}) => {
  const theme = useTheme();
  const { backgroundAccent, textLevel2, textLevel3 } = useColorScheme();
  const { t } = useTranslation();
  const isWhatYouKnowEmpty = whatYouKnow.length === 0;
  const whatYouKnowMore = whatYouKnow.length - 2;

  return (
    <Box marginBottom={theme.spacing(1.5)}>
      <Box mt={2} border={`1px solid ${backgroundAccent}`} borderRadius={theme.spacing(0.5)} p={theme.spacing(2)}>
        <Box display="flex" alignItems="center">
          <Image
            src="/images/lamp-with-laptop.svg"
            alt={t('lampWithLaptop')}
            css={css`
              width: 32px;
            `}
          />
          <Box ml={2}>
            <Typography
              variant="xs"
              isBold
              css={css`
                color: ${textLevel2};
              `}
            >
              {t('currentFocus')}
            </Typography>
            <Typography variant="xs" isBold>
              <Button
                type="text"
                css={css`
                  text-align: left;
                `}
                onClick={() => onNavigateToTopic(currentFocusLearningShortcutId ?? '')}
              >
                {currentFocusName}
              </Button>
            </Typography>
          </Box>
        </Box>
        <Box my={2}>
          <HorizontalLine heightPixels={1} color={backgroundAccent} />
        </Box>
        <Box display="flex" alignItems="center">
          <Image
            src={`/images/${isWhatYouKnowEmpty ? 'party-icon-gray' : 'party-icon'}.svg`}
            alt={t('partyIcon')}
            css={css`
              width: 32px;
            `}
          />
          <Box ml={2}>
            <Typography
              variant="xs"
              isBold
              css={css`
                color: ${textLevel2};
              `}
            >
              {t('whatYouKnow')}
            </Typography>
            <Typography variant="xs" isBold={!isWhatYouKnowEmpty}>
              {isWhatYouKnowEmpty ? (
                <span
                  css={css`
                    color: ${textLevel3};
                  `}
                >
                  {t('youHaventCompletedAny')}
                </span>
              ) : (
                <Box display="flex" flexWrap="wrap">
                  {whatYouKnow.map((topic, index) => {
                    if (index === 2) {
                      return (
                        <Box key={topic?.name} display="flex">
                          <span
                            css={css`
                              color: ${textLevel2};
                            `}
                          >
                            ,&nbsp;
                          </span>
                          <Button
                            type="text"
                            css={css`
                              text-align: left;
                            `}
                            onClick={() => onNavigateToDetails(currentFocusLearningShortcutId ?? '')}
                          >
                            +{whatYouKnowMore}&nbsp;{t('more')}
                          </Button>
                        </Box>
                      );
                    }
                    if (index > 2) {
                      return null;
                    }
                    return (
                      <Box key={topic?.name} display="flex">
                        {index > 0 && (
                          <span
                            css={css`
                              color: ${textLevel2};
                            `}
                          >
                            ,&nbsp;
                          </span>
                        )}
                        <Button
                          type="text"
                          css={css`
                            text-align: left;
                          `}
                          onClick={() => onNavigateToTopic(topic?.id ?? '')}
                        >
                          {topic?.name}
                        </Button>
                      </Box>
                    );
                  })}
                </Box>
              )}
            </Typography>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};
