/** @jsx jsx */
import React, { FC, useEffect, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme, Box, Rating, IconContainerProps } from '@mui/material';

import Config, { BrowserReviewLink } from '~/configs/config';
import { Events } from '~/constants/events';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useAddRatingMutation } from '~/schema/background';

import { Star, StarState } from './Star';

const { Typography, Link } = ToucanComponents;

enum CTAType {
  Store = 'store',
  Feedback = 'feedback',
}

export const Feedback: FC = () => {
  const { t } = useTranslation();
  const theme = useTheme();
  const [clickedStars, setClickedStars] = useState<number | null>(null);
  const [addRating] = useAddRatingMutation();

  const reviewLink = clickedStars === 5 ? BrowserReviewLink : `${Config.siteURL}/contact`;
  const ctaShown = clickedStars === 5 ? CTAType.Store : CTAType.Feedback;

  useEffect(() => {
    ContentMessengers.trackEvent({
      eventName: Events.StarRatingViewed,
      properties: {},
    });
  }, []);

  const onStarClick = async (stars: number | null) => {
    if (!stars) {
      return;
    }
    setClickedStars(stars);

    ContentMessengers.trackEvent({
      eventName: Events.StarRatingClicked,
      properties: {
        starRating: stars,
      },
    });

    await addRating({
      rating: {
        ratingValue: stars,
      },
    });
  };

  const StarComponent = (props: IconContainerProps) => {
    const { className } = props;

    if (className?.includes('iconFocus')) {
      return <Star starState={StarState.Hover} />;
    }
    if (className?.includes('iconFilled')) {
      return <Star starState={StarState.Clicked} />;
    }
    return <Star starState={StarState.Default} />;
  };

  return (
    <Box
      data-testid="notification-feedback"
      width={346}
      py={2}
      display="flex"
      flexDirection="column"
      alignItems="center"
      css={css`
        background-color: ${ToucanColors.yellow[100]};
        border-radius: 4px;
      `}
    >
      <Typography
        variant="xs"
        isBold
        css={css`
          color: ${ToucanColors.yellow[500]};
          padding-bottom: ${theme.spacing(1)};
        `}
      >
        {t('notificationsHowAreYouEnjoying')}
      </Typography>
      <Box display="flex">
        <Rating name="ratings" IconContainerComponent={StarComponent} onChange={(event, value) => onStarClick(value)} />
      </Box>
      {typeof clickedStars === 'number' && (
        <React.Fragment>
          <Typography
            variant="xs"
            css={css`
              color: ${ToucanColors.yellow[500]};
            `}
          >
            {ctaShown === CTAType.Store ? t('notificationsGladYoureLiking') : t('notificationsLetUsKnow')}
          </Typography>
          <Typography variant="xs" isBold>
            <Link href={reviewLink}>
              {ctaShown === CTAType.Store ? t('notificationsLeaveAReview') : t('notificationsShareYourFeedback')}
            </Link>
          </Typography>
        </React.Fragment>
      )}
    </Box>
  );
};
