/** @jsx jsx */
import React, { FC, useState, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { BackButton } from '~/components/Sidebar/BackButton';
import CurrentDomainHelpers from '~/helpers/currentDomainHelpers';
import { SlideDirection, SlideName } from '~/types/sidebar';

import { useSidebarContext } from '../Sidebar/SidebarContext';
import { SidebarSection } from '../Sidebar/SidebarSection';

import { AnonymousUserExtensionSettingsProvider } from './AnonymousUserExtensionSettingsContext';
import { AccountValueProps } from './sections/AccountValueProps';
import { SignUpOrLogInBanner } from './sections/SignUpOrLogInBanner';
import { SignUpSignInButtons } from './sections/SignUpSignInButtons';

const { useColorScheme } = ToucanComponents;

interface AnonymousUserExtensionSettingsProps {
  isInPopup: boolean;
  closeAccountCreationWidget: () => void;
  refetch: () => Promise<void>;
}

export const AnonymousUserExtensionSettings: FC<AnonymousUserExtensionSettingsProps> = ({
  isInPopup,
  closeAccountCreationWidget,
  refetch,
}) => {
  const [currentSiteDomain, setCurrentSiteDomain] = useState<string | null>(null);
  const [isNewSignUp, setIsNewSignUp] = useState<boolean>(true);
  const [isEmailSignupSelected, setIsEmailSignupSelected] = useState<boolean>(false);
  const [isResetSelected, setIsResetSelected] = useState<boolean>(false);
  const { onNavigateSidebar } = useSidebarContext();

  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  useEffect(() => {
    const getCurrentDomainHost = async () => {
      const { currentDomain } = (await CurrentDomainHelpers.getCurrentDomainHost()) ?? {};
      if (currentDomain) {
        setCurrentSiteDomain(currentDomain);
      }
    };
    getCurrentDomainHost();
  }, []);

  return (
    <React.Fragment>
      <SidebarSection hasBottomLine={false}>
        <BackButton
          onClick={() => onNavigateSidebar({ slide: SlideName.MainSettings, slideDirection: SlideDirection.Backward })}
        />
      </SidebarSection>
      <AnonymousUserExtensionSettingsProvider
        isDarkModeEnabled={isDarkMode}
        closeAccountCreationWidget={closeAccountCreationWidget}
        isInPopup={isInPopup}
        currentSiteDomain={currentSiteDomain}
        isNewSignUp={isNewSignUp}
        setIsNewSignUp={setIsNewSignUp}
        isEmailSignupSelected={isEmailSignupSelected}
        setIsEmailSignupSelected={setIsEmailSignupSelected}
        isResetSelected={isResetSelected}
        setIsResetSelected={setIsResetSelected}
      >
        <SignUpOrLogInBanner />
        <div
          css={css`
            padding: ${theme.spacing(4)};
            width: 100%;
          `}
        >
          <AccountValueProps />
          <SignUpSignInButtons refetch={refetch} />
        </div>
      </AnonymousUserExtensionSettingsProvider>
    </React.Fragment>
  );
};
