import React, { FC } from 'react';

import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';

import { Box, BoxProps } from '~/components/Box';

const { Typography, useColorScheme } = ToucanComponents;

interface StepProps extends BoxProps {
  number: number;
}

export const Step: FC<StepProps> = ({ children, number, ...boxProps }) => {
  const { isDarkMode } = useColorScheme();

  return (
    <Box display="flex" alignItems="center" {...boxProps}>
      <Box
        width="28px"
        height="28px"
        borderRadius="28px"
        bgcolor={ToucanColors.teal[400]}
        display="flex"
        alignItems="center"
        justifyContent="center"
        mr={2}
      >
        <Typography variant="xs" color={ToucanColors.white} isBold>
          {number}
        </Typography>
      </Box>
      <Box flex="1">
        <Typography variant="sm" color={isDarkMode ? ToucanColors.gray[100] : ToucanColors.gray[500]}>
          {children}
        </Typography>
      </Box>
    </Box>
  );
};
