/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';
import { motion } from 'framer-motion';

import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

const { Typography, useColorScheme } = ToucanComponents;

interface ButtonProps {
  language: string;
  flagRound: string;
  country: string;
  onClick: () => void;
  isSelected: boolean;
  isReducedSize?: boolean;
}

export const Button: FC<ButtonProps> = ({
  language,
  flagRound,
  country,
  onClick,
  isSelected,
  isReducedSize,
  ...props
}) => {
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const theme = useTheme();

  const getBoxShadowValue = () => {
    if (isDarkMode) {
      return isSelected ? `4px ${ToucanColors.teal[300]}` : `1px ${ToucanColors.gray[400]}`;
    }

    return isSelected ? `4px ${ToucanColors.blue[300]}` : `1px ${ToucanColors.gray[200]}`;
  };

  return (
    <motion.div
      onClick={onClick}
      css={css`
        display: flex;
        align-items: center;
        padding: ${theme.spacing(2)};
        border-radius: 4px;
        box-shadow: inset 0 0 0 ${getBoxShadowValue()};
        cursor: pointer;
      `}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      {...props}
    >
      <Box display="flex" flexDirection="row" alignItems="center">
        <Image
          src={flagRound}
          width={isReducedSize ? 24 : 32}
          height={isReducedSize ? 24 : 32}
          alt={t('flagOfCountry', { country })}
        />
        <Box ml={1}>
          <Typography
            variant={isReducedSize ? 'xs' : 'sm'}
            isBold
            color={isDarkMode ? ToucanColors.white : ToucanColors.gray[600]}
          >
            {language}
          </Typography>
        </Box>
      </Box>
    </motion.div>
  );
};
