/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';

import { SectionItemSeparator } from '~/components/Sidebar/SectionItemSeparator';
import { SidebarItem } from '~/components/Sidebar/SidebarItem';
import { Pulldown } from '~/components/shared/Pulldown';
import { LanguageVariant } from '~/constants/language';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useForceDatasetDownloadMutation, useChangeLanguageMutation } from '~/schema/background';

interface InfoMessageState {
  isVisible: boolean;
  isError?: boolean;
  text?: string;
  isLoaderVisible?: boolean;
}

interface DisplayAlphabetProps {
  setInfoMessage: (args: InfoMessageState) => void;
}

export const DisplayAlphabet: FC<DisplayAlphabetProps> = ({ setInfoMessage }) => {
  const { t } = useTranslation();
  const { targetLanguageVariant } = useUserInfo();
  const [changeLanguage] = useChangeLanguageMutation();
  const [forceDatasetDownload] = useForceDatasetDownloadMutation();
  const { sourceLanguageInfo, targetLanguageInfo } = useLanguageInfo();

  if (!targetLanguageInfo?.variants) {
    return null;
  }

  const defaultTargetLanguageVariant = Object.values(targetLanguageInfo?.variants).find(
    languageInfo => languageInfo.isDefault,
  )?.key;

  const onSelectChange = async (value: string | number) => {
    setInfoMessage({
      isVisible: true,
      isLoaderVisible: true,
      text: t('loadingExtensionLibrary'),
    });

    ContentMessengers.trackEvent({
      eventName: 'Transliteration Option Clicked',
      properties: {
        transliteration: targetLanguageInfo.variants && targetLanguageInfo.variants[value]?.languageId,
      },
    });

    const { errors: changeLanguageErrors } = await changeLanguage({
      sourceLanguage: sourceLanguageInfo?.languageId as string,
      targetLanguage: (targetLanguageInfo?.variants && targetLanguageInfo.variants[value]?.languageId) || '',
    });

    // NOTE: This messages the document that a change has happened with the users language.
    window.document.dispatchEvent(new Event('toucan-change-language'));
    const { errors: forceDatasetDownloadErrors } = await forceDatasetDownload({});

    const changeLanguageError = changeLanguageErrors && changeLanguageErrors[0].message;
    const forceDatasetDownloadError = forceDatasetDownloadErrors && forceDatasetDownloadErrors[0].message;

    if (changeLanguageError || forceDatasetDownloadError) {
      setInfoMessage({
        isVisible: true,
        isLoaderVisible: false,
        isError: true,
        text: changeLanguageError || forceDatasetDownloadError,
      });
      return;
    }

    setInfoMessage({
      isVisible: false,
    });
  };

  interface Option {
    value: LanguageVariant;
    label: string;
  }

  interface OptionLanguage {
    [key: string]: Option[];
  }

  const optionsPerLanguage: OptionLanguage = {
    arabic: [
      {
        value: LanguageVariant.Abjadiyyah,
        label: t('abjadiyyah'),
      },
      {
        value: LanguageVariant.Roman,
        label: t('phoneticSpelling'),
      },
    ],
    hebrew: [
      {
        value: LanguageVariant.KtavAshuri,
        label: t('ktavAshuri'),
      },
      {
        value: LanguageVariant.Roman,
        label: t('phoneticSpelling'),
      },
    ],
    hindi: [
      {
        value: LanguageVariant.Devanagari,
        label: t('devanagari'),
      },
      {
        value: LanguageVariant.Roman,
        label: t('phoneticSpelling'),
      },
    ],
    japanese: [
      {
        value: LanguageVariant.Kanji,
        label: t('kanji'),
      },
      {
        value: LanguageVariant.HiraganaKatakana,
        label: t('hiraganaKatakanaOnly'),
      },
      {
        value: LanguageVariant.Roman,
        label: t('phoneticSpelling'),
      },
    ],
    korean: [
      {
        value: LanguageVariant.Hangul,
        label: t('hangul'),
      },
      {
        value: LanguageVariant.Roman,
        label: t('phoneticSpelling'),
      },
    ],
    chinese: [
      {
        value: LanguageVariant.SimplifiedChinese,
        label: t('simplifiedChinese'),
      },
      {
        value: LanguageVariant.Roman,
        label: t('phoneticSpelling'),
      },
    ],
  };

  const displayAlphabetOptions = optionsPerLanguage[targetLanguageInfo.key as string].filter(languageVariant =>
    sourceLanguageInfo?.variants ? languageVariant.value !== LanguageVariant.Roman : true,
  );

  if (displayAlphabetOptions.length <= 1) {
    return null;
  }

  return (
    <React.Fragment>
      <SectionItemSeparator spacing={1} />
      <SidebarItem>
        <div
          css={css`
            width: 100%;
            & .MuiFormControl-root {
              width: 100%;
            }
          `}
        >
          <Pulldown
            label={t('selectDisplayAlphabet')}
            minWidth={150}
            onChange={onSelectChange}
            css={css`
              width: 100%;
            `}
            value={targetLanguageVariant || defaultTargetLanguageVariant}
            options={displayAlphabetOptions}
          />
        </div>
        <SectionItemSeparator spacing={1} />
      </SidebarItem>
    </React.Fragment>
  );
};
