import { useCallback, useState } from 'react';

/**
 * useToggle hook is a hook that returns a boolean value and a function to toggle it.
 * @param initialState - initial state of the toggle
 * @returns [boolean, toggle]
 */
export const useToggle = (initialState: boolean = false): [boolean, any] => {
  // Initialize the state
  const [state, setState] = useState<boolean>(initialState);
  // Define and memorize toggler function in case we pass down the component,
  // This function change the boolean value to it's opposite value
  const toggle = useCallback((): void => setState(prevState => !prevState), []);
  return [state, toggle];
};
