/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { LanguageVariant } from '~/constants/language';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';

import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, Switch, useColorScheme } = ToucanComponents;

interface PhoneticSpellingProps {
  isEnabled: boolean;
  onToggleEnabledState: (enabled: boolean) => void;
}

export const PhoneticSpelling: FC<PhoneticSpellingProps> = ({ isEnabled, onToggleEnabledState }) => {
  const { t } = useTranslation();
  const { textLevel1, textLevel3 } = useColorScheme();
  const { targetLanguageVariant } = useUserInfo();
  const { targetLanguageInfo, sourceLanguageInfo } = useLanguageInfo();

  if (
    !targetLanguageInfo?.variants ||
    targetLanguageVariant === LanguageVariant.Roman ||
    (sourceLanguageInfo?.variants && Object.keys(sourceLanguageInfo?.variants).length > 0)
  ) {
    return null;
  }

  const onChangeEnabledState = () => {
    onToggleEnabledState(!isEnabled);
    ContentMessengers.trackEvent({
      eventName: 'Phonetic spelling settings toggle',
      properties: {
        enabled: !isEnabled,
      },
    });
  };

  return (
    <React.Fragment>
      <SidebarItemSeparated
        leftContent={
          <React.Fragment>
            <Typography
              variant="sm"
              isBold
              css={css`
                color: ${textLevel1};
              `}
            >
              {t('phoneticSpelling')}
            </Typography>
            <Typography
              variant="xs"
              css={css`
                color: ${textLevel3};
              `}
            >
              {t('showHowPronounceWordsHoverOverThem')}
            </Typography>
          </React.Fragment>
        }
        rightContent={
          <Switch
            value="switch"
            isChecked={isEnabled}
            onChange={onChangeEnabledState}
            css={css`
              margin-right: 0;
            `}
          />
        }
      />
    </React.Fragment>
  );
};
