import React, { FC } from 'react';

import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { GLOBAL_PERMISSION } from '~/constants/permissions';
import { useHostPermissions } from '~/hooks/useHostPermissions';
import { useReferral } from '~/hooks/useReferral';
import { useGetOnboardingChecklistQuery } from '~/schema/background';

import { Checklist } from './Checklist';
import { Placeholder } from './Checklist/Placeholder';

export const OnboardingChecklistContainer: FC = () => {
  const { data, loading, error, refetch } = useGetOnboardingChecklistQuery();
  const { canRequestPermissions } = useHostPermissions({ hosts: [GLOBAL_PERMISSION] });
  const { toucanRewardOfferedPerReferral, referralCTAText } = useReferral();

  return (
    <>
      {loading ? (
        <Placeholder />
      ) : (
        <>
          <SidebarSection>
            <SlideHeader />
          </SidebarSection>
          <Checklist
            amountCompleted={data?.onboarding?.checklist?.amountCompleted}
            checklistItems={data?.onboarding?.checklist?.checklistItems}
            refetch={refetch}
            error={error}
            canRequestPermissions={canRequestPermissions}
            toucanRewardOfferedPerReferral={toucanRewardOfferedPerReferral}
            referralCTAText={referralCTAText}
          />
        </>
      )}
    </>
  );
};
