import React, { ReactNode } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Skeleton, useTheme } from '@mui/material';

import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { useTranslation } from '~/libs/i18n';

const { useColorScheme, ToucanIcon, Tooltip } = ToucanComponents;

interface HeaderProps {
  title: string;
  loading: boolean;
  error: boolean;
  subtitle: string;
  bottomContent?: ReactNode;
}

export const Header = ({ title, subtitle, loading, error, bottomContent }: HeaderProps) => {
  const theme = useTheme();
  const { isDarkMode, textLink } = useColorScheme();
  const { t } = useTranslation();

  return (
    <SidebarSection bottomLineHeight={1} bgcolor={isDarkMode ? ToucanColors.gray[500] : ToucanColors.white}>
      <SlideHeader
        isBackButtonVisible
        subHeader={loading || error ? undefined : subtitle}
        bottomContent={bottomContent}
      >
        {loading ? (
          <Skeleton
            width="160px"
            sx={{
              bgcolor: isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[100],
              borderRadius: '16px',
              transform: 'scale(1)',
            }}
          />
        ) : (
          <>
            {title}
            <Tooltip title={t('toucanWillPrioritizeTeaching')} placement="top-end" width={200}>
              <ToucanIcon
                icon="info-circle"
                height={14}
                width={14}
                color={textLink}
                css={css`
                  margin-left: ${theme.spacing(1)};
                `}
              />
            </Tooltip>
          </>
        )}
      </SlideHeader>
    </SidebarSection>
  );
};
