/* @jsx jsx */
import React, { FC, useMemo } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';

const { Typography, useColorScheme, ToucanIcon } = ToucanComponents;

interface ButtonOptionProps {
  isChecked: boolean;
  onClick: () => void;
  isDisabled?: boolean;
}

export const ButtonOption: FC<ButtonOptionProps> = ({ children, onClick, isChecked, isDisabled, ...props }) => {
  const { backgroundBase, isDarkMode, textLevel1, uiPrimary } = useColorScheme();
  const colors = useMemo(
    () => ({
      background: backgroundBase,
      activeBackground: uiPrimary,
      border: isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200],
      activeBorder: uiPrimary,
      color: textLevel1,
      activeColor: ToucanColors.white,
    }),
    [isDarkMode],
  );

  const disabledColors = useMemo(
    () => ({
      background: backgroundBase,
      activeBackground: isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400],
      border: isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200],
      activeBorder: isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400],
      color: ToucanColors.gray[400],
      activeColor: ToucanColors.white,
    }),
    [isDarkMode],
  );

  const colorsToUse = isDisabled ? disabledColors : colors;

  return (
    <Box
      aria-checked={isChecked}
      aria-disabled={isDisabled}
      role="radio"
      tabIndex={isChecked ? -1 : 0}
      flex={1}
      flexDirection="row"
      position="relative"
      display="inline-flex"
      py={1}
      px={isChecked ? 3 : 2}
      border={0}
      css={css`
        outline: none;
        position: relative;
        background: ${isChecked ? colorsToUse.activeBackground : colorsToUse.background};
        box-shadow: inset 0 0 0 1px ${isChecked ? colorsToUse.activeBorder : colorsToUse.border};
        border-radius: 4px;
        transition: padding 0.3s, box-shadow 0.3s, background 0.1s;
        text-align: center;
        cursor: pointer;
        & h6 {
          color: ${isChecked ? colorsToUse.activeColor : colorsToUse.color};
          transition: all 0.3s;
          transform: ${isChecked ? 'translate(-12px, 0px)' : 'translate(0px, 0px)'};
        }
        &:active:not([aria-checked='true']),
        ${
          !isDisabled
            ? `        
            &:hover:not([aria-checked='true']) {
              background: ${colorsToUse.border};
              box-shadow: inset 0 0 0 0 ${colorsToUse.border};
            }
            &:focus:not([aria-checked='true']) {
              box-shadow: inset 0 0 0 2px ${colorsToUse.activeBorder};
            }`
            : ''
        }
      `}
      onClick={isDisabled ? () => {} : onClick}
      onKeyDown={e => {
        if (e.key === 'Enter' && !isDisabled) {
          onClick();
        }
      }}
      {...props}
    >
      <Typography variant="2xs" isBold>
        {children}
      </Typography>
      <Box
        component="span"
        position="absolute"
        right={0}
        width={16}
        height={16}
        display="inline-flex"
        borderRadius="50%"
        justifyContent="center"
        alignItems="center"
        bgcolor={isDisabled ? ToucanColors.gray[400] : ToucanColors.teal[500]}
        css={css`
          opacity: ${isChecked ? 1 : 0};
          transition: all 0.3s;
          transform: ${isChecked
            ? 'translate(-12px, 0px) scale(1) rotate(0deg)'
            : 'translate(0px, 0px) scale(0.8) rotate(45deg)'};
          & svg {
            width: 8px;
            height: 8px;
            color: ${ToucanColors.white};
          }
        `}
      >
        <ToucanIcon icon="check" />
      </Box>
    </Box>
  );
};
