/** @jsx jsx */
import React, { BaseSyntheticEvent, FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { PremiumLockedBadge } from '~/components/shared/PremiumLockedBadge';
import { Pinchpoints } from '~/constants/pinchpoints';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useSubscription } from '~/hooks/useSubscription';
import { useTranslation } from '~/libs/i18n';

import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, Switch, useColorScheme, Button, ToucanIcon } = ToucanComponents;

interface AlwaysTranslateProps {
  setAlwaysTranslate: (variables: { alwaysTranslate: boolean }) => void;
  isAlwaysTranslateSavedWordsEnabled: boolean;
  isDisabled?: boolean;
}

export const AlwaysTranslate: FC<AlwaysTranslateProps> = ({
  setAlwaysTranslate,
  isAlwaysTranslateSavedWordsEnabled,
  isDisabled,
}) => {
  const { t } = useTranslation();
  const { textLevel1, textLevel3 } = useColorScheme();
  const theme = useTheme();
  const { openSubscribePage, isPremiumSubscribed } = useSubscription();
  const { getFeaturePaywallStatus } = usePremiumExperiment();
  const isFeatureBehindPaywall =
    !isPremiumSubscribed &&
    getFeaturePaywallStatus(PremiumFeatureName.AlwaysTranslateSavedWords) === PremiumFeaturePaywallStatus.Paid;

  const onChangeToggle = async (event: BaseSyntheticEvent) => {
    await setAlwaysTranslate({
      alwaysTranslate: event.target.checked,
    });
  };

  return (
    <SidebarItemSeparated
      data-testid="always-translate-saved-words"
      leftContent={
        <React.Fragment>
          <Typography
            variant="sm"
            isBold
            css={css`
              color: ${isFeatureBehindPaywall || isDisabled ? textLevel3 : textLevel1};
            `}
          >
            {t('alwaysTranslateSavedWords')}
          </Typography>

          {isFeatureBehindPaywall && !isDisabled && (
            <Box display="flex" alignItems="center" gap={1}>
              <PremiumLockedBadge data-testid="settings-panel-always-translate-saved-words-premium-badge" />

              <Typography variant="xs">
                <Button
                  type="text"
                  onClick={() => openSubscribePage({ pinchpoint: Pinchpoints.AlwaysTranslateSavedWord })}
                  css={css`
                    display: flex;
                    align-items: center;
                    gap: ${theme.spacing(0.5)};
                  `}
                >
                  {t('startFreeTrialFirstLetterCapitalized')}
                  <ToucanIcon
                    icon="chevron-right"
                    css={css`
                      width: 10px;
                      color: inherit;
                    `}
                  />
                </Button>
              </Typography>
            </Box>
          )}
        </React.Fragment>
      }
      rightContent={
        <Switch
          isDisabled={isFeatureBehindPaywall || isDisabled}
          value="switch"
          isChecked={isAlwaysTranslateSavedWordsEnabled}
          onChange={onChangeToggle}
          css={css`
            margin-right: 0;
          `}
        />
      }
    />
  );
};
