import React, { FC, useEffect, useState } from 'react';

import { ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { FadeIn } from '~/components/shared/FadeIn';
import { Pulldown } from '~/components/shared/Pulldown';
import { useToggle } from '~/hooks/useToggle';
import { useTranslation } from '~/libs/i18n';
import { ProgressionType, WordListSort as WordListSortType } from '~/schema/background';

import { defaultWordListSorts, ProgressionSortMap } from '../shared';

const { Typography, useColorScheme, Button } = ToucanComponents;

interface WordListSortProps {
  progressionType?: ProgressionType;
  sort: WordListSortType;
  onChange: (sortType: WordListSortType) => void;
}

export const WordListSort: FC<WordListSortProps> = ({ progressionType, sort, onChange }) => {
  const [isSortOpen, toggleSortConfig] = useToggle(false);
  const { isDarkMode, textLevel1 } = useColorScheme();
  const { t } = useTranslation();
  const [currentSort, setCurrentSort] = useState(sort);

  const currentOptions = (progressionType ? ProgressionSortMap[progressionType] : defaultWordListSorts).map(
    options => ({
      label: t(options.label),
      value: options.value,
    }),
  );
  const selectedOption = currentOptions.find(option => option.value === currentSort);

  useEffect(() => {
    setCurrentSort(sort);
  }, [sort]);

  return (
    <>
      {!isSortOpen ? (
        <FadeIn show>
          <Box pt={0.5} display="flex">
            <Typography color={textLevel1}>{t('sortBy')}:</Typography>
            <Box component="span" pl={1} flex="1">
              <Button
                data-testid="word-list-sort-button"
                type="text"
                onClick={() => {
                  toggleSortConfig();
                }}
              >
                {selectedOption?.label ?? null}
              </Button>
            </Box>
          </Box>
        </FadeIn>
      ) : (
        <FadeIn show>
          <Box width="100%" display="flex" flexDirection="column" py={2}>
            <Box pb={1} display="flex" width="100%" flexDirection="column" position="relative" zIndex="2">
              <Pulldown
                label={t('sortBy')}
                value={currentSort}
                options={currentOptions}
                onChange={value => {
                  setCurrentSort(value as WordListSortType);
                }}
                minWidth={150}
              />
            </Box>
            <Box pb={1} display="flex" width="100%" flexDirection="column">
              <Button
                variant="contained"
                onClick={() => {
                  toggleSortConfig();
                  onChange(currentSort);
                }}
              >
                {t('applyChanges')}
              </Button>
            </Box>
            <Button
              type="secondary"
              isReversed={isDarkMode}
              onClick={() => {
                toggleSortConfig();
                setCurrentSort(sort); // revert
              }}
            >
              {t('cancel')}
            </Button>
          </Box>
        </FadeIn>
      )}
    </>
  );
};
