/** @jsx jsx */
import React, { ChangeEvent, FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { Language } from '~/constants/language';
import { AudioControlExampleLanguageKey, useToucanTranslation } from '~/hooks/useToucanTranslation';
import { VoiceNumber } from '~/libs/TextToSpeech';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';

import { Slider } from '../Settings/DifficultySelector/Slider';

const { Typography, useColorScheme, Radio, RadioGroup } = ToucanComponents;

interface AudioControlsProps {
  targetLanguage: Language;
  playbackRate: number;
  voiceNumber: VoiceNumber;
  setUserAudioPreferences: (voiceNumber: number, playbackRate: number) => Promise<void>;
}

const playbackRateOptions = [0.5, 0.75, 1, 1.25, 1.5];

export const AudioControls: FC<AudioControlsProps> = ({
  targetLanguage,
  playbackRate,
  voiceNumber,
  setUserAudioPreferences,
}) => {
  const { t } = useTranslation();
  const { tt } = useToucanTranslation();
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const targetPhrase = tt(AudioControlExampleLanguageKey.LetsLearnLanguage);
  const eventName = 'User Changed Audio Settings';

  return (
    <React.Fragment>
      <SidebarSection>
        <SlideHeader>{t('audioControls')}</SlideHeader>
      </SidebarSection>
      <SidebarSection>
        <Typography
          variant="sm"
          isBold
          css={css`
            color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
          `}
        >
          {t('pronunciationSpeed')}
        </Typography>
        <Box display="flex" flexDirection="row" mt={1} alignItems="flex-start">
          <AudioPlayer
            targetPhrase={targetPhrase}
            language={targetLanguage}
            voiceNumber={voiceNumber}
            playbackRate={playbackRate}
            marginRight={1}
            key={`${voiceNumber}-${playbackRate}`}
          />
          <Box>
            <Typography
              variant="sm"
              css={css`
              color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}};
            `}
            >
              {targetPhrase}
            </Typography>
            <Typography
              variant="xs"
              css={css`
              color: ${isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400]}};
            `}
            >
              {t('letsLearn', { language: t(targetLanguage) })}
            </Typography>
          </Box>
        </Box>
        <Slider
          step={1}
          max={4}
          min={0}
          value={playbackRateOptions.indexOf(playbackRate)}
          onChange={async playback => {
            if (playbackRate !== playbackRateOptions[playback]) {
              const properties = {
                setting: 'pronunciationSpeed',
                value: playbackRateOptions[playback],
              };
              contentMessengers.trackEvent({ eventName, properties });
              await setUserAudioPreferences(voiceNumber, playbackRateOptions[playback]);
            }
          }}
          paddingTop={1}
        />
        <Typography
          variant="xs"
          css={css`
            color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}};
            margin-top: ${theme.spacing(1)};
          `}
        >
          {t('xSpeed', { speed: playbackRate })}
        </Typography>
      </SidebarSection>
      <SidebarSection hasBottomLine={false}>
        <Typography
          variant="sm"
          isBold
          css={css`
            color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
          `}
        >
          {t('chooseAVoice')}
        </Typography>
        <RadioGroup
          name="voice-number"
          data-testid="voice-number"
          onChange={async (e: ChangeEvent<HTMLInputElement>) => {
            const voice = parseInt(e.currentTarget.value, 10);
            const properties = {
              setting: 'voiceOptions',
              value: voice,
            };
            contentMessengers.trackEvent({ eventName, properties });
            await setUserAudioPreferences(voice, playbackRate);
          }}
          defaultValue={voiceNumber}
          css={css`
            margin-top: ${theme.spacing(2)};
          `}
        >
          <Radio
            value={0}
            isChecked={voiceNumber === 0}
            label={
              <Box display="flex" flexDirection="row">
                <AudioPlayer
                  targetPhrase={targetPhrase}
                  language={targetLanguage}
                  voiceNumber={0}
                  playbackRate={playbackRate}
                  marginRight={1}
                  key={`${playbackRate}`}
                />
                <Typography
                  variant="sm"
                  css={css`
                    color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}};
                  `}
                >
                  {t('voiceOne')}
                </Typography>
              </Box>
            }
          />
          <Radio
            value={1}
            isChecked={voiceNumber === 1}
            label={
              <Box display="flex" flexDirection="row">
                <AudioPlayer
                  targetPhrase={targetPhrase}
                  language={targetLanguage}
                  voiceNumber={1}
                  playbackRate={playbackRate}
                  marginRight={1}
                  key={`${playbackRate}`}
                />
                <Typography
                  variant="sm"
                  css={css`
                    color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}};
                  `}
                >
                  {t('voiceTwo')}
                </Typography>
              </Box>
            }
          />
        </RadioGroup>
      </SidebarSection>
    </React.Fragment>
  );
};
