/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { AnimatePresence } from 'framer-motion';

import { BirdCage } from '~/components/BirdCage';
import { OverlayName } from '~/types/sidebar';

import { MotionBox, MotionBoxProps } from '../Box';

import { useSidebarContext } from './SidebarContext';
import { SidebarError } from './SidebarError';

const fadeVariants = {
  hidden: { opacity: 0 },
  visible: { opacity: 1 },
};

interface OverlayProps extends MotionBoxProps {
  overlayName: OverlayName;
  zIndex?: number;
}

/**
 * Container for overlay content
 * @param props
 * @param props.overlayName String that is used to determine if the overlay should be visible
 */
export const Overlay: FC<OverlayProps> = ({ children, overlayName, zIndex, ...boxProps }) => {
  const { activeOverlayName } = useSidebarContext();

  return (
    <BirdCage errorComponent={SidebarError}>
      <AnimatePresence>
        {overlayName === activeOverlayName && (
          <MotionBox
            key="fade-content"
            initial="hidden"
            animate="visible"
            exit="hidden"
            variants={fadeVariants}
            css={css`
              height: 100%;
              width: 100%;
              position: absolute;
              top: 0;
              left: 0;
              z-index: ${zIndex || 1};
            `}
            {...boxProps}
          >
            {children}
          </MotionBox>
        )}
      </AnimatePresence>
    </BirdCage>
  );
};
