/** @jsx jsx */
import React, { FC, memo } from 'react';

import { css, jsx } from '@emotion/react';

import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

export enum StarState {
  Clicked = 'clicked',
  Default = 'default',
  Hover = 'hover',
}

interface StarProps {
  starState: StarState;
}

export const Star: FC<StarProps> = memo(({ starState = StarState.Hover }) => {
  const { t } = useTranslation();

  return (
    <Image
      src={`images/sidebar/notifications/star-${starState}.svg`}
      alt={t('notificationsFeedbackStar')}
      css={css`
        cursor: pointer;
      `}
    />
  );
});
