/** @jsx jsx */
import React, { FC, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme, TextField as MuiTextField } from '@mui/material';

import { Box, BoxProps } from '~/components/Box';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';

import { shortenUrl } from './helpers';

const { useColorScheme, Button } = ToucanComponents;

interface LinkCopyProps extends BoxProps {
  children: string;
}

const copyUrl = async (copyString: string, onCopiedToClipboard = () => {}) => {
  await navigator.clipboard.writeText(copyString);
  onCopiedToClipboard();
  ContentMessengers.trackEvent({
    eventName: 'User Shared a Referral',
    properties: {
      sharedTo: 'copyLink',
      source: 'referral-panel',
    },
  });
};

export const LinkCopy: FC<LinkCopyProps> = ({ children, ...boxProps }) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const [isCopied, setIsCopied] = useState(false);

  return (
    <Box {...boxProps}>
      <MuiTextField
        variant="outlined"
        fullWidth
        type="text"
        value={shortenUrl(children)}
        css={css`
          & .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline,
          & .MuiOutlinedInput-root:active .MuiOutlinedInput-notchedOutline {
            border-color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
          }
          & input {
            border-radius: 4px;
            padding: ${theme.spacing(3)} ${theme.spacing(2)};
            border: 1px solid ${isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200]};
            color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[500]};
          }
        `}
      />
      <Box
        component={Button}
        // @ts-ignore: type comes from Button component
        type="utility"
        mt={1}
        onClick={() => copyUrl(children, () => setIsCopied(true))}
        width="100%"
      >
        {t(isCopied ? 'copied' : 'copyLink')}
      </Box>
    </Box>
  );
};
