/** @jsx jsx */
import React, { FC, ReactNode } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { BackButton } from '~/components/Sidebar/BackButton';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { Title } from '~/components/Sidebar/Title';

const { Typography, useColorScheme } = ToucanComponents;

interface SlideHeaderProps {
  subHeader?: string;
  titleRightContent?: ReactNode;
  bottomContent?: ReactNode;
  isBackButtonVisible?: boolean;
}

export const SlideHeader: FC<SlideHeaderProps> = ({
  children,
  subHeader,
  titleRightContent,
  bottomContent,
  isBackButtonVisible = true,
}) => {
  const theme = useTheme();
  const { onNavigateBack } = useSidebarContext();
  const { textLevel2 } = useColorScheme();

  return (
    <React.Fragment>
      {isBackButtonVisible && (
        <BackButton
          css={css`
            ${(subHeader || children || titleRightContent) && `margin-bottom: ${theme.spacing(1)};`}
          `}
          onClick={onNavigateBack}
        />
      )}

      <div
        css={css`
          display: flex;
          justify-content: space-between;
          align-items: center;
        `}
      >
        {children && <Title>{children}</Title>}
        {titleRightContent}
      </div>

      {subHeader && (
        <Typography
          variant="sm"
          color={textLevel2}
          css={css`
            margin-top: ${theme.spacing(1)};
          `}
        >
          {subHeader}
        </Typography>
      )}

      {bottomContent}
    </React.Fragment>
  );
};
