/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box, BoxProps } from '~/components/Box';

const { useColorScheme, Typography, ToucanIcon } = ToucanComponents;

interface ShareOptionProps extends BoxProps {
  icon: string;
  onClick: () => void;
}

export const ShareOption: FC<ShareOptionProps> = ({ children, icon, onClick, ...boxProps }) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();

  return (
    <Box
      {...boxProps}
      display="flex"
      flexDirection="column"
      alignItems="center"
      gap={`${theme.spacing(1)}`}
      role="button"
      css={css`
        color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
        &:hover {
          color: ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[200]};
        }
        &:active {
          color: ${isDarkMode ? ToucanColors.teal[400] : ToucanColors.blue[400]};
        }
        transition: color 100ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        cursor: pointer;
      `}
      onClick={onClick}
    >
      <Box
        component={ToucanIcon}
        // @ts-ignore: icon comes from Button component
        icon={icon}
        width="24px"
        height="24px"
        css={css`
          color: inherit;
          &:hover {
            color: inherit;
          }
          &:active {
            color: inherit;
          }
          transition: inherit;
        `}
      />
      <Typography
        variant="xs"
        isBold
        css={css`
          text-align: center;
          color: inherit;
          &:hover {
            color: inherit;
          }
          &:active {
            color: inherit;
          }
          transition: inherit;
        `}
      >
        {children}
      </Typography>
    </Box>
  );
};
