import React, { FC, ReactNode } from 'react';

import { browserVendor } from '~/configs/config';
import { ComingSoonLanguageId } from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';

import { AllowAccessAlert } from './AllowAccessAlert';
import { TryToucanAlert } from './TryToucanAlert';

/*
 Settings Alert is a component that will hold business logic
 for the visible states of the alerts that go on the settings popup.
*/
interface SettingsAlertProps {
  isUserRegistered: boolean;
  hasDomainPermissions: boolean;
  hasGlobalPermissions: boolean;
  isLoaded: boolean;
  isInPopup: boolean;
  selectedComingSoonLanguageId: ComingSoonLanguageId | null;
  canRequestPermissions: boolean;
  onRequestGlobalPermissions: () => void;
  onRequestDomainPermissions: () => Promise<void>;
}

export const SettingsAlert: FC<SettingsAlertProps> = ({
  isUserRegistered,
  hasGlobalPermissions,
  hasDomainPermissions,
  isLoaded,
  isInPopup,
  selectedComingSoonLanguageId,
  canRequestPermissions,
  onRequestGlobalPermissions,
  onRequestDomainPermissions,
}) => {
  let alertNode: ReactNode = null;

  if (browserVendor !== BrowserVendors.Firefox && isUserRegistered && canRequestPermissions) {
    alertNode = <AllowAccessAlert onRequestGlobalPermissions={onRequestGlobalPermissions} />;
  }

  if (!hasDomainPermissions) {
    alertNode = <TryToucanAlert onRequestDomainPermissions={onRequestDomainPermissions} isInPopup={isInPopup} />;
  }

  if (browserVendor === BrowserVendors.Firefox && !hasGlobalPermissions && canRequestPermissions) {
    alertNode = <AllowAccessAlert onRequestGlobalPermissions={onRequestGlobalPermissions} />;
  }

  return (
    <>
      {!isLoaded ||
      (isUserRegistered && hasGlobalPermissions) ||
      (hasGlobalPermissions && selectedComingSoonLanguageId !== null)
        ? null
        : alertNode}
    </>
  );
};
