import React, { useEffect } from 'react';

import { Box } from '~/components/Box';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { ErrorMessage } from '~/components/shared/ErrorMessage';
import { Image } from '~/components/shared/Image';
import { useReferral } from '~/hooks/useReferral';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useShareToTwitterMutation, useShareToFacebookMutation } from '~/schema/background';

import { LoadingIndicator } from './LoadingIndicator';
import { SharePremium } from './SharePremium';
import { Step } from './Step';

export const ReferralContainer = () => {
  const { t } = useTranslation();
  const [shareToTwitter] = useShareToTwitterMutation();
  const [shareToFacebook] = useShareToFacebookMutation();
  const {
    toucanRewardOfferedPerReferral,
    referralUrl,
    loading,
    errorId,
    referralSidebarTypeDescription,
  } = useReferral();

  useEffect(() => {
    ContentMessengers.trackEvent({
      eventName: 'Referrer Viewed Referral Panel',
    });
  }, []);

  return (
    <>
      <SidebarSection>
        <SlideHeader>{t('tellYourFriends')}</SlideHeader>
      </SidebarSection>
      <SidebarSection hasBottomLine={false}>
        <Image
          src="/images/sidebar/referral/graphic-invites.svg"
          alt={t('paperAirplanes')}
          display="block"
          mt={-2}
          mx={-2}
        />

        <Box pt={2}>
          <Step number={1}>{t('sendAFriend')}</Step>
          <Step number={2} mt={2}>
            {t('yourFriendCreates')}
          </Step>
          {toucanRewardOfferedPerReferral && (
            <Step number={3} mt={2}>
              {referralSidebarTypeDescription}
            </Step>
          )}
        </Box>

        {(() => {
          if (loading) {
            return <LoadingIndicator mt={2} />;
          }

          if (errorId || !referralUrl) {
            return <ErrorMessage message={t('birdGotLoose')} errorId={errorId} mt={2} />;
          }

          return (
            <SharePremium
              mt={2}
              linkUrl={referralUrl}
              onShareToFacebook={share => shareToFacebook({ share })}
              onShareToTwitter={share => shareToTwitter({ share })}
            />
          );
        })()}
      </SidebarSection>
    </>
  );
};
