/** @jsx jsx */
import React, { BaseSyntheticEvent, FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { browserVendor } from '~/configs/config';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import {
  useEnableKeyboardShortcutsMutation,
  useGetKeyboardShortcutsInfoQuery,
  useEditShortcutsMutation,
} from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';

import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, Switch, useColorScheme, Button } = ToucanComponents;

export const KeyboardShortcuts: FC = () => {
  const { t } = useTranslation();
  const { textLevel1 } = useColorScheme();
  const [enableKeyboardShortcuts] = useEnableKeyboardShortcutsMutation();
  const { data, refetch } = useGetKeyboardShortcutsInfoQuery();
  const theme = useTheme();
  const [editShortcut] = useEditShortcutsMutation();

  const onChangeEnabledState = async (event: BaseSyntheticEvent) => {
    await enableKeyboardShortcuts({ enabled: event.target.checked });
    ContentMessengers.trackEvent({
      eventName: 'Keyboard shortcuts settings toggle',
      properties: {
        enabled: event.target.checked,
      },
    });
    refetch();
  };

  const onEditShortcuts = async () => {
    await editShortcut({});

    ContentMessengers.trackEvent({
      eventName: 'Edit keyboard shortcuts',
      properties: {},
    });
  };

  return (
    <SidebarItemSeparated
      data-testid="keyboard-shortcut-switch"
      leftContent={
        <React.Fragment>
          <Typography
            variant="sm"
            isBold
            css={css`
              color: ${textLevel1};
            `}
          >
            {t('keyboardShortcuts')}
          </Typography>
          {/* TODO: remove/update when we want to start allowing editing of keyboard shortcuts for other browsers */}
          {browserVendor === BrowserVendors.Chrome && (
            <div>
              <div
                css={css`
                  width: 48px;
                  margin-right: ${theme.spacing(2)};
                `}
              />
              <Typography variant="xs">
                <Button data-testid="keyboard-shortcut-button" type="text" onClick={onEditShortcuts}>
                  {t('editShortcuts')}
                </Button>
              </Typography>
            </div>
          )}
        </React.Fragment>
      }
      rightContent={
        <Switch
          value="switch"
          isChecked={data ? data.settings.isKeyboardShortcutsEnabled : true}
          onChange={onChangeEnabledState}
          css={css`
            margin-right: 0;
          `}
        />
      }
    />
  );
};
