import { TranslationKeys } from '~/libs/i18n/types';
import { ProgressionPeriod, ProgressionType, WordListSort } from '~/schema/background';

export const TabsOrdering = [ProgressionPeriod.Yesterday, ProgressionPeriod.Today];

export const ProgressionSortMap: Record<ProgressionType, Array<{ value: WordListSort; label: TranslationKeys }>> = {
  [ProgressionType.Learned]: [
    {
      value: WordListSort.LastLearned,
      label: 'lastLearned',
    },
    {
      value: WordListSort.FirstLearned,
      label: 'firstLearned',
    },
  ],
  [ProgressionType.Viewed]: [
    {
      value: WordListSort.LastSeen,
      label: 'lastSeen',
    },
    {
      value: WordListSort.MostSeen,
      label: 'mostSeen',
    },
    {
      value: WordListSort.LeastSeen,
      label: 'leastSeen',
    },
  ],
};

export const defaultWordListSorts: Array<{ value: WordListSort; label: TranslationKeys }> = [
  {
    value: WordListSort.LastSeen,
    label: 'lastSeen',
  },
  {
    value: WordListSort.LeastSeen,
    label: 'leastSeen',
  },
  {
    value: WordListSort.MostSeen,
    label: 'mostSeen',
  },
];

export const isProgressionSortValid = (progressionType: ProgressionType, sort: WordListSort) => {
  const sortOptions = ProgressionSortMap[progressionType];
  return sortOptions.some(option => option.value === sort);
};
