import React, { FC } from 'react';

import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box, BoxProps } from '~/components/Box';
import { useTranslation } from '~/libs/i18n';

const { useColorScheme, Typography } = ToucanComponents;

interface OrLineProps extends BoxProps {}

export const OrLine: FC<OrLineProps> = boxProps => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();

  return (
    <Box {...boxProps} display="flex" alignItems="center" gap={`${theme.spacing(2)}`}>
      <Box bgcolor={isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]} height="1px" flex="1" />
      <Typography variant="xs" isBold color={isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400]}>
        {t('or')}
      </Typography>
      <Box bgcolor={isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]} height="1px" flex="1" />
    </Box>
  );
};
