/** @jsx jsx */
import React, { FC, useState, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Pulldown } from '~/components/shared/Pulldown';
import { getDurationOptions } from '~/helpers/durationHelpers';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { SnoozeDuration } from '~/schema/background';

const { Typography, useColorScheme } = ToucanComponents;

interface SnoozeDurationSelectorProps {
  duration: SnoozeDuration;
  onDurationChange: (duration: SnoozeDuration) => void;
  domain: String;
}

const SnoozeDurationSelector: FC<SnoozeDurationSelectorProps> = ({ duration, onDurationChange, domain }) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const [localDuration, setLocalDuration] = useState<SnoozeDuration>(() => duration);
  const durationOptions = getDurationOptions(t);

  // TODO: only show save when changed.
  const onSelectChange = (value: string | number) => {
    const selectedDuration = value as SnoozeDuration;
    setLocalDuration(selectedDuration);

    // track only if 'indefinitely' duration is being switched out
    if (selectedDuration === SnoozeDuration.Indefinitely) {
      const eventName = 'Extension unsnoozed indefinitely';
      const properties = {
        URL: domain,
        timeUnSnoozed: new Date().toLocaleTimeString(),
        dateUnSnoozed: new Date().toISOString(),
      };
      ContentMessengers.trackEvent({ eventName, properties });
    }
  };

  const trackChangeDuration = (changedDuration: SnoozeDuration) => {
    let eventName;
    switch (changedDuration) {
      case SnoozeDuration.OneHour:
        eventName = 'Extension snoozed for 1 hour';
        break;
      case SnoozeDuration.Evening:
        eventName = 'Extension snoozed until 6PM';
        break;
      case SnoozeDuration.Tomorrow:
        eventName = 'Extension snoozed until tomorrow';
        break;
      case SnoozeDuration.DailyNineToSix:
        eventName = 'Extension snoozed daily between 9AM and 6PM';
        break;
      case SnoozeDuration.Indefinitely:
      default:
        eventName = 'Extension snoozed indefinitely';
        break;
    }

    const properties = {
      URL: domain,
      timeSnoozed: new Date().toLocaleTimeString(),
      dateSnoozed: new Date().toISOString(),
    };
    ContentMessengers.trackEvent({ eventName, properties });
  };

  useEffect(() => {
    if (localDuration) {
      trackChangeDuration(localDuration);
      onDurationChange(localDuration);
    }
  }, [localDuration]);

  return (
    <div
      css={css`
        width: 100%;
        & .MuiFormControl-root {
          width: 100%;
        }
      `}
    >
      <Pulldown
        label={t('setPauseDuration')}
        minWidth={150}
        onChange={onSelectChange}
        value={duration}
        options={Object.values(durationOptions)}
      />
      {localDuration === SnoozeDuration.Indefinitely && (
        <Typography
          variant="xs"
          css={css`
            color: ${isDarkMode ? ToucanColors.red[200] : ToucanColors.red[300]};
            margin-top: ${theme.spacing(2)};
          `}
        >
          {t('optionMeanToucanWithNoLongerWork')}
        </Typography>
      )}
    </div>
  );
};

export default SnoozeDurationSelector;
