import React, { FC } from 'react';

import { Box, BoxProps } from '@mui/material';

interface SidebarItemSeparatedProps extends BoxProps {
  leftContent?: React.ReactNode;
  rightContent?: React.ReactNode;
  rightContentAlignItems?: 'flex-start' | 'center' | 'flex-end';
}

export const SidebarItem: FC = ({ children }) => <Box py={1}>{children}</Box>;

export const SidebarItemSeparated: FC<SidebarItemSeparatedProps> = ({
  leftContent,
  rightContent,
  rightContentAlignItems = 'flex-start',
  ...boxProps
}) => (
  <Box py={1} display="flex" {...boxProps}>
    <Box>{leftContent}</Box>
    <Box marginLeft="auto" pl={2} display="flex" alignItems={rightContentAlignItems}>
      {rightContent}
    </Box>
  </Box>
);
