/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { motion, AnimatePresence } from 'framer-motion';

import { Image } from '~/components/shared/Image';
import Config from '~/configs/config';
import { getLanguageByLanguageId } from '~/helpers/languageHelpers';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';
import { ComingSoonLanguageId, SnoozeDuration } from '~/schema/background';
import { LanguageId } from '~/schema/server';

import SnoozeDurationSelector from './SnoozeDurationSelector';

const { Typography, Button, useColorScheme } = ToucanComponents;

interface EnableExtensionProps {
  disabledDuration: SnoozeDuration | null;
  onDisabledDurationChange: (duration: SnoozeDuration) => Promise<void>;
  domain: string;
  selectedComingSoonLanguageId?: ComingSoonLanguageId | null;
}

const fadeInOutVariants = {
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
    transition: { ease: 'easeIn', duration: 0.3 },
  },
  close: {
    opacity: 0,
    transition: { ease: 'easeOut', duration: 0.3 },
  },
};

export const EnableExtension: FC<EnableExtensionProps> = ({
  disabledDuration,
  onDisabledDurationChange,
  domain,
  selectedComingSoonLanguageId,
}) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { upcomingLanguages } = useLanguageInfo();
  const { isDarkMode } = useColorScheme();

  const DurationIncrements: Record<SnoozeDuration, string> = {
    [SnoozeDuration.FiveMinutes]: t('forFiveMinutes'),
    [SnoozeDuration.OneHour]: t('forOneHour'),
    [SnoozeDuration.Evening]: t('until6pm'),
    [SnoozeDuration.Tomorrow]: t('untilTomorrow'),
    [SnoozeDuration.DailyNineToSix]: t('daily9amTo6pm'),
    [SnoozeDuration.Indefinitely]: t('indefinitely'),
  };

  // TODO: Time selection should bubble up from SnoozeDurationSelector.
  // currently there is a bug in the selector component in toucan-design
  const onEditDisabledDuration = (duration: SnoozeDuration) => {
    onDisabledDurationChange(duration);
  };

  const onChooseAnotherLanguage = () => {
    window.open(`${Config.siteURL}/onboarding`, '_blank');
  };

  return (
    <React.Fragment>
      {disabledDuration === SnoozeDuration.Indefinitely &&
      selectedComingSoonLanguageId &&
      upcomingLanguages[selectedComingSoonLanguageId] ? (
        <div
          css={css`
            display: flex;
            flex-direction: column;
            align-items: center;
          `}
        >
          <div
            css={css`
              display: flex;
              align-items: center;
              margin-bottom: ${theme.spacing(1)};
            `}
          >
            <Image
              src={upcomingLanguages[selectedComingSoonLanguageId].flagRound}
              alt={`${upcomingLanguages[selectedComingSoonLanguageId].language} flag`}
              width="24px"
              height="24px"
              css={css`
                margin-right: ${theme.spacing(1)};
              `}
            />
            <Typography
              isBold
              variant="sm"
              css={css`
                color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]};
              `}
            >
              {selectedComingSoonLanguageId === 'other'
                ? t('moreLanguagesAreComingSoon')
                : t('langIsComingSoon', {
                    language: getLanguageByLanguageId((selectedComingSoonLanguageId as unknown) as LanguageId),
                    context: selectedComingSoonLanguageId,
                  })}
            </Typography>
          </div>
          <Typography
            variant="xs"
            css={css`
              color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[400]};
              margin-bottom: ${theme.spacing(1)};
            `}
          >
            {t('untilThenToucansPausing')}
          </Typography>
          <Button type="text" onClick={onChooseAnotherLanguage}>
            <Typography
              isBold
              variant="xs"
              css={css`
                color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.blue[300]};
              `}
            >
              {t('chooseAnotherLanguage')}
            </Typography>
          </Button>
        </div>
      ) : (
        <div
          css={css`
            width: 100%;
          `}
        >
          <React.Fragment>
            <Typography
              variant="sm"
              isBold
              css={css`
                color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[500]};
                margin: ${theme.spacing(2)} 0;
                text-align: center;
              `}
            >
              <Trans
                i18nKey="toucanIsState"
                values={{ state: t('paused') }}
                components={[
                  <span
                    css={css`
                      color: ${isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400]};
                    `}
                  >
                    {t('off')}
                  </span>,
                ]}
              />
              {disabledDuration !== SnoozeDuration.Indefinitely && disabledDuration && (
                <AnimatePresence exitBeforeEnter>
                  <motion.span
                    key="snooze-length"
                    initial="hidden"
                    animate="visible"
                    exit="close"
                    variants={fadeInOutVariants}
                  >
                    &nbsp;
                    {DurationIncrements[disabledDuration]}
                  </motion.span>
                </AnimatePresence>
              )}
            </Typography>
            <AnimatePresence exitBeforeEnter>
              <motion.div
                key="snooze-duration-selector"
                initial="hidden"
                animate="visible"
                exit="close"
                variants={fadeInOutVariants}
              >
                {disabledDuration === SnoozeDuration.Indefinitely && (
                  <Typography
                    variant="xs"
                    css={css`
                      color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[400]};
                      margin-bottom: ${theme.spacing(2)};
                    `}
                  >
                    {t('switchToucanBackOnOrEditDuration')}
                  </Typography>
                )}
                {disabledDuration && (
                  <SnoozeDurationSelector
                    domain={domain}
                    duration={disabledDuration}
                    onDurationChange={onEditDisabledDuration}
                  />
                )}
              </motion.div>
            </AnimatePresence>
          </React.Fragment>
        </div>
      )}
    </React.Fragment>
  );
};
