import React from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { HorizontalLine } from '~/components/Sidebar/HorizontalLine';

const { Typography, useColorScheme, ToucanIcon } = ToucanComponents;

interface TopicItemProps {
  label: string;
  asideLabel: string;
  onClick: (topicId: string) => void;
  topicId: string;
}

export const TopicItem = ({ label, asideLabel, onClick, topicId }: TopicItemProps) => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  return (
    <>
      <Box
        data-testid="learning-shortcut-topic-item"
        px={4}
        py={2}
        role="button"
        tabIndex={0}
        onClick={() => {
          onClick(topicId);
        }}
        onKeyDown={e => {
          if (e.key === 'Enter') {
            onClick(topicId);
          }
        }}
        display="flex"
        css={css`
          cursor: pointer;
          outline: none;
          &:focus {
            p,
            svg {
              text-decoration: underline;
            }
          }
          &:hover {
            p,
            svg {
              color: ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[500]};
            }
          }
          p,
          svg {
            color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[400]};
          }
        `}
      >
        <Box flex="1">
          <Typography variant="sm" isBold>
            {label}
          </Typography>
        </Box>
        <Box
          flex="0"
          display="flex"
          alignItems="center"
          css={css`
            svg {
              margin-left: ${theme.spacing(1)};
            }
          `}
        >
          <Typography variant="sm">{asideLabel}</Typography>
          <ToucanIcon icon="chevron-right" />
        </Box>
      </Box>
      <HorizontalLine heightPixels={1} />
    </>
  );
};
