/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme, Box } from '@mui/material';
import { motion, AnimatePresence } from 'framer-motion';

import { LottieAnimation } from '~/components/shared/LottieAnimation';
import { useOnboarding } from '~/hooks/useOnboarding';
import { ChecklistItemAll, ChecklistOnClick } from '~/schema/background';

import { CheckMark } from './CheckMark';

const { Typography, useColorScheme, Button, ToucanIcon } = ToucanComponents;

interface ListItemProps {
  onClickType?: ChecklistOnClick | null;
  isCompleted: boolean;
  item?: ChecklistItemAll | null;
  filePath?: string | null;
  title?: string | null;
  refetch: () => void;
  canRequestPermissions: boolean;
}

export const ListItem: FC<ListItemProps> = ({
  onClickType,
  isCompleted,
  item,
  filePath,
  title,
  refetch,
  canRequestPermissions,
}) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { isExpanded, onClickFunction } = useOnboarding(refetch);

  const shouldHidePermissions = !canRequestPermissions && item === ChecklistItemAll.HasGrantedPermissions;
  const shouldShowLink = !isCompleted && !shouldHidePermissions;

  return (
    <Box display="flex" flexDirection="column" width="100%" justifyContent="center">
      <Box display="flex" alignItems="center">
        <Box pr={2}>
          <CheckMark isCompleted={isCompleted} />
        </Box>
        <Typography
          variant="xs"
          isBold={!isCompleted}
          css={css`
            color: ${ToucanColors.gray[isDarkMode ? 200 : 500]};
            ${isCompleted && `text-decoration: line-through;`}
          `}
        >
          {shouldShowLink ? (
            <Box display="flex" flexDirection="column" alignItems="flex-start">
              <Box
                css={css`
                  path {
                    fill: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
                  }
                  :hover {
                    path {
                      fill: ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[200]};
                    }
                  }
                  :active {
                    path {
                      fill: ${isDarkMode ? ToucanColors.teal[400] : ToucanColors.blue[400]};
                    }
                  }
                `}
              >
                <Button
                  type="text"
                  onClick={onClickType ? () => onClickFunction({ onClickType }) : () => {}}
                  css={css`
                    text-align: left;
                    display: inline-block;
                  `}
                >
                  <Box display="flex" alignItems="center" mb={1}>
                    {title}
                    {onClickType === ChecklistOnClick.ShowLottieAnimation && (
                      <ToucanIcon
                        icon={`chevron-${isExpanded ? 'up' : 'down'}`}
                        css={css`
                          margin-left: ${theme.spacing(1)};
                          width: 12px;
                          height: 12px;
                        `}
                      />
                    )}
                  </Box>
                </Button>
              </Box>
              <AnimatePresence>
                {isExpanded && filePath && (
                  <motion.div
                    key={item}
                    initial={{ height: '0%', opacity: 0 }}
                    animate={{ height: '100%', opacity: 1, transition: { ease: 'easeIn', duration: 0.3 } }}
                    exit={{ height: '0%', opacity: 0, transition: { ease: 'easeOut', duration: 0.3 } }}
                  >
                    <LottieAnimation
                      filePath={filePath}
                      loop
                      rendererSettings={{ preserveAspectRatio: 'xMidYMin slice' }}
                      style={{ width: '274px' }}
                      isStopped={isExpanded}
                    />
                  </motion.div>
                )}
              </AnimatePresence>
            </Box>
          ) : (
            <Box display="flex" alignItems="center" mb={1}>
              {title}
            </Box>
          )}
        </Typography>
      </Box>
    </Box>
  );
};
