/** @jsx jsx */
import { FC, memo } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

const { Typography, useColorScheme } = ToucanComponents;

interface TitleProps {
  children: string;
}

export const Title: FC<TitleProps> = memo(({ children }) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();

  return (
    <Typography
      variant="sm"
      isBold
      css={css`
        color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
        padding-bottom: ${theme.spacing(1)};
      `}
    >
      {children}
    </Typography>
  );
});
