import qs from 'query-string';

const TWITTER_SHARE_INTENT_ENDPOINT = 'https://twitter.com/intent/tweet';

const FACEBOOK_SHARE_INTENT_ENDPOINT = 'https://www.facebook.com/dialog/share';
const FACEBOOK_APP_ID = 3686080981506760;

export const getFacebookShareUrl = ({ url }: { url: string }) =>
  qs.stringifyUrl({
    url: FACEBOOK_SHARE_INTENT_ENDPOINT,
    query: {
      app_id: FACEBOOK_APP_ID,
      display: 'popup',
      href: url,
    },
  });

export const getTwitterShareUrl = ({ url, text }: { url: string; text?: string }) =>
  qs.stringifyUrl({
    url: TWITTER_SHARE_INTENT_ENDPOINT,
    query: {
      text,
      url,
    },
  });
