/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { Image } from '~/components/shared/Image';
import { Language } from '~/constants/language';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';
import { TranslationKeys, Variables } from '~/libs/i18n/types';
import { ProgressionPeriod } from '~/schema/background';
import { LanguageId } from '~/schema/server';
import { OverlayName } from '~/types/sidebar';

interface TopPercentProps {
  percent: number;
  targetLanguage: string | Language;
  period: ProgressionPeriod;
}

const { Typography, ProgressBar, Button, ToucanIcon } = ToucanComponents;

const getTopPercentCopy = (
  {
    percent,
    targetLanguage,
    period,
    targetLanguageId,
  }: {
    percent: number;
    targetLanguage: string | Language;
    period: ProgressionPeriod;
    targetLanguageId: LanguageId;
  },
  translate: (key: TranslationKeys, variables?: Variables) => string,
) => {
  if (percent > 50) {
    return translate('youreOnYourWayToLearningLang', {
      language: targetLanguage,
      context: targetLanguageId as string,
    });
  }

  if (ProgressionPeriod.Today === period) {
    return translate('youreCurrentlyInTheTopPercent', {
      percent,
      language: targetLanguage,
      context: targetLanguageId as string,
    });
  }

  return translate('youFinishedInTheTopPercent', { language: targetLanguage, percent });
};

export const TopPercent: FC<TopPercentProps> = ({ percent, targetLanguage, period }) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { onNavigateSidebar } = useSidebarContext();
  const { targetLanguageInfo } = useLanguageInfo();

  return (
    <Box pb={3} borderBottom="1px solid rgba(245, 245, 245, 0.1);">
      <Box display="flex">
        <Box>
          <Typography variant="sm" isBold color={ToucanColors.white}>
            {getTopPercentCopy(
              {
                percent,
                targetLanguage: targetLanguageInfo?.language ?? targetLanguage,
                period,
                targetLanguageId: targetLanguageInfo?.languageId ?? LanguageId.Es,
              },
              t,
            )}
          </Typography>
          <Button
            type="text"
            onClick={() => onNavigateSidebar({ overlay: OverlayName.MakingProgress })}
            css={css`
              margin-top: ${theme.spacing(0.5)};
            `}
          >
            <Typography
              variant="2xs"
              isBold
              color={ToucanColors.white}
              css={css`
                display: flex;
                align-items: center;
              `}
            >
              {t('learnMore')}
              <ToucanIcon
                icon="chevron-right"
                css={css`
                  color: inherit;
                  width: 10px;
                  height: 10px;
                  margin-left: ${theme.spacing(0.5)};
                `}
              />
            </Typography>
          </Button>
        </Box>
        {percent < 50 && (
          <Box pl={2}>
            <Image
              width="56px"
              height="56px"
              src={`images/progress/${period === ProgressionPeriod.Today ? 'trophy' : 'fire'}.svg`}
              alt={t('greatJob')}
            />
          </Box>
        )}
      </Box>
      {period === ProgressionPeriod.Yesterday && percent <= 10 && (
        <Box mt={2} position="relative">
          <ProgressBar
            beginPercentage={0}
            endPercentage={100}
            barColor={ToucanColors.green[300]}
            barCompleteTransitionColor={ToucanColors.green[300]}
            barTransitionOptions={{ duration: 0.3, delay: 0.5 }}
          />
          <Box position="absolute" top={0} bottom={0} right={8}>
            <Typography variant="2xs" color={ToucanColors.white}>
              {t('top10Percent')}
            </Typography>
          </Box>
        </Box>
      )}
    </Box>
  );
};
