import React, { FC, useState } from 'react';

import { useTranslation } from '~/libs/i18n';
import { useGetTabInjectionQuery, useInjectIntoPageMutation } from '~/schema/background';

import { Alert } from './Alert';

interface TryToucanAlertProps {
  isInPopup: boolean;
  onRequestDomainPermissions: () => Promise<void>;
}

export const TryToucanAlert: FC<TryToucanAlertProps> = ({ onRequestDomainPermissions, isInPopup }) => {
  const { t } = useTranslation();
  const { data, refetch } = useGetTabInjectionQuery({
    skip: !isInPopup,
  });
  const [injectIntoPage] = useInjectIntoPageMutation();
  const shouldShowInject = isInPopup && data && !data.browser?.activeTab.isToucanInjected;
  const [injecting, setInjecting] = useState<boolean>(false);
  const componentProps = shouldShowInject
    ? {
        title: t('wantToucanOnThisSite'),
        description: t('tapBelowToTest'),
        cta: t('testToucanOut'),
        onClick: async () => {
          if (injecting) {
            return;
          }
          setInjecting(true);
          await injectIntoPage({});
          setInjecting(false);
          await refetch();
        },
        secondaryCta: t('alwaysAllowOnTheSite'),
        onSecondaryClick: onRequestDomainPermissions,
      }
    : {
        title: t('toucanIsTemporarilyActivated'),
        description: t('ifYouLikeToucanAllowAccess'),
        cta: t('alwaysAllowOnTheSite'),
        onClick: onRequestDomainPermissions,
      };
  return <Alert {...componentProps} />;
};
