/** @jsx jsx */
import { useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { useForm } from 'react-hook-form';

import { ErrorMessage } from '~/components/shared/ErrorMessage';
import Config from '~/configs/config';
import { email as emailRegex } from '~/constants/regex';
import { ContentMessageProducer } from '~/contentMessageProducer';
import { resolveErrors } from '~/helpers/errors';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';
import ContentMessengers from '~/messengers/contentMessengers';

import { useAnonymousUserSettings } from '../AnonymousUserExtensionSettingsContext';

import { FormFooter } from './FormFooter';

const { Typography, useColorScheme, Link, Button, TextField } = ToucanComponents;

interface LoginInput {
  email: string;
  password: string;
}

export const Login = () => {
  const theme = useTheme();
  const {
    register,
    handleSubmit,
    formState: { errors: clientErrors },
  } = useForm<LoginInput>();
  const { isDarkModeEnabled, closeAccountCreationWidget, isInPopup } = useAnonymousUserSettings();
  const { t } = useTranslation();
  const { textLevel3 } = useColorScheme();

  let errors = resolveErrors<{
    email?: string;
    password?: string;
  }>(clientErrors);

  useEffect(() => {
    ContentMessengers.trackEvent({
      eventName: 'Login Screen Viewed',
      properties: {
        isInGuidedOnboarding: false,
      },
    });
  }, []);

  const onLogIn = async (inputs: LoginInput) => {
    const { data, error: loginErrors } = await ContentMessageProducer.loginUser(inputs);

    if (loginErrors) {
      errors = resolveErrors(loginErrors);
    }

    if (data?.loginUser.email || (loginErrors && !loginErrors.length)) {
      ContentMessengers.trackEvent({
        eventName: 'Login Success',
        properties: {
          isInGuidedOnboarding: false,
        },
      });
      if (isInPopup) {
        window.close();
        // reload page in order to display personalization widget
        ContentMessengers.reloadCurrentPage();
      } else {
        // Check user registration status
        ContentMessengers.fetchRegistrationStatus();
        closeAccountCreationWidget();
      }
    }
  };

  return (
    <div>
      <form
        css={css`
          margin-bottom: ${theme.spacing(2)};
        `}
        onSubmit={handleSubmit(onLogIn)}
      >
        <TextField
          label={t('email')}
          hasError={!!errors.email}
          helperText={errors.email?.message}
          type="email"
          css={css`
            margin-bottom: ${theme.spacing(1)};
          `}
          {...register('email', {
            required: `${t('pleaseEnterValidEmail')}`,
            pattern: {
              value: emailRegex,
              message: t('pleaseEnterValidEmail'),
            },
          })}
          isDarkMode={isDarkModeEnabled}
        />
        <TextField
          type="password"
          label={t('password')}
          canPasswordBeToggled
          hasError={!!errors.password}
          helperText={errors.password?.message}
          {...register('password', {
            required: `${t('passwordMustBeEightChars')}`,
            minLength: {
              value: 8,
              message: t('passwordMustBeEightChars'),
            },
          })}
          css={css`
            & .MuiInputBase-root {
              box-sizing: border-box;
              height: 70px;
            }
          `}
          showCta={t('show')}
          hideCta={t('hide')}
          isDarkMode={isDarkModeEnabled}
          inputProps={{
            autoCapitalize: 'off',
          }}
        />
      </form>
      <ErrorMessage
        message={errors.message}
        mt={1}
        mb={1}
        css={css`
          margin-bottom: ${theme.spacing(2)};
        `}
        frameless
      />
      <Typography
        variant="xs"
        color={textLevel3}
        css={css`
          margin-bottom: ${theme.spacing(2)};
        `}
      >
        <Trans
          i18nKey="byClickingLogIn"
          components={[
            <Link href={`${Config.siteURL}/terms`} target="_blank" />,
            <Link href={`${Config.siteURL}/privacy`} target="_blank" />,
          ]}
        />
      </Typography>{' '}
      <Button
        size="medium"
        type="utility"
        onClick={handleSubmit(onLogIn)}
        css={css`
          width: 100%;
          margin-bottom: ${theme.spacing(3)};
        `}
      >
        {t('logIn')}
      </Button>
      <FormFooter />
    </div>
  );
};
