import React from 'react';

import { Box } from '@mui/material';

import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { useNotificationFeedQuery } from '~/schema/background';
import { FeedNotificationLayout } from '~/schema/server';

import { ChecklistNotification } from '../Checklist/ChecklistNotification';
import { FeedNotifications } from '../types';

import { CTA } from './CTA';
import { Description } from './Description';
import { Image } from './Image';
import { Title } from './Title';

interface FeedProps {
  prependedNotifications?: FeedNotifications;
}

export const Feed = ({ prependedNotifications = [] }: FeedProps) => {
  const { data, loading } = useNotificationFeedQuery();
  const notifications = data?.me?.notificationFeed ?? [];
  const amountCompleted = data?.onboarding?.checklist?.amountCompleted ?? 0;
  const allNotifications = [...prependedNotifications, ...notifications];

  return (
    <Box data-testid="sidebar-content" aria-live="polite" aria-busy={loading ? 'true' : 'false'}>
      <>
        <ChecklistNotification amountCompleted={amountCompleted} />
        {allNotifications.map(notification => {
          const { cta, ctaURL, description, imageURL, layout, title } = notification;

          return (
            <SidebarSection key={title}>
              {layout === FeedNotificationLayout.Medium ? (
                <Box display="flex" alignItems="center">
                  <Box pr={2}>
                    <Title>{title}</Title>
                    {description && <Description>{description}</Description>}
                    {cta && <CTA ctaURL={ctaURL}>{cta}</CTA>}
                  </Box>
                  <Box>
                    <Image imageURL={imageURL} layout={layout} />
                  </Box>
                </Box>
              ) : (
                <>
                  {layout !== FeedNotificationLayout.Small && <Image imageURL={imageURL} layout={layout} />}
                  <Title>{title}</Title>
                  {description && <Description>{description}</Description>}
                  {cta && <CTA ctaURL={ctaURL}>{cta}</CTA>}
                </>
              )}
            </SidebarSection>
          );
        })}
      </>
    </Box>
  );
};
