import React, { FC } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { InlineQuiz } from '~/components/InlineTranslations/InlineQuiz';
import { QuizSize } from '~/components/InlineTranslations/InlineQuiz/useQuizSize';
import { QuizType } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

interface WordQuizProps {
  translationIds: Array<string>;
  quizType: QuizType;
  onQuizExit: () => void;
  quizStatus: QuizStatus;
}

const { useColorScheme } = ToucanComponents;

export const WordQuiz: FC<WordQuizProps> = ({ quizStatus, quizType, ...props }) => {
  const { isDarkMode } = useColorScheme();
  const removePadding = quizType !== QuizType.MultiPractice && quizStatus === QuizStatus.Finished;

  return (
    <InlineQuiz
      {...props}
      quizType={quizType}
      quizSize={QuizSize.Medium}
      width="100%"
      bgcolor={isDarkMode ? ToucanColors.gray[500] : ToucanColors.white}
      ml={0}
      mr={0}
      px={removePadding ? 0 : 4}
      pt={removePadding ? 0 : 2}
      height="100%"
      hideFooter
      onSetIdle={() => {}}
      isInlineTranslation={false}
    />
  );
};
