import React, { useState } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Skeleton } from '@mui/material';

import { Box } from '~/components/Box';
import { FocusManagement } from '~/components/LearningShortcuts';
import { Slide } from '~/components/shared/Slide';
import { FeatureName } from '~/constants/features';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslation } from '~/libs/i18n';
import {
  ProgressionPeriod,
  ProgressionType,
  QuizType,
  useDoesCurrentLanguageVectorSupportShortcutsQuery,
  WordListSort,
} from '~/schema/background';
import { OverlayName, PanelName, SlideName } from '~/types/sidebar';

import { Overlay } from '../../Overlay';
import { useSidebarContext } from '../../SidebarContext';

import { ProgressOverview } from './ProgressOverview';
import { MakingProgress } from './ProgressOverview/MakingProgress';
import { ProgressSlides } from './ProgressSlides';
import { WordList } from './WordList';
import { ProgressHeader } from './layout/ProgressHeader';
import { isProgressionSortValid, ProgressionSortMap } from './shared';

const { useColorScheme } = ToucanComponents;

interface ProgressContainerProps {
  quizInfo: {
    translationIds: Array<string>;
    quizType: QuizType;
  } | null;
  onQuizEnter: (translationId: string, quizType: QuizType) => void;
  onQuizExit: () => void;
  isInPopup: boolean;
}

export const ProgressContainer = ({ quizInfo, onQuizEnter, onQuizExit, isInPopup }: ProgressContainerProps) => {
  const { t } = useTranslation();
  const { targetLanguage } = useUserInfo();
  const [period, setPeriod] = useState<ProgressionPeriod>(ProgressionPeriod.Today);
  const [progressionType, setProgressionType] = useState<ProgressionType>(ProgressionType.Viewed);
  const [sort, setSort] = useState<WordListSort>(WordListSort.LastSeen);
  const { isDarkMode } = useColorScheme();
  const { onNavigateSidebar } = useSidebarContext();
  const { data, loading } = useDoesCurrentLanguageVectorSupportShortcutsQuery();
  const isShortcutsEnabled =
    useFeatureEnabled(FeatureName.Shortcuts) && data?.doesCurrentLanguageVectorSupportShortcuts;

  // Current sort resolves a valid sort, for the word list to avoid issues with bad sorts on progression types
  const currentSort = isProgressionSortValid(progressionType, sort)
    ? sort
    : ProgressionSortMap[progressionType]?.[0].value ?? WordListSort.LastSeen;

  if (loading) {
    return (
      <Skeleton
        variant="rectangular"
        height="100%"
        width="100%"
        style={{ backgroundColor: isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200] }}
      />
    );
  }

  return (
    <Box position="relative" height="100%" display="flex" flexDirection="column">
      {!isShortcutsEnabled && (
        <ProgressHeader
          title={t('progress')}
          onChangePeriod={setPeriod}
          period={period}
          progressionType={progressionType}
          sort={currentSort}
          onChangeSort={setSort}
          isPeriodShown={!isShortcutsEnabled}
        />
      )}
      {isShortcutsEnabled ? (
        <FocusManagement isInPopup={isInPopup} />
      ) : (
        <Box display="flex" flex="1" bgcolor={isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]}>
          <ProgressSlides display="flex" flexDirection="column" flex="1">
            <Slide
              slideName={SlideName.ProgressOverview}
              css={css`
                height: auto;
                flex: 1;
                display: flex;
                z-index: 0;
              `}
            >
              <ProgressOverview
                targetLanguage={targetLanguage}
                period={period}
                onItemClicked={itemProgressionType => {
                  setProgressionType(itemProgressionType);
                  onNavigateSidebar({ panel: PanelName.Progress, slide: SlideName.WordList });
                }}
              />
            </Slide>
            <Slide
              slideName={SlideName.ProgressOverview}
              css={css`
                z-index: 2;
                height: 0;
                flex: 0;
              `}
            >
              <Overlay overlayName={OverlayName.MakingProgress}>
                <MakingProgress />
              </Overlay>
            </Slide>
            <Slide
              slideName={SlideName.WordList}
              css={css`
                height: auto;
                flex: 1;
                display: flex;
              `}
            >
              <WordList
                key={period}
                progressionType={progressionType}
                sort={currentSort}
                period={period}
                quizInfo={quizInfo}
                onQuizEnter={onQuizEnter}
                onQuizExit={onQuizExit}
                onProgressionTypeChange={setProgressionType}
              />
            </Slide>
          </ProgressSlides>
        </Box>
      )}
    </Box>
  );
};
