import React, { useState, FC } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { Pinchpoints } from '~/constants/pinchpoints';
import { practiceSavedWordsUpperLimit } from '~/constants/sidebar';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';
import { TranslationKeys } from '~/libs/i18n/types';
import { SavedWordsCallout, SavedWordsCalloutStatus } from '~/schema/background';

import { WordListCard } from '../Progress/WordList/WordListCard';

import { SavedWordCallout } from './SavedWordCallout';

const { useColorScheme, Typography, ProgressBar, Button, ToucanIcon } = ToucanComponents;

interface PostPracticeOverlayProps {
  isPremiumSubscribed: boolean;
  practicedTranslations: Array<{
    translationId: string;
    sourcePhrase: string;
    targetPhrase: string;
    isSaved: boolean;
    isKnown: boolean;
    progressionScore: number;
    isFluent: boolean;
    targetLanguage: string;
  }>;
  amountPracticed: number;
  amountOfSavedWords: number;
  callout?: Partial<SavedWordsCallout>;
  refetch: () => Promise<void>;
  onClose: () => void;
  onContinue: () => void;
  onOpenSubscribePage: (options: { pinchpoint: Pinchpoints }) => void;
}

const PostPracticeTitles: Array<TranslationKeys> = ['wellDone', 'keepItUp'];
const PostPracticeIcons: Array<string> = ['/images/sidebar/savedWords/fire.svg', '/images/sidebar/savedWords/tada.svg'];

export const PostPracticeOverlay: FC<PostPracticeOverlayProps> = ({
  practicedTranslations,
  amountPracticed,
  amountOfSavedWords,
  callout,
  isPremiumSubscribed,
  onContinue,
  onClose,
  onOpenSubscribePage,
  refetch,
}) => {
  const upperLimit = Math.min(amountOfSavedWords, practiceSavedWordsUpperLimit);
  const { t } = useTranslation();
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { targetLanguageInfo } = useLanguageInfo();
  const languageKey = targetLanguageInfo?.key;
  const percent = Math.floor(Math.min(100, amountPracticed === 0 ? 0 : (amountPracticed / upperLimit) * 100));
  const [randomTitle] = useState(() => PostPracticeTitles[Math.floor(Math.random() * PostPracticeTitles.length)]);
  const [randomIcon] = useState(() => PostPracticeIcons[Math.floor(Math.random() * PostPracticeIcons.length)]);
  const wordsLeft = upperLimit - amountPracticed > 0;
  const icon = wordsLeft ? randomIcon : '/images/progress/trophy.svg';
  const title = wordsLeft ? t(randomTitle) : t('youreSimplyAwesome');
  const subtitle = wordsLeft
    ? t('numberOfWordLeftToPractice', { number: upperLimit - amountPracticed })
    : t('youPracticedYourSavedWords', { number: amountPracticed });

  const shouldUseCallout = SavedWordsCalloutStatus.Locked === callout?.status;

  const buttonType = wordsLeft ? 'primary' : 'secondary';
  const isButtonReversed = wordsLeft ? false : isDarkMode;
  const buttonText = wordsLeft ? t('continue') : t('keepPracticing');

  return (
    <Box
      height="100%"
      width="100%"
      overflow="scroll"
      bgcolor={isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]}
    >
      <Box
        bgcolor={isDarkMode ? ToucanColors.gray[500] : ToucanColors.white}
        px={4}
        py={3}
        textAlign="center"
        css={css`
          h4 {
            padding-bottom: ${theme.spacing(1)};
          }
          p {
            padding-bottom: ${theme.spacing(2)};
          }
          h4,
          p {
            color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
          }
        `}
      >
        <Image src={icon} alt={title} width="80px" height="80px" mb={2} />
        <Typography variant="md" font="secondary">
          {title}
        </Typography>
        <Typography variant="sm">{subtitle}</Typography>
        <Box pb={3}>
          <ProgressBar
            beginPercentage={0}
            endPercentage={percent}
            barColor={ToucanColors.blue[200]}
            barCompleteTransitionColor={percent === 100 ? ToucanColors.green[300] : ToucanColors.blue[300]}
          />
        </Box>
        {shouldUseCallout ? (
          <Box pb={3}>
            {/* Locked and callout to subscribe */}
            <SavedWordCallout
              title={callout?.title ?? ''}
              cta={callout?.cta ?? ''}
              icon={callout?.icon ?? ''}
              status={callout?.status ?? SavedWordsCalloutStatus.Locked}
              onClick={() => {
                onOpenSubscribePage({ pinchpoint: Pinchpoints.SavedWordsPractice });
              }}
            />
          </Box>
        ) : (
          <Box
            pb={3}
            display="flex"
            css={css`
              button {
                width: 100%;
              }
            `}
          >
            <Button type={buttonType} isReversed={isButtonReversed} onClick={onContinue}>
              {buttonText}
            </Button>
          </Box>
        )}
        <Box
          display="flex"
          justifyContent="center"
          css={css`
            svg {
              margin-left: ${theme.spacing(0.5)};
              vertical-align: middle;
              width: 10px;
            }
            path {
              fill: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
            }
            :hover {
              path {
                fill: ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[200]};
              }
            }
            :active {
              path {
                fill: ${isDarkMode ? ToucanColors.teal[400] : ToucanColors.blue[400]};
              }
            }
          `}
        >
          <Button type="text" onClick={onClose}>
            {t('viewSavedWords')}
            <ToucanIcon icon="chevron-right" />
          </Button>
        </Box>
      </Box>
      <Box pb={2}>
        <Box
          pl={2}
          pt={2}
          css={css`
            p {
              color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
            }
          `}
        >
          <Typography isBold variant="sm">
            {t('wordsPracticed')}
          </Typography>
        </Box>
        {practicedTranslations.map(translation => (
          <WordListCard
            key={translation.translationId}
            {...translation}
            hideProgressButton
            refetch={refetch}
            isPremiumSubscribed={isPremiumSubscribed}
            openSubscribePage={onOpenSubscribePage}
            languageKey={languageKey}
          />
        ))}
      </Box>
    </Box>
  );
};
