import React, { FC } from 'react';

import { css } from '@emotion/react';
import { ButtonType, ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { SavedWordsCalloutStatus } from '~/schema/background';

const { Button, Typography, useColorScheme } = ToucanComponents;

interface SavedWordCalloutProps {
  title: string;
  cta: string;
  status: SavedWordsCalloutStatus;
  icon: string;
  onClick: () => void;
}

export const SavedWordCallout: FC<SavedWordCalloutProps> = ({ title, cta, onClick, status, icon }) => {
  const { backgroundBase, isDarkMode, textAlert, textLevel1, textPromotion, uiAlert, uiPromotion } = useColorScheme();
  // TEMP: we want to bring back this button
  const showButton = SavedWordsCalloutStatus.NotEnoughWords !== status;

  let bgColor = isDarkMode ? ToucanColors.teal[600] : ToucanColors.teal[100];
  let titleColor = isDarkMode ? ToucanColors.teal[100] : ToucanColors.teal[500];
  let ctaType: ButtonType = 'primary';
  let isReversedCTA = false;

  switch (status) {
    case SavedWordsCalloutStatus.AllPracticed:
      bgColor = backgroundBase;
      titleColor = textLevel1;
      ctaType = 'secondary';
      isReversedCTA = isDarkMode;
      break;
    case SavedWordsCalloutStatus.Locked:
      bgColor = uiPromotion;
      titleColor = textPromotion;
      ctaType = 'alert';
      break;
    case SavedWordsCalloutStatus.NotEnoughWords:
      bgColor = uiAlert;
      titleColor = textAlert;
      ctaType = 'text';
      break;
    case SavedWordsCalloutStatus.Practice:
    default:
      break;
  }

  return (
    <Box borderRadius="8px" bgcolor={bgColor} p={2} display="flex" flexDirection="column">
      <Box
        display="flex"
        pb={showButton ? 2 : 0}
        css={css`
          h5 {
            color: ${titleColor};
            text-align: left;
          }
        `}
      >
        <Typography variant="xs" isBold>
          {title}
        </Typography>
        <Image src={icon} alt={title} ml={2} width="40px" height="40px" />
      </Box>
      {showButton && (
        <Box
          display="flex"
          css={css`
            button {
              width: 100%;
              ${ctaType === 'text'
                ? `
                text-align: left;
              `
                : ''}
            }
          `}
        >
          <Button isReversed={isReversedCTA} type={ctaType} onClick={onClick}>
            {cta}
          </Button>
        </Box>
      )}
    </Box>
  );
};
