import React, { useState } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Skeleton } from '@mui/material';

import { SidebarSection } from '~/components/Sidebar/SidebarSection';

const { useColorScheme } = ToucanComponents;

export const Placeholder = ({ topicAmount = 8 }: { topicAmount?: number }) => {
  const { isDarkMode } = useColorScheme();
  const [topics] = useState(() => new Array(topicAmount ?? 0).fill(0));
  return (
    <>
      <Skeleton
        variant="rectangular"
        sx={{
          mb: 3,
          bgcolor: isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[100],
          transform: 'scale(1)',
          height: '104px',
        }}
      />
      <SidebarSection hasBottomLine={false}>
        {topics.map((_, index) => (
          <Skeleton
            key={index}
            sx={{
              mb: 3,
              bgcolor: isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[100],
              borderRadius: '16px',
              transform: 'scale(1)',
            }}
          />
        ))}
      </SidebarSection>
    </>
  );
};
