import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { useTranslation } from '~/libs/i18n';

const { Typography, ToucanIcon, Button } = ToucanComponents;

interface BackButtonProps {
  className?: string;
  onClick: () => void;
}

export const BackButton: FC<BackButtonProps> = ({ className, onClick }) => {
  const { t } = useTranslation();
  const theme = useTheme();

  return (
    <Typography variant="xs" className={className}>
      <Button
        type="text"
        onClick={onClick}
        css={css`
          display: flex;
          align-items: center;
        `}
      >
        <ToucanIcon
          icon="chevron-left"
          css={css`
            color: inherit;
            width: 12px;
            height: 12px;
          `}
        />
        <span
          css={css`
            margin-left: ${theme.spacing(1)};
          `}
        >
          {t('back')}
        </span>
      </Button>
    </Typography>
  );
};
