/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme, Box } from '@mui/material';

import { ReferralCta } from '~/components/Referral/ReferralCta';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { Events } from '~/constants/events';
import { FeatureName } from '~/constants/features';
import { Pinchpoints } from '~/constants/pinchpoints';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { PanelName, SlideName } from '~/types/sidebar';

const { Typography, useColorScheme, ProgressBar: LibraryProgressBar } = ToucanComponents;

interface ProgressBarProps {
  amountCompleted: number;
  toucanRewardOfferedPerReferral?: string;
  referralCTAText: string;
}

export const ProgressBar: FC<ProgressBarProps> = ({
  amountCompleted,
  toucanRewardOfferedPerReferral,
  referralCTAText,
}) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const isReferralEnabled = useFeatureEnabled(FeatureName.ReferralsV2);
  const { onNavigateSidebar } = useSidebarContext();

  const onClickReferral = () => {
    contentMessengers.trackEvent({
      eventName: Events.ReferralCtaClicked,
      properties: {
        pinchpointLocation: Pinchpoints.OnboardingChecklistReferralCta,
      },
    });
    onNavigateSidebar({ panel: PanelName.Referral, slide: SlideName.MainReferral });
  };

  return (
    <Box display="flex" flexDirection="column" mt={2} px={4} width="100%" mb={4}>
      <Box>
        <Typography
          variant="2xs"
          isBold
          css={css`
            color: ${ToucanColors.green[isDarkMode ? 200 : 300]};
            margin-bottom: ${theme.spacing(1)};
          `}
        >
          {t('percentComplete', { amountCompleted: amountCompleted.toFixed(0) })}
        </Typography>
      </Box>
      <Box>
        <LibraryProgressBar
          beginPercentage={0}
          endPercentage={amountCompleted}
          barColor={ToucanColors.green[200]}
          barCompleteTransitionColor={ToucanColors.green[200]}
        />
      </Box>
      {isReferralEnabled && amountCompleted === 100 && toucanRewardOfferedPerReferral && (
        <ReferralCta referralCTAText={referralCTAText} pt={1} onClickReferral={onClickReferral} />
      )}
    </Box>
  );
};
