import React, { FC, ReactNode, useEffect, useRef, useState } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { AnimatePresence } from 'framer-motion';
import { AutoSizer, CellMeasurer, CellMeasurerCache, List, ListRowRenderer } from 'react-virtualized';

import { Box, MotionBox } from '~/components/Box';
import { Overlay } from '~/components/Sidebar/Overlay';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { ErrorMessage } from '~/components/shared/ErrorMessage';
import { FadeIn } from '~/components/shared/FadeIn';
import { SubscriptionRedirectContainer } from '~/components/shared/SubscriptionRedirectContainer';
import { Events, EventSources } from '~/constants/events';
import { Language } from '~/constants/language';
import { Pinchpoints } from '~/constants/pinchpoints';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { getErrorId } from '~/helpers/errors';
import { filterNull } from '~/helpers/utilityHelpers';
import { useAddDictionaryEntry } from '~/hooks/useDictionary';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { usePrevious } from '~/hooks/usePrevious';
import { useQuiz } from '~/hooks/useQuiz';
import { useSubscription } from '~/hooks/useSubscription';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { ProgressionTranslation, QuizType, useSetAlwaysTranslateMutation } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';
import { OverlayName } from '~/types/sidebar';

import { WordListCard } from '../Progress/WordList/WordListCard';
import { WordListPlaceholder } from '../Progress/WordList/WordListPlaceholder';
import { WordQuiz } from '../Progress/WordList/WordQuiz';

import { AlwaysTranslate } from './AlwaysTranslate';
import { PostPracticeOverlay } from './PostPracticeOverlay';
import { SavedWordsHeader } from './SavedWordsHeader';
import { useSavedWords } from './useSavedWords';

const { useColorScheme } = ToucanComponents;

interface WordListContainerProps {
  quizInfo: { translationIds: Array<string>; quizType: QuizType } | null;
  onQuizEnter: (translationIds: Array<string>, quizType: QuizType) => void;
  onQuizExit: () => void;
}

type WordListWord = Pick<
  ProgressionTranslation,
  | 'translationId'
  | 'sourcePhrase'
  | 'targetPhrase'
  | 'isSaved'
  | 'isKnown'
  | 'isFluent'
  | 'progressionScore'
  | 'targetLanguage'
>;

type RenderingList = Array<WordListWord | { children: ReactNode }>;

const cache = new CellMeasurerCache({
  // 138 card + 16 margin
  defaultHeight: 154,
  defaultWidth: 380,
  fixedWidth: true,
});

const isWordListWord = (row: RenderingList[number]): row is WordListWord =>
  'translationId' in row && !!row.translationId;

export const wordListRenderer: (options: {
  wordList: RenderingList;
  refetch: () => Promise<void>;
  openSubscribePage: (options: { pinchpoint: Pinchpoints }) => void;
  isPremiumSubscribed: boolean;
  languageKey: Language | undefined;
  isWordListCardDisabled?: boolean;
  isIKnowThisWordDisabled?: boolean;
}) => ListRowRenderer = ({
  wordList,
  refetch,
  openSubscribePage,
  isPremiumSubscribed,
  languageKey,
  isWordListCardDisabled,
  isIKnowThisWordDisabled,
}) => ({ index, style, parent }) => {
  const rowData = wordList[index];

  return isWordListWord(rowData) ? (
    <CellMeasurer key={rowData.translationId} cache={cache} columnIndex={0} parent={parent} rowIndex={index}>
      <WordListCard
        style={style}
        hideProgressButton
        refetch={refetch}
        isPremiumSubscribed={isPremiumSubscribed}
        openSubscribePage={openSubscribePage}
        isDisabled={isWordListCardDisabled}
        isIKnowThisWordDisabled={isIKnowThisWordDisabled}
        languageKey={languageKey}
        {...rowData}
      />
    </CellMeasurer>
  ) : (
    <CellMeasurer cache={cache} columnIndex={0} key={index} parent={parent} rowIndex={index} style={style}>
      <Box>{rowData.children}</Box>
    </CellMeasurer>
  );
};

export const SavedWordsContainer: FC<WordListContainerProps> = ({ onQuizEnter, quizInfo, onQuizExit }) => {
  const { onNavigateSidebar, hydration, activeOverlayName } = useSidebarContext();
  const [postScreenInfo, setPostScreenInfo] = useState<{ translationIds: Array<string> } | null>(null);
  const { backgroundAccent, backgroundBase, isDarkMode } = useColorScheme();
  const { targetLanguageInfo } = useLanguageInfo();
  const languageKey = targetLanguageInfo?.key;
  const highlightedTranslation = hydration?.entryTranslationId;
  const addTranslations = useAddDictionaryEntry();
  const { openSubscribePage, isPremiumSubscribed } = useSubscription();
  const { isAlwaysTranslateSavedWordsEnabled } = useUserInfo();
  const parentRef = useRef<HTMLDivElement>(null);
  const { currentQuiz, onReset } = useQuiz();
  const pastQuiz = usePrevious(quizInfo);
  const pastQuizInfo = usePrevious(currentQuiz);
  const listRef = useRef<List>(null);
  // Used to unmount the overlay
  const overlayRef = useRef<OverlayName | null>(activeOverlayName);
  overlayRef.current = activeOverlayName;
  const [setAlwaysTranslate] = useSetAlwaysTranslateMutation();
  const { t } = useTranslation();
  const { getFeaturePaywallStatus } = usePremiumExperiment();

  const isSaveWordPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.SaveWord) === PremiumFeaturePaywallStatus.Paid;
  const isIKnowThisPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.IKnowThisWord) === PremiumFeaturePaywallStatus.Paid;

  const openQuiz = (
    practicableTranslations: Array<{ translationId: string; sourcePhrase: string; targetPhrase: string }>,
  ) => {
    practicableTranslations.forEach(entry => {
      addTranslations({
        [entry.translationId]: {
          translationId: entry.translationId,
          sourcePhrase: entry.sourcePhrase,
          targetPhrase: entry.targetPhrase,
        },
      });
    });

    onQuizEnter(wordsToPractice, QuizType.MultiPractice);
    setTimeout(() => {
      onNavigateSidebar({ overlay: OverlayName.PracticeQuiz });
    }, 0);
  };

  const {
    wordListSections,
    refetch,
    loading,
    error,
    sort,
    setSort,
    wordsToPractice,
    callout,
    amountToPracticed,
    wordList,
  } = useSavedWords({
    highlightedTranslation,
    openQuiz,
    openSubscribePage,
  });

  useEffect(() => {
    if (listRef.current && wordList.length === 0) {
      cache.clearAll();
      listRef.current.forceUpdateGrid();
    }
  }, [wordList.length, listRef]);

  useEffect(() => {
    // Refetch navigate when there was a quiz but is not longer one.
    const justFinishedQuiz = pastQuizInfo?.status === QuizStatus.Finished;
    const noLongerPracticing = pastQuiz && !quizInfo;
    if (noLongerPracticing) {
      if (justFinishedQuiz) {
        // Post practice overlay
        setPostScreenInfo(pastQuiz ?? null);
      } else {
        // Just exiting the quiz
        setPostScreenInfo(null);
        onNavigateSidebar({ overlay: null });
      }
      refetch();
    }
  }, [quizInfo, pastQuiz, pastQuizInfo]);

  useEffect(() => {
    contentMessengers.trackEvent({
      eventName: Events.SavedWordsViewed,
      properties: { source: EventSources.Sidebar },
    });
    // Unmount component
    return () => {
      // Stop quiz if it's still running
      onReset();
      // Clear out the overlay
      if (overlayRef.current === OverlayName.PracticeQuiz) {
        onNavigateSidebar({ overlay: null });
      }
    };
  }, []);

  // Clear cache when we load new words
  useEffect(() => {
    // Clear list cache
    cache.clearAll();
  }, [loading]);

  return (
    <>
      <Box flex="1" display="flex" flexDirection="column" position="relative" bgcolor={backgroundAccent} zIndex={1}>
        <SavedWordsHeader
          sort={sort}
          title={t('savedWords')}
          onChangeSort={setSort}
          isPremiumLocked={isSaveWordPremiumFeature && !isPremiumSubscribed}
        />
        <Box
          display="flex"
          flexDirection="column"
          position="relative"
          bgcolor={backgroundBase}
          zIndex={1}
          borderBottom={`1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.white}`}
        >
          <SubscriptionRedirectContainer
            shouldRedirect={isSaveWordPremiumFeature && !isPremiumSubscribed}
            pinchpoint={Pinchpoints.AlwaysTranslateSavedWord}
          >
            <SidebarSection hasBottomLine={false}>
              <AlwaysTranslate
                setAlwaysTranslate={setAlwaysTranslate}
                isAlwaysTranslateSavedWordsEnabled={isAlwaysTranslateSavedWordsEnabled}
                isDisabled={isSaveWordPremiumFeature && !isPremiumSubscribed}
              />
            </SidebarSection>
          </SubscriptionRedirectContainer>
        </Box>
        <Box display="flex" flex="1" zIndex={1}>
          <Box width="100%" flex="1" display="flex" flexDirection="column" ref={parentRef} height="100%">
            {(() => {
              // Render null here to avoid extended height from loader
              if (loading) {
                return <WordListPlaceholder />;
              }
              if (error) {
                const errorId = getErrorId(error);
                return (
                  <Box p={2}>
                    <ErrorMessage message={error.message} errorId={errorId} />
                  </Box>
                );
              }

              return (
                <FadeIn show flex="1" display="block">
                  <AutoSizer>
                    {({ width, height }) => (
                      <SubscriptionRedirectContainer
                        shouldRedirect={isSaveWordPremiumFeature && !isPremiumSubscribed}
                        pinchpoint={Pinchpoints.SavedWordsPremiumUpsell}
                      >
                        <List
                          ref={listRef}
                          deferredMeasurementCache={cache}
                          height={height || 300}
                          width={width || 380}
                          rowHeight={cache.rowHeight}
                          rowCount={wordListSections.length}
                          rowRenderer={wordListRenderer({
                            wordList: wordListSections,
                            refetch,
                            isPremiumSubscribed,
                            openSubscribePage,
                            languageKey,
                            isWordListCardDisabled: isSaveWordPremiumFeature && !isPremiumSubscribed,
                            isIKnowThisWordDisabled: isIKnowThisPremiumFeature && !isPremiumSubscribed,
                          })}
                        />
                      </SubscriptionRedirectContainer>
                    )}
                  </AutoSizer>
                </FadeIn>
              );
            })()}
          </Box>
        </Box>
      </Box>
      <Overlay overlayName={OverlayName.PracticeQuiz} zIndex={2} transition={{ duration: 0 }}>
        <Box position="relative" height="100%" bgcolor={backgroundBase}>
          <AnimatePresence>
            {(() => {
              if (quizInfo) {
                return (
                  <WordQuiz
                    key={quizInfo.translationIds.join(':')}
                    onQuizExit={onQuizExit}
                    quizStatus={currentQuiz.status}
                    {...quizInfo}
                  />
                );
              }

              if (postScreenInfo?.translationIds) {
                return (
                  <MotionBox
                    key="post-practice-overlay"
                    initial={{ opacity: 0, translateY: '100px' }}
                    animate={{ opacity: 1, translateY: '0px' }}
                    exit={{ opacity: 0, translateY: '100px' }}
                    transition={{ ease: 'easeOut', duration: 0.5 }}
                    height="100%"
                    width="100%"
                    position="absolute"
                    top="0"
                    left="0"
                    zIndex={2}
                  >
                    <PostPracticeOverlay
                      practicedTranslations={postScreenInfo.translationIds
                        .map(id => wordList.find(({ translationId }) => translationId === id))
                        .filter(filterNull)}
                      callout={callout ?? undefined}
                      amountPracticed={amountToPracticed}
                      amountOfSavedWords={wordList.length}
                      isPremiumSubscribed={isPremiumSubscribed}
                      onClose={() => {
                        setPostScreenInfo(null);
                        onNavigateSidebar({ overlay: null });
                      }}
                      onContinue={() => {
                        setPostScreenInfo(null);
                        openQuiz(wordList.filter(word => wordsToPractice.includes(word.translationId)));
                      }}
                      onOpenSubscribePage={openSubscribePage}
                      refetch={refetch}
                    />
                  </MotionBox>
                );
              }
              return null;
            })()}
          </AnimatePresence>
        </Box>
      </Overlay>
    </>
  );
};
