import { Language } from '~/constants/language';
import { useLocalContext } from '~/libs/i18n/context';

import dictionary from './dictionary';
import { HighlightToTranslateLanguageKey, GameBannerLanguageKey, AudioControlExampleLanguageKey } from './types';

type LanguageKey = HighlightToTranslateLanguageKey | GameBannerLanguageKey | AudioControlExampleLanguageKey;

export const useToucanTranslation = (passedLanguage?: Language) => {
  const { targetLanguage } = useLocalContext();
  const language = passedLanguage ?? targetLanguage;
  return { tt: (key: LanguageKey) => dictionary[language][key] };
};
