import React, { FC, useState } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme, ClickAwayListener, Fade } from '@mui/material';

import { Box, BoxProps } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { SupportedSourceLanguages } from '~/constants/language';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';

const { Typography, ToucanIcon, useColorScheme, Button } = ToucanComponents;

interface SourceLanguageSwitcherProps extends BoxProps {
  supportedLanguageIds: Array<SupportedSourceLanguages>;
  sourceLanguageId: SupportedSourceLanguages;
  onChangeSourceLanguage: (language: SupportedSourceLanguages) => void;
}

export const SourceLanguageSwitcher: FC<SourceLanguageSwitcherProps> = ({
  supportedLanguageIds,
  sourceLanguageId,
  onChangeSourceLanguage,
  ...boxProps
}) => {
  const theme = useTheme();
  const [isSourceLanguageListVisible, setIsSourceLanguageListVisible] = useState(false);
  const { getLanguageInfoFromLanguageId } = useLanguageInfo();
  const { isDarkMode } = useColorScheme();

  const showSourceLanguageList = () => setIsSourceLanguageListVisible(true);
  const hideSourceLanguageList = () => setIsSourceLanguageListVisible(false);

  const onUpdateLanguage = (languageId: SupportedSourceLanguages) => {
    onChangeSourceLanguage(languageId);
    hideSourceLanguageList();
  };

  const selectedSourceLanguageInfo = getLanguageInfoFromLanguageId(sourceLanguageId);

  return (
    <ClickAwayListener onClickAway={hideSourceLanguageList}>
      <Box
        position="relative"
        css={css`
          display: inline-block;
          vertical-align: middle;
        `}
        {...boxProps}
      >
        <Typography variant="sm">
          <Box
            component="span"
            css={css`
              svg {
                color: inherit;
              }
            `}
          >
            <Button type="text" data-testid="source-language-switch-button" onClick={showSourceLanguageList}>
              <Box display="inline-flex" flexDirection="row" alignItems="center" justifyContent="center">
                <Box component="span" pr={0.5} display="flex" alignItems="center">
                  <Image
                    src={selectedSourceLanguageInfo?.flagRound || ''}
                    alt={selectedSourceLanguageInfo?.language || ''}
                    css={css`
                      width: 16px;
                      height: 16px;
                    `}
                  />
                </Box>
                {selectedSourceLanguageInfo?.language}
                <Box
                  css={css`
                    display: flex;
                    align-items: center;
                    margin-left: ${theme.spacing(0.25)};
                    & svg {
                      width: 10px;
                    }
                  `}
                >
                  <ToucanIcon icon="chevron-down" />
                </Box>
              </Box>
            </Button>
          </Box>
        </Typography>

        <Fade in={isSourceLanguageListVisible} timeout={250}>
          <Box
            position="absolute"
            bgcolor={isDarkMode ? ToucanColors.gray[500] : ToucanColors.white}
            border={`1px solid ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white}`}
            borderRadius="4px"
            boxShadow="0px 4px 12px rgba(22, 22, 22, 0.04)"
          >
            <Box>
              <Box py={1}>
                {supportedLanguageIds.map(sourceLanguageOptionId => {
                  const sourceLanguageOptionInfo = getLanguageInfoFromLanguageId(sourceLanguageOptionId);

                  return (
                    <Box py={1} px={2} key={sourceLanguageOptionInfo?.key}>
                      <Typography variant="xs" isBold>
                        <Button
                          type="text"
                          onClick={() => onUpdateLanguage(sourceLanguageOptionId)}
                          data-testid={`source-language-option-${sourceLanguageOptionInfo?.languageId}`}
                        >
                          <Box display="flex" flexDirection="row">
                            <Box pr={1}>
                              <Image
                                src={sourceLanguageOptionInfo?.flagRound || ''}
                                alt={sourceLanguageOptionInfo?.language || ''}
                                css={css`
                                  width: 16px;
                                  height: 16px;
                                `}
                              />
                            </Box>
                            {sourceLanguageOptionInfo?.inLanguage}
                          </Box>
                        </Button>
                      </Typography>
                    </Box>
                  );
                })}
              </Box>
            </Box>
          </Box>
        </Fade>
      </Box>
    </ClickAwayListener>
  );
};
