/** @jsx jsx */
import React from 'react';

import { css, jsx } from '@emotion/react';
import { useTheme } from '@mui/material';

import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { Language } from '~/constants/language';
import { StorageKey } from '~/constants/storage';
import { filterNull } from '~/helpers/utilityHelpers';
import { useStorage } from '~/hooks/useStorage';
import { useTranslation } from '~/libs/i18n';

import { Feed } from './Feed';
import { Header } from './Header';
import { FeedNotifications } from './types';

export const Notifications = () => {
  const theme = useTheme();
  const { t } = useTranslation();
  const [doctorChangedLanguage] = useStorage<Language | null>(StorageKey.DoctorChangedLanguage, null);
  const [selectedLanguage] = useStorage<Language | null>(StorageKey.TargetLanguage, null);

  const prependedNotifications: FeedNotifications = [
    doctorChangedLanguage && selectedLanguage === doctorChangedLanguage
      ? {
          title: t('toucanTweakedYourSettings'),
          description: t('thereWasAHiccup', { language: doctorChangedLanguage ?? Language.Spanish }),
          layout: 'Medium',
          imageURL: browser.runtime.getURL('images/toucan-doctor.svg'),
        }
      : null,
  ].filter(filterNull);

  return (
    <React.Fragment>
      <SidebarSection
        css={css`
          padding-left: ${theme.spacing(2)};
          padding-right: ${theme.spacing(2)};
        `}
      >
        <Header />
      </SidebarSection>
      <Feed prependedNotifications={prependedNotifications} />
    </React.Fragment>
  );
};
