import React, { FC, ReactNode } from 'react';

import { css } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { Tab, Tabs } from '~/components/Sidebar/Tabs';
import Config from '~/configs/config';
import { useTranslation } from '~/libs/i18n';
import { ProgressionPeriod, ProgressionType, WordListSort as WordListSortType } from '~/schema/background';
import { SlideName } from '~/types/sidebar';

import { TabsOrdering } from '../shared';

import { WordListSort } from './WordListSort';

const { Link, ToucanIcon, useColorScheme } = ToucanComponents;

interface ProgressHeaderProps {
  title: ReactNode;
  period: ProgressionPeriod;
  onChangePeriod: (period: ProgressionPeriod) => void;
  progressionType: ProgressionType;
  onChangeSort: (sort: WordListSortType) => void;
  sort: WordListSortType;
  isPeriodShown: boolean;
}

export const ProgressHeader: FC<ProgressHeaderProps> = ({
  title,
  period,
  onChangePeriod,
  progressionType,
  sort,
  onChangeSort,
  isPeriodShown = true,
}) => {
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const tabIndex = TabsOrdering.indexOf(period);
  const { activeSlideName } = useSidebarContext();

  const isWordList = activeSlideName === SlideName.WordList;

  let wordListTitle = t('seenWords');
  switch (progressionType) {
    case ProgressionType.Learned:
      wordListTitle = t('learnedWords');
      break;
    default:
      break;
  }

  return (
    <>
      <Box py={2} px={4}>
        {!isWordList ? (
          <SlideHeader
            titleRightContent={
              <Box>
                <Link variant="xs" href={`${Config.siteURL}/dashboard`}>
                  {t('dashboard')}
                  <Box
                    component="span"
                    ml={1}
                    css={css`
                      svg {
                        width: 12px;
                        height: 12px;
                        fill: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
                      }
                    `}
                  >
                    <ToucanIcon icon="link-out" />
                  </Box>
                </Link>
              </Box>
            }
            isBackButtonVisible={false}
          >
            {title}
          </SlideHeader>
        ) : (
          <SlideHeader
            bottomContent={<WordListSort progressionType={progressionType} sort={sort} onChange={onChangeSort} />}
          >
            {wordListTitle}
          </SlideHeader>
        )}
      </Box>
      {isPeriodShown && (
        <Box
          position="sticky"
          top={-1}
          bgcolor={isDarkMode ? ToucanColors.gray[500] : ToucanColors.white}
          borderBottom={`1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]}`}
          zIndex="1"
        >
          <Tabs value={tabIndex}>
            <Tab
              label={t('yesterday')}
              value={TabsOrdering.indexOf(ProgressionPeriod.Yesterday)}
              onClick={() => {
                onChangePeriod(ProgressionPeriod.Yesterday);
              }}
            />
            <Tab
              label={t('today')}
              value={TabsOrdering.indexOf(ProgressionPeriod.Today)}
              onClick={() => {
                onChangePeriod(ProgressionPeriod.Today);
              }}
            />
          </Tabs>
        </Box>
      )}
    </>
  );
};
