/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import Config from '~/configs/config';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';

import { useAnonymousUserSettings } from '../AnonymousUserExtensionSettingsContext';

const { Typography, useColorScheme, Link, Button } = ToucanComponents;

export const ButtonSelectionFooter = () => {
  const { isNewSignUp, setIsNewSignUp } = useAnonymousUserSettings();
  const theme = useTheme();
  const { textLevel3 } = useColorScheme();
  const { t } = useTranslation();

  const onLogInClick = () => {
    setIsNewSignUp(false);
  };

  const onSignUpClick = () => {
    setIsNewSignUp(true);
  };

  return (
    <div
      css={css`
        margin-top: ${theme.spacing(2)};
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
      `}
    >
      <Typography
        variant="xs"
        color={textLevel3}
        css={css`
          text-align: center;
        `}
      >
        {isNewSignUp ? (
          <Trans
            i18nKey="bySigningUpIAgree"
            components={[
              <Link href={`${Config.siteURL}/terms`} target="_blank" />,
              <Link href={`${Config.siteURL}/privacy`} target="_blank" />,
            ]}
          />
        ) : (
          <Trans
            i18nKey="byLoggingInIAgree"
            components={[
              <Link href={`${Config.siteURL}/terms`} target="_blank" />,
              <Link href={`${Config.siteURL}/privacy`} target="_blank" />,
            ]}
          />
        )}
      </Typography>
      <div
        css={css`
          height: 1px;
          width: 100%;
          background-color: ${ToucanColors.gray[200]};
          margin: ${theme.spacing(3)} 0;
        `}
      />
      <Typography
        variant="xs"
        css={css`
          color: ${textLevel3};
          text-align: center;
        `}
      >
        {isNewSignUp ? (
          <div
            css={css`
              display: inline-flex;
            `}
          >
            {t('alreadyHaveAnAccount')}&nbsp;
            <span>
              <Button
                type="text"
                onClick={onLogInClick}
                css={css`
                  text-decoration: none !important;
                `}
              >
                {t('logIn')}
              </Button>
            </span>
            .
          </div>
        ) : (
          <div
            css={css`
              display: inline-flex;
            `}
          >
            {t('dontHaveAnAccountYet')}&nbsp;
            <span>
              <Button
                type="text"
                onClick={onSignUpClick}
                css={css`
                  text-decoration: none !important;
                `}
              >
                {t('signUp')}
              </Button>
            </span>
            .
          </div>
        )}
      </Typography>
    </div>
  );
};
