import React, { useCallback, useState } from 'react';

import Configs, { browserVendor } from '~/configs/config';
import { Events, EventSources } from '~/constants/events';
import { FeatureName } from '~/constants/features';
import { Pinchpoints } from '~/constants/pinchpoints';
import { useCurrentDomain } from '~/hooks/useCurrentDomain';
import { useMount } from '~/hooks/useMount';
import { useQuiz } from '~/hooks/useQuiz';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useFeatureEnabled } from '~/libs/features/client';
import ContentMessengers from '~/messengers/contentMessengers';
import {
  QuizType,
  useChangeGloballyEnabledStateMutation,
  useGetInitialSidebarValuesQuery,
  useVerifyWebsiteUserMutation,
} from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';

import { PanelName, SidebarContainerProps, SlideName } from '../../types/sidebar';
import { HighlightToTranslateSidebar } from '../HighlightToTranslate';
import { LoginSignup } from '../LoginSignup';
import { ModalType } from '../ModalContainer/types';

import { PermissionAlert } from './Alerts/PermissionAlert';
import { Panel } from './Panel';
import { useSidebarContext } from './SidebarContext';
import { SidebarError } from './SidebarError';
import { SidebarLayout } from './SidebarLayout';
import { SidebarPanelPlaceholder } from './SidebarPanelPlaceholder';
import { ExtraPracticeExplanationModal } from './content/ExtraPracticeExplanation';
import { Progress } from './content/Progress';
import { SavedWords } from './content/SavedWords';
import { VerifyInformation } from './content/VerifyInformation';
import { NotificationsPanel } from './panels/NotificationsPanel';
import { ReferralPanel } from './panels/ReferralPanel';
import { SettingsPanel } from './panels/SettingsPanel';
import { useSidebar } from './useSidebar';

/**
 * Handles the visibility of the Sidebar.
 * It sets up a structure of panels which can be switched via buttons in the footer
 * @param props
 * @param props.isInPopup A flag to indicate if the sidebar is being shown via the browser action or from hover container
 * @param props.onRequestOpen To set a new modal as active
 * @param props.onRequestClose To close a modal
 * @param props.selectedModalType Lets us know which modal is currently active
 */
export const SidebarContainer: React.FC<SidebarContainerProps> = React.memo(
  ({
    isInPopup,
    onRequestOpen,
    onRequestClose,
    sidebarWidth,
    isSettingsProgressCtaVisible,
    isLoadingExperimentsList,
  }) => {
    const { data, error, loading: sidebarValuesLoading, refetch } = useGetInitialSidebarValuesQuery();
    const [verifyUser, { loading: verifyingUser }] = useVerifyWebsiteUserMutation();
    const [changeGlobalEnabledState] = useChangeGloballyEnabledStateMutation();
    const { targetLanguage, isLoading, role, isUserRegistered, isLoading: isUserInfoLoading } = useUserInfo();
    const { closeSidebar, openSidebar } = useSidebar();
    const { activePanelName, onNavigateSidebar } = useSidebarContext();
    const [quizInfo, setQuizInfo] = useState<{ translationIds: Array<string>; quizType: QuizType } | null>(null);
    const { onReset } = useQuiz();
    const { currentDomain } = useCurrentDomain();
    const isReferralPanel = activePanelName === PanelName.Referral;
    const isReferralEnabled = useFeatureEnabled(FeatureName.ReferralsV2);
    const isMobileSafari = browserVendor === BrowserVendors.SafariiOS;

    const isGloballyEnabled = data?.settings?.isGloballyEnabled ?? true;
    const showWebsiteAuth = !!data?.me?.websiteUser?.email;
    const setIsGloballyEnabled = useCallback(async (isEnabled: boolean) => {
      await changeGlobalEnabledState({
        enabled: isEnabled,
      });
      await refetch();
    }, []);

    const verifyWebsiteUser = async () => {
      await verifyUser({});
      await refetch();
    };

    const closeWidget = async () => {
      closeSidebar();
      if (isInPopup) {
        // call directly when opened from action
        onRequestClose(ModalType.Sidebar);
      }
    };

    const setIsSidebarOpen = useCallback(
      (open: boolean) => {
        if (open) {
          openSidebar({});
        } else {
          closeSidebar();
        }
      },
      [openSidebar, closeSidebar],
    );

    const onQuizExit = () => {
      ContentMessengers.trackEvent({
        eventName: Events.LearningExperienceCompleted,
        properties: {
          exercise: Events.ReviewQuizCompleted,
          source: EventSources.Sidebar,
          url: currentDomain,
        },
        userOptions: { includePremiumSubscriptionStatus: true },
      });
      setQuizInfo(null);
      onReset();
    };

    // Referral CTA for desktop
    const isPersistentPremiumReferralShown =
      !isInPopup && !isLoading && !sidebarValuesLoading && !isReferralPanel && isGloballyEnabled && !!isReferralEnabled;

    const onPersistentReferralClick = () => {
      onNavigateSidebar({ panel: PanelName.Referral, slide: SlideName.MainReferral });

      ContentMessengers.trackEvent({
        eventName: Events.ReferralCtaClicked,
        properties: {
          pinchpointLocation: Pinchpoints.SidebarPersistentReferralCta,
        },
      });
    };

    // Referral CTA for mobile
    const isMobileReferralShown = isMobileSafari && !isReferralPanel && isGloballyEnabled && !!isReferralEnabled;

    const onMobileReferralClick = () => {
      onNavigateSidebar({ panel: PanelName.Referral, slide: SlideName.MainReferral });

      ContentMessengers.trackEvent({
        eventName: Events.ReferralCtaClicked,
        properties: {
          pinchpointLocation: Pinchpoints.MobileSidebarPersistentReferralCta,
        },
      });
    };

    useMount(() => {
      if (Configs.env !== 'production' || role === 'admin') {
        document.addEventListener('keydown', event => {
          if (event.ctrlKey && event.shiftKey && event.code === 'KeyE') {
            onNavigateSidebar({ slide: SlideName.ExperimentInformation });
          }
        });
      }
    });

    /**
     * This is the loading state for the sidebar.
     * By default it is the settings loading state.
     *
     * This also will handle errors if there are some in the initial sidebar query
     */
    if (isLoading || sidebarValuesLoading || error || isLoadingExperimentsList) {
      const hasPermissionError = error && 'extensions' in error && error.extensions?.code === 'PERMISSION_ERROR';
      const renderError = () =>
        hasPermissionError ? <PermissionAlert isInPopup={isInPopup} /> : <SidebarError error={error as Error} />;
      return (
        <SidebarLayout
          isInPopup={isInPopup}
          onRequestClose={closeWidget}
          isExtensionGloballyEnabled={isGloballyEnabled}
          setIsExtensionGloballyEnabled={async () => {}}
          isQuizOpen={false}
          onQuizExit={onQuizExit}
          hideFooter
          sidebarWidth={sidebarWidth}
          isPersistentPremiumReferralShown={isPersistentPremiumReferralShown}
          onPersistentReferralClick={onPersistentReferralClick}
          isMobileReferralShown={isMobileReferralShown}
          onMobileReferralClick={onMobileReferralClick}
          isUserRegistered={isUserRegistered}
        >
          {error ? renderError() : <SidebarPanelPlaceholder />}
        </SidebarLayout>
      );
    }

    /**
     * This is the state when the user is logged into the website but not in the extension.
     * We take over the sidebar and show them a verification page. Once the user confirms
     * they will be taken into the sidebar.
     */
    if (showWebsiteAuth) {
      return (
        <SidebarLayout
          isInPopup={isInPopup}
          onRequestClose={closeWidget}
          isExtensionGloballyEnabled={isGloballyEnabled}
          setIsExtensionGloballyEnabled={async () => {}}
          isQuizOpen={false}
          onQuizExit={onQuizExit}
          hideFooter
          sidebarWidth={sidebarWidth}
          isPersistentPremiumReferralShown={isPersistentPremiumReferralShown}
          onPersistentReferralClick={onPersistentReferralClick}
          isMobileReferralShown={isMobileReferralShown}
          onMobileReferralClick={onMobileReferralClick}
          isUserRegistered={isUserRegistered}
        >
          <VerifyInformation
            verifying={verifyingUser as boolean}
            onVerify={verifyWebsiteUser}
            email={data?.me?.websiteUser?.email ?? undefined}
            name={data?.me?.websiteUser?.name ?? undefined}
          />
        </SidebarLayout>
      );
    }

    /**
     * This is the state when the user is considered anonymous (no email). The extension will not work
     * until they create an account. Once an account has been created they can continue using the extension
     * pro bono.
     */
    if (!isUserRegistered) {
      return (
        <SidebarLayout
          isInPopup={isInPopup}
          onRequestClose={closeWidget}
          isExtensionGloballyEnabled={isGloballyEnabled}
          setIsExtensionGloballyEnabled={async () => {}}
          isQuizOpen={false}
          onQuizExit={onQuizExit}
          hideFooter
          sidebarWidth={sidebarWidth}
          isPersistentPremiumReferralShown={false}
          onPersistentReferralClick={onPersistentReferralClick}
          isMobileReferralShown={isMobileReferralShown}
          onMobileReferralClick={onMobileReferralClick}
          isUserRegistered={isUserRegistered}
        >
          <LoginSignup
            isAccountCreationWidgetShown
            onAccountCreationWidgetClose={closeWidget}
            isInPopup={isInPopup}
            refetch={async () => {}}
            shouldRequireRegistration
            isUserInfoLoading={isUserInfoLoading}
          />
        </SidebarLayout>
      );
    }

    return (
      <>
        <SidebarLayout
          isInPopup={isInPopup}
          onRequestClose={closeWidget}
          isExtensionGloballyEnabled={isGloballyEnabled}
          setIsExtensionGloballyEnabled={setIsGloballyEnabled}
          isQuizOpen={!!quizInfo}
          onQuizExit={onQuizExit}
          isProgressCtaVisible={isSettingsProgressCtaVisible}
          sidebarWidth={sidebarWidth}
          isPersistentPremiumReferralShown={isPersistentPremiumReferralShown}
          onPersistentReferralClick={onPersistentReferralClick}
          isMobileReferralShown={isMobileReferralShown}
          onMobileReferralClick={onMobileReferralClick}
          isUserRegistered={isUserRegistered}
        >
          <Panel name={PanelName.Settings}>
            <SettingsPanel
              isInPopup={isInPopup}
              onRequestOpen={onRequestOpen}
              onRequestClose={onRequestClose}
              setIsSidebarOpen={setIsSidebarOpen}
              targetLanguage={targetLanguage}
            />
          </Panel>
          <Panel name={PanelName.Notifications}>
            <NotificationsPanel />
          </Panel>
          <Panel name={PanelName.Progress}>
            <Progress
              quizInfo={quizInfo}
              onQuizEnter={(translationId, quizType) => {
                setQuizInfo({
                  translationIds: [translationId],
                  quizType,
                });
                ContentMessengers.trackEvent({
                  eventName: Events.LearningExperienceStarted,
                  properties: {
                    exercise: Events.ReviewQuizStarted,
                    source: EventSources.Sidebar,
                    url: currentDomain,
                  },
                  userOptions: { includePremiumSubscriptionStatus: true },
                });
              }}
              onQuizExit={onQuizExit}
              isInPopup={isInPopup}
            />
          </Panel>
          <Panel name={PanelName.SavedWords} display="flex" flexDirection="column">
            <SavedWords
              quizInfo={quizInfo}
              onQuizEnter={(translationIds, quizType) => {
                setQuizInfo({
                  translationIds,
                  quizType,
                });
              }}
              onQuizExit={onQuizExit}
            />
          </Panel>
          <Panel name={PanelName.HighlightToTranslate} display="flex" flexDirection="column">
            <HighlightToTranslateSidebar />
          </Panel>
          <Panel name={PanelName.Referral}>
            <ReferralPanel />
          </Panel>
          {!isInPopup && <ExtraPracticeExplanationModal />}
        </SidebarLayout>
      </>
    );
  },
);

window.__sidebar = SidebarContainer;
