/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { Image } from '~/components/shared/Image';
import { Language } from '~/constants/language';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';
import { SlideName } from '~/types/sidebar';

import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, useColorScheme, Button } = ToucanComponents;

export const ChangeLanguage: FC<{ targetLanguage: Language }> = ({ targetLanguage }) => {
  const { t } = useTranslation();
  const { languageInfo } = useLanguageInfo();
  const theme = useTheme();
  const { textLevel1 } = useColorScheme();
  const { onNavigateSidebar } = useSidebarContext();

  const targetLanguageInfo = Object.values(languageInfo).find(({ key }) => key === targetLanguage);

  if (!targetLanguageInfo) {
    return null;
  }

  const navigateToChangeLanguage = () => {
    onNavigateSidebar({ slide: SlideName.ChangeLanguage });
  };

  const { country, flagRound, language } = targetLanguageInfo;

  return (
    <SidebarItemSeparated
      leftContent={
        <React.Fragment>
          <Box display="flex">
            <Image
              src={flagRound}
              width={24}
              height={24}
              alt={t('flagOfCountry', { country })}
              css={css`
                margin-right: ${theme.spacing(1)};
              `}
            />
            <Typography variant="sm" isBold color={textLevel1}>
              {language}
            </Typography>
          </Box>
        </React.Fragment>
      }
      rightContent={
        <Typography variant="xs">
          <Button type="text" onClick={navigateToChangeLanguage}>
            {t('change')}
          </Button>
        </Typography>
      }
      rightContentAlignItems="center"
    />
  );
};
