import React, { useCallback, useEffect, useRef, useState, useMemo } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { debounce } from 'lodash';

import { Box } from '~/components/Box';
import { browserVendor } from '~/configs/config';
import { BrowserVendors } from '~/types/browsers';

const { Typography, useColorScheme } = ToucanComponents;

interface SliderProps<Value extends number> {
  max: Value;
  min: Value;
  value: Value;
  step: number;
  onChange: (value: Value) => Promise<void>;
  paddingTop: number;
  isDisabled?: boolean;
}

export const Slider = <Value extends number>({
  min,
  max,
  step,
  value,
  onChange,
  paddingTop,
  isDisabled,
  ...props
}: SliderProps<Value>) => {
  const theme = useTheme();
  const { backgroundAccent, isDarkMode, textLevel1, uiPrimary } = useColorScheme();
  const [mutableValue, setMutableValue] = useState(() => value);
  const ref = useRef<(value: Value) => Promise<void>>(onChange);
  ref.current = onChange;

  const onChangeHandler = useCallback(
    debounce((newValue: Value) => {
      ref.current(newValue);
    }, 300),
    [],
  );

  const onValueChange = (dir: number = 1) => () => {
    const newValue = Math.max(min, mutableValue + step * dir) as Value;
    onChangeHandler(newValue);
    setMutableValue(newValue);
  };

  useEffect(() => {
    onChangeHandler(mutableValue);
  }, [mutableValue, onChangeHandler]);

  const disabledThumb = isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400];

  const thumbCSS = `
    appearance: none;
    cursor: pointer;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    background: ${ToucanColors.white};
    box-shadow: inset 0 0 0 2px ${isDisabled ? disabledThumb : uiPrimary};
    transition: background 0.3s ease-in-out;
  `;

  const thumbFocusCSS = isDisabled
    ? ''
    : `
    box-shadow: inset 0 0 0 2px ${ToucanColors.teal[300]};
  `;

  const trackCSS = `
    appearance: none;
  `;

  const colors = useMemo(
    () => ({
      textColor: textLevel1,
      background: isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200],
    }),
    [isDarkMode],
  );

  const disabledColors = useMemo(
    () => ({
      textColor: ToucanColors.gray[400],
      background: backgroundAccent,
    }),
    [isDarkMode],
  );

  const colorsToUse = isDisabled ? disabledColors : colors;

  return (
    <Box
      aria-disabled={isDisabled}
      display="flex"
      flexDirection="row"
      alignItems="center"
      pt={paddingTop}
      css={css`
        h4 {
          user-select: none;
          line-height: 8px;
          color: ${colorsToUse.textColor};
        }
        span {
          cursor: pointer;
          border-radius: 50%;
          height: 16px;
          width: 16px;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          :first-of-type {
            margin-right: ${theme.spacing(2)};
          }
          :last-of-type {
            margin-left: ${theme.spacing(2)};
          }
        }
        input[type='range'] {
          width: 100%;
          height: 8px;
          border-radius: 4px;
          appearance: none;
          background-color: ${colorsToUse.background};
          background-image: linear-gradient(
            ${isDisabled ? ToucanColors.gray[100] : uiPrimary},
            ${isDisabled ? ToucanColors.gray[100] : uiPrimary}
          );
          background-size: ${(100 * mutableValue) / max}% 100%;
          background-repeat: no-repeat;
          :focus {
            outline: none;
          }
        }
        ${browserVendor === BrowserVendors.Firefox
          ? `
            input[type='range']::-moz-range-thumb {
              ${thumbCSS}
            }
            input[type='range']::-moz-range-track {
              ${trackCSS}
            }
            input[type='range']:focus::-moz-range-thumb {
              ${thumbFocusCSS}
            }
        `
          : `
            input[type='range']::-webkit-slider-thumb {
              ${thumbCSS}
            }
            input[type='range']:focus::-webkit-slider-thumb {
              ${thumbFocusCSS}
            }
            input[type='range']::-webkit-slider-runnable-track {
              ${trackCSS}
            }
        `}
      `}
      {...props}
    >
      <Box role="button" component="span" tabIndex={-1} onClick={isDisabled ? () => {} : onValueChange(-1)}>
        <Typography variant="md" isBold>
          -
        </Typography>
      </Box>
      <Box flex="1">
        <input
          type="range"
          value={mutableValue}
          max={max}
          min={min}
          step={step}
          onChange={e => {
            if (!isDisabled) {
              setMutableValue(parseInt(e.target.value, 10) as Value);
            }
          }}
          disabled={isDisabled}
        />
      </Box>
      <Box role="button" component="span" tabIndex={-1} onClick={isDisabled ? () => {} : onValueChange(1)}>
        <Typography variant="md" isBold>
          +
        </Typography>
      </Box>
    </Box>
  );
};
