import React, { FC, useEffect } from 'react';

import { AccountDeletionConfirm, AccountDeletionConfirmation } from '~/components/AccountDeletion';
import { Box } from '~/components/Box';
import { StorageKey } from '~/constants/storage';
import { useStorage } from '~/hooks/useStorage';
import { useMarkSettingsOpenedMutation } from '~/schema/background';
import { SettingsPanelProps, SlideName } from '~/types/sidebar';

import { Slide } from '../../shared/Slide';
import { SidebarError } from '../SidebarError';
import { SidebarPanelPlaceholder } from '../SidebarPanelPlaceholder';
import { Slides } from '../Slides';
import { AudioControls } from '../content/AudioControls';
import { ChangeLanguage } from '../content/ChangeLanguage';
import { ExperimentInformation } from '../content/ExperimentInformation';
import { Settings } from '../content/Settings';
import { BlockedSites } from '../content/Settings/BlockedSites';
import { useSettings } from '../content/Settings/useSettings';

export const SettingsPanel: FC<SettingsPanelProps> = ({ isInPopup, onRequestOpen, onRequestClose, targetLanguage }) => {
  const settings = useSettings({ isInPopup });
  const [markSettingsOpened] = useMarkSettingsOpenedMutation();
  const [hasOpenedSettings, , isLoading] = useStorage<boolean>(StorageKey.HasOpenedSettings, false);

  useEffect(() => {
    if (!isLoading && !hasOpenedSettings) {
      markSettingsOpened({});
    }
  }, [hasOpenedSettings, isLoading]);

  return (
    <Box data-testid="sidebar-content" aria-live="polite" aria-busy={settings.isLoaded ? 'false' : 'true'}>
      {(() => {
        if (!settings.isLoaded) {
          return <SidebarPanelPlaceholder />;
        }

        if (settings.error) {
          return <SidebarError error={settings.error as Error} />;
        }

        return (
          <Slides>
            <Slide slideName={SlideName.MainSettings}>
              <Settings
                isInPopup={isInPopup}
                onRequestOpen={onRequestOpen}
                onRequestClose={onRequestClose}
                targetLanguage={targetLanguage}
                {...settings}
              />
            </Slide>
            <Slide slideName={SlideName.ChangeLanguage}>
              <ChangeLanguage />
            </Slide>
            <Slide slideName={SlideName.BlockedSites}>
              <BlockedSites isInPopup={isInPopup} onDomainChange={settings.refetchAll} />
            </Slide>
            <Slide slideName={SlideName.AudioControls}>
              <AudioControls
                targetLanguage={targetLanguage}
                playbackRate={settings.playbackRate}
                voiceNumber={settings.voiceNumber}
                setUserAudioPreferences={settings.setUserAudioPreferences}
              />
            </Slide>
            <Slide slideName={SlideName.AccountDeletionConfirm}>
              <AccountDeletionConfirm />
            </Slide>
            <Slide slideName={SlideName.AccountDeletionConfirmation}>
              <AccountDeletionConfirmation
                logout={async () => {
                  await settings.logout({});
                }}
                refetchAll={settings.refetchAll}
              />
            </Slide>
            <Slide slideName={SlideName.ExperimentInformation}>
              <ExperimentInformation />
            </Slide>
          </Slides>
        );
      })()}
    </Box>
  );
};
