/** @jsx jsx */
import { useRef, useState, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useSelect, SelectOption, ClickAwayListener } from '@mui/base';
import { Box, FormHelperText } from '@mui/material';
import { styled } from '@mui/system';
import { isNil } from 'lodash';

import { useTranslation } from '~/libs/i18n';

const { ToucanIcon } = ToucanComponents;

const Root = styled('div')`
  font-family: 'Karla', sans-serif;
  position: relative;
  display: inline-block;
  width: 100%;
`;

interface ToggleProps {
  minWidth: number;
  listboxVisible: boolean;
  native: boolean;
  isDisabled: boolean;
  hasError: boolean;
}

const Toggle = styled('div', {
  shouldForwardProp: (prop: string) =>
    !['minWidth', 'listboxVisible', 'native', 'isDisabled', 'hasError'].includes(prop),
})<ToggleProps>(
  ({ minWidth, listboxVisible, native, isDisabled, hasError }) => `
  font-family: "Karla", sans-serif;
  font-size: 16px;
  box-sizing: border-box;
  position: relative;
  min-width: ${minWidth}px;
  background-color: white;
  border-width: 1px;
  border-style: solid;
  border-color: ${listboxVisible ? ToucanColors.blue[300] : ToucanColors.gray[200]};
  ${
    isDisabled
      ? `
    background-color: ${ToucanColors.gray[100]};
    border-color: rgba(0, 0, 0, 0.26);
  `
      : ''
  }
  ${
    hasError
      ? `
    border-color: ${ToucanColors.red[300]};
  `
      : ''
  }
  border-radius: 4px;
  border-bottom-left-radius: ${listboxVisible && !native ? 0 : '4px'};
  border-bottom-right-radius: ${listboxVisible && !native ? 0 : '4px'};
  height: 74px;
  padding: 37px 16px 14px 16px;
  cursor: ${listboxVisible || isDisabled ? 'default' : 'pointer'};
  &:hover {
    ${
      !isDisabled &&
      `
        border-color: ${ToucanColors.blue[300]};
      `
    }
  }
  `,
);

const Listbox = styled('ul')(
  () => `
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  margin-top: -1px;
  list-style: none;
  position: absolute;
  height: auto;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  width: 100%;
  background-color: ${ToucanColors.white};
  border: 1px solid ${ToucanColors.blue[300]};
  overflow: auto;
  z-index: 1;
  outline: 0px;

  &.hidden {
    opacity: 0;
    pointer-events: none;
  }

  & > li {
    font-family: 'Karla', sans-serif;
    min-height: auto;
    height: 54px;
    padding: 6px 16px;
    font-size: 16px;
    line-height: 140%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    cursor: pointer;

    &:hover {
      background-color: ${ToucanColors.blue[100]};
    }

    &[aria-selected='true'] {
      background-color: ${ToucanColors.blue[100]};
    }
  }
  `,
);

interface PulldownProps {
  label: string;
  emptyLabel?: string;
  emptyValue?: string;
  options: SelectOption<string | number>[];
  minWidth?: number;
  onChange?: (value: number | string) => void;
  value?: string | number;
  isEmptyOptionVisible?: boolean;
  hasError?: boolean;
  helperText?: string;
  isDisabled?: boolean;
  className?: string;
  native?: boolean;
}

export const Pulldown = ({
  label,
  emptyLabel,
  emptyValue = '',
  options,
  onChange,
  minWidth = 150,
  value,
  isEmptyOptionVisible,
  isDisabled = false,
  hasError = false,
  className,
  helperText,
  native,
}: PulldownProps) => {
  const { t } = useTranslation();
  const listboxRef = useRef<HTMLUListElement>(null);
  const [listboxVisible, setListboxVisible] = useState(false);
  const [selectedValue, setSelectedValue] = useState<string | number | undefined | null>(value);

  const { getButtonProps, getListboxProps, getOptionProps } = useSelect({
    listboxRef,
    options,
    value: selectedValue,
    onChange: newValue => {
      setSelectedValue(newValue);
      if (onChange && !isNil(newValue)) {
        onChange(newValue);
      }

      if (listboxVisible) {
        setListboxVisible(false);
      }
    },
  });

  const selectedLabel = options.find(option => String(option.value) === String(selectedValue))?.label;

  useEffect(() => {
    setSelectedValue(value);
  }, [value]);

  useEffect(() => {
    if (listboxVisible && !native) {
      listboxRef.current?.focus();
    }
  }, [listboxVisible, native]);

  return (
    <ClickAwayListener onClickAway={() => setListboxVisible(false)}>
      <Root>
        <Box onMouseDown={() => setListboxVisible(!isDisabled)}>
          <Toggle
            {...getButtonProps()}
            minWidth={minWidth}
            listboxVisible={listboxVisible}
            native={!!native}
            isDisabled={isDisabled}
            className={className}
            hasError={hasError}
          >
            <div
              css={css`
                position: absolute;
                top: 0;
                left: 0;
                transform: translate(16px, 26px) scale(1);
                font-size: 16px;
                font-family: 'Karla', sans-serif;
                font-weight: 400;
                line-height: 1.1876em;
                color: ${listboxVisible ? ToucanColors.blue[300] : ToucanColors.gray[500]};
                ${isDisabled &&
                `
                  color: ${ToucanColors.gray[300]};
                `}
                ${hasError
                  ? `
                    color: ${ToucanColors.red[300]}
                  `
                  : ''}
                transition: color 200ms cubic-bezier(0, 0, 0.2, 1) 0ms,
                  transform 200ms cubic-bezier(0, 0, 0.2, 1) 0ms,
                  font-weight 200ms cubic-bezier(0, 0, 0.2, 1) 0ms,
                  font-size 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
                ${(listboxVisible || selectedValue) &&
                `
                  transform: translate(16px, 14px) scale(1);
                  font-size: 14px;
                  font-weight: 700;
                `}
              `}
            >
              {label}
            </div>
            {selectedLabel}
            <div
              css={css`
                position: absolute;
                right: 16px;
                top: 25px;
                transform: rotate(${listboxVisible ? '180' : '0'}deg);
                ${(listboxVisible || selectedValue) &&
                `
                  top: auto;
                  bottom: 18px;
                `}
              `}
            >
              <ToucanIcon
                icon="chevron-down"
                css={css`
                  color: ${listboxVisible ? ToucanColors.blue[300] : ToucanColors.gray[300]};
                  display: block;
                  width: 16px;
                  height: 16px;
                `}
              />
            </div>
            {native && !isDisabled && (
              <select
                css={css`
                  opacity: 0;
                  cursor: pointer;
                  position: absolute;
                  top: 0;
                  left: 0;
                  width: 100%;
                  height: 100%;
                `}
                onChange={event => {
                  setSelectedValue(event.target.value);
                  if (onChange) {
                    onChange(event.target.value);
                  }
                }}
              >
                {isEmptyOptionVisible && <option value={emptyValue}>{emptyLabel || t('none')}</option>}
                {options.map(option => (
                  <option key={option.value} value={option.value}>
                    {option.label}
                  </option>
                ))}
              </select>
            )}
          </Toggle>
        </Box>
        {!native && (
          <Listbox {...getListboxProps()} className={listboxVisible ? '' : 'hidden'}>
            {isEmptyOptionVisible && (
              <li
                {...getOptionProps({
                  label: emptyLabel,
                  value: emptyValue,
                })}
                css={css`
                  font-style: italic;
                `}
              >
                {emptyLabel}
              </li>
            )}
            {options.map(option => (
              <li key={option.value} {...getOptionProps(option)} data-value={option.value}>
                {option.label}
              </li>
            ))}
          </Listbox>
        )}
        {helperText && (
          <FormHelperText disabled={isDisabled} error={hasError}>
            {helperText}
          </FormHelperText>
        )}
      </Root>
    </ClickAwayListener>
  );
};
