/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import { useTheme, Box } from '@mui/material';

import { useTranslation } from '~/libs/i18n';

import { useAnonymousUserSettings } from '../AnonymousUserExtensionSettingsContext';

const { Button } = ToucanComponents;

export const FormFooter = () => {
  const {
    isDarkModeEnabled,
    isNewSignUp,
    setIsNewSignUp,
    setIsEmailSignupSelected,
    setIsResetSelected,
  } = useAnonymousUserSettings();
  const theme = useTheme();
  const { t } = useTranslation();
  const onSignUpBackClick = () => {
    setIsEmailSignupSelected(false);
    setIsNewSignUp(true);
  };

  const onLogInBackClick = () => {
    setIsEmailSignupSelected(false);
    setIsNewSignUp(false);
  };

  const onForgotPasswordClick = () => {
    setIsResetSelected(true);
  };

  return (
    <Box display="flex" justifyContent="center" alignItems="center">
      <Button
        onClick={isNewSignUp ? onSignUpBackClick : onLogInBackClick}
        type="text"
        css={css`
          text-decoration: none !important;
          font-size: 14px;
          display: inline-flex;
          align-items: center;
        `}
      >
        <ChevronLeftIcon
          css={css`
            width: 25px;
            margin-right: ${theme.spacing(-0.5)};
          `}
        />
        {t('back')}
      </Button>
      {!isNewSignUp && (
        <div
          css={css`
            display: inline-flex;
            align-items: center;
          `}
        >
          <div
            css={css`
              color: ${ToucanColors.gray[isDarkModeEnabled ? 300 : 400]};
              font-size: 12px;
              margin-left: ${theme.spacing(2)};
              margin-right: ${theme.spacing(2)};
            `}
          >
            &#8226;
          </div>
          <Button
            type="text"
            onClick={onForgotPasswordClick}
            css={css`
              text-decoration: none !important;
              font-size: 14px;
            `}
          >
            {t('forgotYourPassword')}
          </Button>
        </div>
      )}
    </Box>
  );
};
