/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { SlideName } from '~/types/sidebar';

import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, useColorScheme, Button } = ToucanComponents;

export const AudioControls: FC = () => {
  const { t } = useTranslation();
  const { textLevel1, textLink } = useColorScheme();
  const { onNavigateSidebar } = useSidebarContext();

  const navigateToAudioControls = () => {
    onNavigateSidebar({ slide: SlideName.AudioControls });
    const eventName = 'Audio Controls opened';
    const properties = {
      source: 'sidePanel',
    };
    contentMessengers.trackEvent({ eventName, properties });
  };

  return (
    <SidebarItemSeparated
      data-testid="audio-controls-sidebar-item"
      leftContent={
        <React.Fragment>
          <Typography
            variant="sm"
            isBold
            css={css`
              color: ${textLevel1};
            `}
          >
            {t('audioControls')}
          </Typography>
        </React.Fragment>
      }
      rightContent={
        <Typography variant="xs">
          <Button
            data-testid="audio-controls-button"
            type="text"
            onClick={navigateToAudioControls}
            css={css`
              text-decoration: none;
              color: ${textLink};
            `}
          >
            {t('change')}
          </Button>
        </Typography>
      }
    />
  );
};
