import React, { createContext, FC, useContext } from 'react';

interface AnonymousUserContextProps {
  isDarkModeEnabled: boolean;
  closeAccountCreationWidget: () => void;
  isInPopup: boolean;
  currentSiteDomain: null | string;
  isNewSignUp: boolean;
  setIsNewSignUp: (state: boolean) => void;
  isEmailSignupSelected: boolean;
  setIsEmailSignupSelected: (state: boolean) => void;
  isResetSelected: boolean;
  setIsResetSelected: (state: boolean) => void;
}

const AnonymousUserExtensionSettingsContext = createContext<AnonymousUserContextProps>({
  isDarkModeEnabled: false,
  closeAccountCreationWidget: () => {},
  isInPopup: false,
  currentSiteDomain: null,
  isNewSignUp: true,
  setIsNewSignUp: () => {},
  isEmailSignupSelected: false,
  setIsEmailSignupSelected: () => {},
  isResetSelected: false,
  setIsResetSelected: () => {},
});

export const useAnonymousUserSettings = () => useContext(AnonymousUserExtensionSettingsContext);

export const AnonymousUserExtensionSettingsProvider: FC<AnonymousUserContextProps> = ({ children, ...value }) => (
  <AnonymousUserExtensionSettingsContext.Provider value={value}>
    {children}
  </AnonymousUserExtensionSettingsContext.Provider>
);
