/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box, BoxProps } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

const { ToucanIcon, Typography, Button, useColorScheme } = ToucanComponents;

interface PremiumUpsellProps extends BoxProps {
  children: string;
  onClickTextButton: () => void;
  linkText: string;
  imageUrl: string;
  imageAlt: string;
}

export const PremiumUpsell: React.FC<PremiumUpsellProps> = ({
  children,
  onClickTextButton,
  linkText,
  imageUrl,
  imageAlt,
  ...boxProps
}) => {
  const { t } = useTranslation();
  const { uiPromotion, textPromotion } = useColorScheme();
  const theme = useTheme();

  return (
    <Box display="flex" bgcolor={uiPromotion} borderRadius="8px" p={2} mx={-2} mt={2} {...boxProps}>
      {imageUrl && (
        <Image
          src={imageUrl}
          width={32}
          height={32}
          alt={imageAlt || t('premiumUpsellIcon')}
          mr={2}
          css={css`
            align-self: center;
          `}
        />
      )}
      <Box>
        <Typography
          variant="xs"
          color={textPromotion}
          isBold
          css={css`
            margin-bottom: ${theme.spacing(0.5)};
          `}
        >
          {children}
        </Typography>

        <Typography variant="xs">
          <Button
            type="text"
            onClick={onClickTextButton}
            css={css`
              display: flex;
              align-items: center;
              gap: ${theme.spacing(0.5)};
            `}
          >
            {linkText}
            <ToucanIcon
              icon="chevron-right"
              css={css`
                width: 10px;
                color: inherit;
              `}
            />
          </Button>
        </Typography>
      </Box>
    </Box>
  );
};
