import React from 'react';

import Config from '~/configs/config';
import { useHostPermissions } from '~/hooks/useHostPermissions';
import { useTranslation } from '~/libs/i18n';
import { useRequestReloadMutation } from '~/schema/background';

import { Alert } from './Alert';

interface PermissionAlertProps {
  isInPopup: boolean;
}

export const PermissionAlert = ({ isInPopup }: PermissionAlertProps) => {
  const { t } = useTranslation();
  const { requestPermissions } = useHostPermissions({
    hosts: [`${Config.siteURL}/`],
  });
  const [requestReload] = useRequestReloadMutation();

  const handleClick = async () => {
    const hasReceivedPermission = await requestPermissions();
    if (hasReceivedPermission) {
      if (isInPopup) {
        // I'd like to call injectIntoPage here to show inline translations.
        // However, any code I add here seems to execute only if the
        // developer tools for the popup are open.
      } else {
        requestReload({});
      }
    }
  };
  return (
    <Alert
      title={t('toucanNeedsPermission')}
      description={t('giveToucanPermissionBrowser')}
      cta={t('turnOnToucan')}
      onClick={handleClick}
    />
  );
};
