const Badge: FC<Omit<DensitySelectorProps, 'density' | 'onClick'>> = ({ active, locked }) => {
  let status = active ? 'active' : 'idle';
  if (locked) {
    status = 'locked';
  }

  return (
    <Box
      data-testid={`badge-${status}`}
      position="absolute"
      top="6px"
      right="6px"
      width="16px"
      height="16px"
      borderRadius="8px"
      bgcolor={locked ? ToucanColors.yellow[300] : ToucanColors.teal[500]}
      display="flex"
      justifyContent="center"
      alignItems="center"
      css={css`
        transform: scale(${locked || active ? 1 : 0});
        transition: all 0.2s ease-in-out;
      `}
    >
      <ToucanIcon
        icon={locked ? 'lock' : 'check'}
        css={css`
          width: 8px;
          height: 8px;
          color: ${locked ? ToucanColors.yellow[500] : ToucanColors.white};
        `}
      />
    </Box>
  );
};

export const DensityOptionOld: FC<DensitySelectorProps> = ({ density, active, locked, onClick }) => {
  const { t } = useTranslation();
  const { scheme, isDarkMode } = useColorScheme();
  const DensityImage = DensityImages[density];
  const currentScheme = active ? Schemes.active : Schemes[scheme];

  const DensityName: Record<DensityValues, string> = {
    [DensityValues.Less]: t('less'),
    [DensityValues.More]: t('more'),
    [DensityValues.Many]: t('aLot'),
    [DensityValues.Premium]: t('premium'),
  };

  return (
    <Button
      type="secondary"
      onClick={() => onClick(density)}
      key={density}
      data-testid={density}
      css={css`
        position: relative;
        width: 71px;
        padding: 0;
        transition: all 0.2s ease-in-out;
        box-shadow: ${active
          ? 'none'
          : `inset 0 0 0 1px ${isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200]}`};
        background: ${currentScheme.background};
        :hover {
          background: ${currentScheme.backgroundHover};
        }
        :active {
          box-shadow: ${active
            ? 'none'
            : `inset 0 0 0 3px ${isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200]}`};
        }
      `}
    >
      <Badge locked={locked} active={active} />
      <Box
        position="absolute"
        top="6px"
        right="6px"
        width="16px"
        height="16px"
        borderRadius="8px"
        bgcolor={ToucanColors.teal[500]}
        display="flex"
        justifyContent="center"
        alignItems="center"
        css={css`
          transform: scale(${active ? 1 : 0});
          transition: all 0.2s ease-in-out;
        `}
      >
        <ToucanIcon
          icon="check"
          css={css`
            width: 8px;
            height: 8px;
            color: ${ToucanColors.white};
          `}
        />
      </Box>

      <Box
        component="span"
        width="71px"
        display="flex"
        flexDirection="column"
        alignItems="center"
        paddingTop="14px"
        paddingBottom="8px"
      >
        <svg
          width="45"
          height="39"
          viewBox="0 0 45 39"
          fill="none"
          css={css`
            rect {
              transition: fill 0.2s ease-in-out;
            }
          `}
        >
          <DensityImage {...currentScheme} />
        </svg>
        <Typography
          variant="2xs"
          isBold
          css={css`
            transition: color 0.2s ease-in-out;
            padding-top: 7px;
            text-transform: none;
            color: ${isDarkMode || active ? ToucanColors.white : ToucanColors.gray[600]};
          `}
        >
          {DensityName[density]}
        </Typography>
      </Box>
    </Button>
  );
};
