/** @jsx jsx */
import React from 'react';

import { css, jsx } from '@emotion/react';
import { useTheme } from '@mui/material';
import { AnimatePresence } from 'framer-motion';

import { useLanguageInfo } from '~/hooks/useLanguageInfo';

import { Button } from './Button';

interface LanguageButtonsGridProps<LangInfo> {
  dataSource: string[];
  onClickButton: (languageId: LangInfo) => void;
  selectedLanguageId: LangInfo;
  className?: string;
  testIdBase?: string;
  isReducedSize?: boolean;
}

export const LanguageButtonsGrid = <LangInfo extends string>({
  dataSource,
  onClickButton,
  selectedLanguageId,
  className,
  testIdBase,
  isReducedSize,
}: LanguageButtonsGridProps<LangInfo>) => {
  const theme = useTheme();
  const { languageInfo } = useLanguageInfo();

  return (
    <AnimatePresence initial={false}>
      <div
        className={className}
        css={css`
          margin-top: ${theme.spacing(2)};
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          grid-template-rows: auto;
          grid-column-gap: 8px;
          grid-row-gap: 8px;
        `}
      >
        {dataSource.map(languageId => {
          const currentLanguageInfo = Object.values(languageInfo).find(
            languageData => languageData.languageId === languageId,
          );

          if (!currentLanguageInfo) {
            return null;
          }

          return (
            <Button
              key={languageId}
              language={currentLanguageInfo.language}
              data-testid={`${testIdBase}-${languageId}`}
              flagRound={currentLanguageInfo.flagRound}
              country={currentLanguageInfo.country}
              onClick={() => onClickButton(currentLanguageInfo.languageId as LangInfo)}
              isSelected={currentLanguageInfo.languageId === selectedLanguageId}
              isReducedSize={isReducedSize}
            />
          );
        })}
      </div>
    </AnimatePresence>
  );
};
