import React, { useEffect, useState } from 'react';

import { Alert } from '~/components/Sidebar/Alerts/Alert';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useSendReferralEmailsMutation } from '~/schema/background';

import { EmailReferralError } from './EmailReferralError';
import { EmailReferralForm } from './EmailReferralForm';

export const EmailReferralContainer = () => {
  const { t } = useTranslation();
  const [numEmailsSent, setNumEmailsSent] = useState(0);
  const [sendReferralEmails, useSendReferralEmailsMutationResponse] = useSendReferralEmailsMutation();

  const { error: sendReferralEmailsError } = useSendReferralEmailsMutationResponse;

  const sendReferralEmailsErrorId =
    (sendReferralEmailsError &&
      'extensions' in sendReferralEmailsError &&
      sendReferralEmailsError.extensions?.errorId) ??
    sendReferralEmailsError?.message;

  useEffect(() => {
    ContentMessengers.trackEvent({
      eventName: 'Referral panel email referral slide viewed',
    });
  }, []);

  const sendEmailsToReferredUsers = async (emails: string[]) => {
    ContentMessengers.trackEvent({
      eventName: 'User Shared a Referral',
      properties: {
        sharedTo: 'email',
        numEmails: emails.length,
        source: 'referral-panel',
      },
    });
    const response = await sendReferralEmails({ emails });

    if (response.data.sendReferralEmails?.sharedTo) {
      setNumEmailsSent(emails.length);
    }
  };

  return (
    <>
      {numEmailsSent > 0 && (
        <Alert
          title={t('digitalCarrierPigeonOnWay', { count: numEmailsSent })}
          description={t('successfullySentInvite', { count: numEmailsSent })}
        />
      )}
      <SidebarSection>
        <SlideHeader subHeader={t('toucanWillSend')}>{t('emailYourFriends')}</SlideHeader>
      </SidebarSection>
      <SidebarSection hasBottomLine={false}>
        <EmailReferralForm onSubmitForm={sendEmailsToReferredUsers} />
        {sendReferralEmailsError && (
          <EmailReferralError error={sendReferralEmailsError} errorId={sendReferralEmailsErrorId} mt={2} />
        )}
      </SidebarSection>
    </>
  );
};
