import React, { FC } from 'react';

import { GraphQLFormattedError } from 'graphql';

import { Box } from '~/components/Box';
import { SidebarError } from '~/components/Sidebar/SidebarError';
import { Description } from '~/components/Sidebar/content/Notifications/Feed/Description';
import { Image } from '~/components/Sidebar/content/Notifications/Feed/Image';
import { Title } from '~/components/Sidebar/content/Notifications/Feed/Title';
import { getErrorId } from '~/helpers/errors';
import { useTranslation } from '~/libs/i18n';
import { OnboardingItem } from '~/schema/background';
import { FeedNotificationLayout } from '~/schema/server';

import { ListItem } from './ListItems';
import { ProgressBar } from './ProgressBar';

interface ChecklistProps {
  amountCompleted: number;
  checklistItems: Array<Omit<OnboardingItem, 'hasCallout' | 'itemType'>>;
  refetch: () => void;
  error?: GraphQLFormattedError | Error;
  canRequestPermissions: boolean;
  toucanRewardOfferedPerReferral?: string;
  referralCTAText: string;
}

export const Checklist: FC<ChecklistProps> = ({
  amountCompleted,
  checklistItems,
  refetch,
  error,
  canRequestPermissions,
  toucanRewardOfferedPerReferral,
  referralCTAText,
}) => {
  const { t } = useTranslation();

  return (
    <>
      <Box display="flex" flexDirection="column" alignItems="center" mt={2}>
        <Image
          imageURL={`https://public.toucanapi.com/img-checklist-${amountCompleted === 100 ? 'done' : 'inprogress'}.svg`}
          layout={FeedNotificationLayout.Large}
        />
        <Box display="flex" flexDirection="column" alignItems="flex-start" width={314}>
          <Title>{t('getToKnowToucan')}</Title>
          <Description>{t('exploreFeaturesNewExtension')}</Description>
        </Box>
      </Box>
      {error ? (
        () => {
          const errorId = getErrorId(error);
          return (
            <Box p={2}>
              <SidebarError error={error as Error} errorId={errorId} />
            </Box>
          );
        }
      ) : (
        <>
          <ProgressBar
            amountCompleted={amountCompleted}
            toucanRewardOfferedPerReferral={toucanRewardOfferedPerReferral}
            referralCTAText={referralCTAText}
          />
          <Box display="flex" flexDirection="column" px={4} width="100%" data-testid="checklist-items">
            {checklistItems.map(listItem => {
              const { onClickType, isCompleted, item, filePath, title } = listItem;
              return (
                <Box key={item} display="flex" mb={2}>
                  <ListItem
                    onClickType={onClickType}
                    isCompleted={isCompleted}
                    item={item}
                    filePath={filePath}
                    title={title}
                    refetch={refetch}
                    canRequestPermissions={canRequestPermissions}
                  />
                </Box>
              );
            })}
          </Box>
        </>
      )}
    </>
  );
};
