/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

const { Typography, useColorScheme } = ToucanComponents;

interface MakingProgressTipProps {
  stepNumber: number;
  tipName: string;
  tipDescription: string;
  textColor: string;
}

const MakingProgressTip: FC<MakingProgressTipProps> = ({ stepNumber, tipName, tipDescription, textColor }) => (
  <Box mb={2} display="flex">
    <Box
      minWidth="28px"
      width="28px"
      height="28px"
      borderRadius="14px"
      mr={2}
      bgcolor={ToucanColors.teal[400]}
      color={ToucanColors.white}
      display="flex"
      justifyContent="center"
      alignItems="center"
    >
      {stepNumber}
    </Box>
    <Box>
      <Typography variant="xs" isBold color={textColor}>
        {tipName}
      </Typography>
      <Typography variant="xs" color={textColor}>
        {tipDescription}
      </Typography>
    </Box>
  </Box>
);

export const MakingProgress: FC = () => {
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const textColor = isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500];

  return (
    <Box flex="1" minHeight="100%" bgcolor={isDarkMode ? ToucanColors.gray[500] : ToucanColors.white}>
      <SidebarSection>
        <SlideHeader>{t('makingProgress')}</SlideHeader>
      </SidebarSection>
      <SidebarSection hasBottomLine={false}>
        <Box py={4} display="flex" flexDirection="column" alignItems="center">
          <Image width="120px" height="120px" src="images/progress/trophy.svg" alt="trophy" />
        </Box>
        <Typography
          variant="xs"
          color={textColor}
          css={css`
            margin-bottom: ${theme.spacing(1)};
          `}
        >
          {t('seeHowYouStackUp')}
        </Typography>
        <Typography
          variant="xs"
          color={textColor}
          css={css`
            margin-bottom: ${theme.spacing(3)};
          `}
        >
          {t('heresHowYouSpeedUp')}
        </Typography>
        <MakingProgressTip
          stepNumber={1}
          tipName={t('justKeepBrowsing')}
          tipDescription={t('justKeepBrowsingDescription')}
          textColor={textColor}
        />
        <MakingProgressTip
          stepNumber={2}
          tipName={t('checkYourPronunciation')}
          tipDescription={t('checkYourPronunciationDescription')}
          textColor={textColor}
        />
      </SidebarSection>
    </Box>
  );
};
