/** @jsx jsx */
import React, { FC, useEffect, useRef, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { motion, AnimatePresence } from 'framer-motion';

import { Box } from '~/components/Box';
import { ModalBaseProps } from '~/components/ModalContainer/types';
import { SettingsAlert } from '~/components/Sidebar/Alerts/SettingsAlert';
import { InfoMessage } from '~/components/Sidebar/InfoMessage';
import { Overlay } from '~/components/Sidebar/Overlay';
import { SectionItemSeparator } from '~/components/Sidebar/SectionItemSeparator';
import { Setting } from '~/components/Sidebar/Setting';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { Snooze } from '~/components/Sidebar/content/Snooze';
import Config from '~/configs/config';
import { FeatureName } from '~/constants/features';
import { Language } from '~/constants/language';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';
import { OverlayName, SettingsType, SettingName, SlideName } from '~/types/sidebar';

import { AllowOnSite } from './AllowOnSite';
import { AudioControls } from './AudioControls';
import { ChangeLanguage } from './ChangeLanguage';
import { ContextlessLearningMoment } from './ContextlessLearningMoment';
import { DarkMode } from './DarkMode';
import { DataConsent } from './DataConsent';
import { DifficultySelector } from './DifficultySelector';
import { DisplayAlphabet } from './DisplayAlphabet';
import { Header } from './Header';
import { HighlightToTranslateSettings } from './HighlightToTranslateSettings';
import { KeyboardShortcuts } from './KeyboardShortcuts';
import { PhoneticSpelling } from './PhoneticSpelling';

const { Typography, useColorScheme, Link, ToucanIcon, Button } = ToucanComponents;

interface SettingsProps extends Omit<ModalBaseProps, 'selectedModalType'>, SettingsType {
  targetLanguage: Language;
  isInPopup: boolean;
}

interface InfoMessageState {
  isVisible: boolean;
  isError?: boolean;
  text?: string;
  isLoaderVisible?: boolean;
}

export const Settings: FC<SettingsProps> = ({
  isInPopup,
  domain,
  isLoaded,
  isChromePermissionGranted,
  densityLevel,
  difficultyTier,
  onDifficultyTierChange,
  isDomainBanned,
  isExtensionGloballyEnabled,
  isHighlightToTranslateEnabled,
  disabledDuration,
  isUserRegistered,
  unbannable,
  hasDomainPermissions,
  isUserAccountPremium,
  selectedComingSoonLanguageId,
  canRequestPermissions,
  setDifficultyLevel,
  setDisabledDuration,
  onAddDomainToBanlist,
  onRemoveDomainFromBanlist,
  onRequestGlobalPermissions,
  onRequestDomainPermissions,
  onToggleHighlightToTranslateEnabled,
  targetLanguage,
  isPronunciationEnabled,
  onTogglePronunciation,
  logout,
  refetchAll,
  hasDataConsent,
  onChangeDataConsent,
  isDifficultyEnabled,
}) => {
  const container = useRef<HTMLDivElement | null>(null);
  const theme = useTheme();
  const { t } = useTranslation();
  const { isDarkMode, textLevel2 } = useColorScheme();
  const { onNavigateSidebar } = useSidebarContext();
  const isAudioControlsEnabled = useFeatureEnabled(FeatureName.AudioControls);
  const [infoMessage, setInfoMessage] = useState<InfoMessageState>({
    isVisible: false,
    isError: false,
    text: '',
    isLoaderVisible: false,
  });

  useEffect(() => {
    if (isExtensionGloballyEnabled) {
      onNavigateSidebar({ overlay: null });
      return;
    }

    onNavigateSidebar({ overlay: OverlayName.Snooze });
  }, [isExtensionGloballyEnabled]);

  return (
    <React.Fragment>
      {isExtensionGloballyEnabled && (
        <React.Fragment>
          <SettingsAlert
            isInPopup={isInPopup}
            hasDomainPermissions={hasDomainPermissions}
            hasGlobalPermissions={isChromePermissionGranted}
            isLoaded={isLoaded}
            isUserRegistered={isUserRegistered}
            canRequestPermissions={canRequestPermissions}
            onRequestDomainPermissions={onRequestDomainPermissions}
            onRequestGlobalPermissions={onRequestGlobalPermissions}
            selectedComingSoonLanguageId={selectedComingSoonLanguageId}
          />
        </React.Fragment>
      )}

      <SidebarSection>
        <Header isUserRegistered={isUserRegistered} />
      </SidebarSection>
      <div
        css={css`
          position: relative;
        `}
      >
        <AnimatePresence exitBeforeEnter initial={false}>
          {isExtensionGloballyEnabled === null ||
            (isExtensionGloballyEnabled && (
              <motion.div ref={container} key="settings" initial={false} animate={{ opacity: 1 }} exit={{ opacity: 0 }}>
                <Setting name={SettingName.AllowOnSite}>
                  <SidebarSection>
                    <AllowOnSite
                      onAddDomainToBanlist={onAddDomainToBanlist}
                      onRemoveDomainFromBanlist={onRemoveDomainFromBanlist}
                      onRequestDomainPermissions={onRequestDomainPermissions}
                      domain={domain}
                      isBanned={isDomainBanned ?? false}
                      unbannable={unbannable}
                      hasDomainPermissions={hasDomainPermissions}
                    />
                  </SidebarSection>
                </Setting>
                <SidebarSection>
                  <Setting name={SettingName.ChangeLanguage}>
                    <ChangeLanguage targetLanguage={targetLanguage} />
                  </Setting>
                  <Setting name={SettingName.DisplayAlphabet}>
                    <DisplayAlphabet setInfoMessage={setInfoMessage} />
                  </Setting>
                  <Setting name={SettingName.PhoneticSpelling}>
                    <PhoneticSpelling isEnabled={isPronunciationEnabled} onToggleEnabledState={onTogglePronunciation} />
                  </Setting>
                </SidebarSection>
                {isDifficultyEnabled && (
                  <SidebarSection>
                    <Setting name={SettingName.TranslationDifficultySelector}>
                      <DifficultySelector
                        densityLevel={densityLevel}
                        onChangeDensityLevel={setDifficultyLevel}
                        difficultyTier={difficultyTier}
                        onDifficultyTierChange={onDifficultyTierChange}
                        isUserAccountPremium={isUserAccountPremium}
                        disableDifficulty={!isDifficultyEnabled}
                      />
                    </Setting>
                  </SidebarSection>
                )}
                <SidebarSection>
                  <Setting name={SettingName.HighlightToTranslateSettings}>
                    <HighlightToTranslateSettings
                      isEnabled={isHighlightToTranslateEnabled}
                      onToggleEnabledState={onToggleHighlightToTranslateEnabled}
                    />
                  </Setting>
                  <Setting name={SettingName.ContextlessLearningMoment}>
                    <ContextlessLearningMoment isInPopup={isInPopup} />
                    <SectionItemSeparator />
                  </Setting>
                </SidebarSection>
                <SidebarSection>
                  {isAudioControlsEnabled && (
                    <Setting name={SettingName.AudioControls}>
                      <AudioControls />
                      <SectionItemSeparator />
                    </Setting>
                  )}
                  <Setting name={SettingName.DarkMode}>
                    <DarkMode />
                    <SectionItemSeparator />
                  </Setting>
                  <Setting name={SettingName.DataConsent}>
                    <DataConsent hasDataConsent={hasDataConsent} onChangeDataConsent={onChangeDataConsent} />
                    <SectionItemSeparator />
                  </Setting>
                  <Setting name={SettingName.KeyboardShortcuts}>
                    <KeyboardShortcuts />
                  </Setting>
                </SidebarSection>
                <SidebarSection hasBottomLine>
                  {isUserRegistered && (
                    <Button
                      type="text"
                      onClick={async () => {
                        await logout({});
                        await refetchAll();
                        container.current?.scrollTo({ top: 0, behavior: 'smooth' });
                      }}
                      css={css`
                        align-items: center;
                        display: inline-flex;
                      `}
                    >
                      {t('logOut')}
                      <ToucanIcon
                        icon="link-out"
                        css={css`
                          color: inherit;
                          width: 12px;
                          height: 12px;
                          margin-left: ${theme.spacing(1)};
                        `}
                      />
                    </Button>
                  )}
                  <Box pt={isUserRegistered ? 3 : 0}>
                    <Typography variant="sm" color={textLevel2} isBold>
                      {t('lookingForHelp')}
                    </Typography>
                    <Typography variant="xs" color={textLevel2}>
                      <Trans
                        i18nKey="visitTheFaqs"
                        values={{
                          faqs: t('faqs'),
                          contactUs: t('contactUs'),
                        }}
                        components={[
                          <Link href={`${Config.siteURL}/faq`}>a</Link>,
                          <Link href={`${Config.siteURL}/contact`}>a</Link>,
                        ]}
                      />
                    </Typography>
                  </Box>
                </SidebarSection>
                <SidebarSection hasBottomLine={false}>
                  {isUserRegistered && (
                    <Button
                      type="text"
                      onClick={async () => {
                        onNavigateSidebar({
                          slide: SlideName.AccountDeletionConfirm,
                        });
                      }}
                      css={css`
                        align-items: center;
                        display: inline-flex;
                        color: ${isDarkMode ? ToucanColors.red[200] : ToucanColors.red[500]};
                        :hover {
                          color: ${isDarkMode ? ToucanColors.red[100] : ToucanColors.red[400]};
                        }
                        :active {
                          color: ${isDarkMode ? ToucanColors.red[200] : ToucanColors.red[500]};
                        }
                      `}
                    >
                      {t('deleteAccount')}
                      <ToucanIcon
                        icon="link-out"
                        css={css`
                          color: inherit;
                          width: 12px;
                          height: 12px;
                          margin-left: ${theme.spacing(1)};
                        `}
                      />
                    </Button>
                  )}
                </SidebarSection>

                <InfoMessage
                  isVisible={infoMessage.isVisible}
                  isError={infoMessage.isError}
                  text={infoMessage.text}
                  isLoaderVisible={infoMessage.isLoaderVisible}
                />
              </motion.div>
            ))}
        </AnimatePresence>
        <Overlay overlayName={OverlayName.Snooze}>
          <Snooze
            domain={domain}
            disabledDuration={disabledDuration}
            selectedComingSoonLanguageId={selectedComingSoonLanguageId}
            setDisabledDuration={setDisabledDuration}
          />
        </Overlay>
      </div>
    </React.Fragment>
  );
};
