/** @jsx jsx */
import React from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import Header from '~/components/shared/Header';
import { Image } from '~/components/shared/Image';
import { Modal, ModalContent, ModalHeader } from '~/components/shared/Modal';
import { defaultSidebarWidth } from '~/constants/sidebar';
import { StorageKey } from '~/constants/storage';
import { useModalStorage } from '~/hooks/useModalStorage';
import { useTranslation } from '~/libs/i18n';

const { Button, ToucanIcon, Typography } = ToucanComponents;

const sites = [
  {
    iconUrl: '/images/youtube-logo.svg',
    name: 'Youtube',
    url: 'https://youtu.be/s4BH7-xDMDk',
  },
  {
    iconUrl: '/images/reddit-logo.svg',
    name: 'Reddit',
    url: 'https://reddit.com',
  },
  {
    iconUrl: '/images/google-logo.svg',
    name: 'Google',
    url: 'https://www.google.com/search?q=language+acquisition',
  },
  {
    iconUrl: '/images/twitter-logo.svg',
    name: 'Twitter',
    url: 'https://twitter.com/JoinToucan',
  },
];

interface SiteOptionProps {
  handleClick: () => void;
  siteIconUrl: string;
  siteName: string;
  siteUrl: string;
}

const SiteOption = ({ handleClick, siteIconUrl, siteName, siteUrl }: SiteOptionProps) => (
  <a
    css={css`
      text-decoration: none;
    `}
    href={siteUrl}
    target="_blank"
    rel="noreferrer noopener"
    onClick={handleClick}
  >
    <Box
      width={90}
      height={96}
      border={`2px solid ${ToucanColors.gray[100]}`}
      borderRadius="4px"
      display="flex"
      flexDirection="column"
      alignItems="center"
      justifyContent="center"
      gap={0.5}
      sx={{
        '&:hover': {
          borderColor: ToucanColors.blue[300],
          backgroundColor: ToucanColors.blue[200],
        },
        '&:active': {
          borderColor: ToucanColors.blue[300],
          backgroundColor: 'transparent',
        },
      }}
    >
      <Box display="flex" alignItems="center" justifyContent="center" height={40} width={40}>
        <Image src={siteIconUrl} alt={siteName} />
      </Box>
      <Typography color={ToucanColors.blue[300]} isBold variant="xs">
        {siteName}
      </Typography>
    </Box>
  </a>
);

export const ExtraPracticeExplanationModal = () => {
  const { closeSidebar } = useSidebar();
  const [isModalOpen, setIsModalOpen] = useModalStorage(StorageKey.IsExtraPracticeExplanationModalOpen, false);
  const { t } = useTranslation();

  const closeModal = () => setIsModalOpen(false);
  return (
    <Modal
      css={css`
        max-width: 450px;
      `}
      leftOverride={`calc(${defaultSidebarWidth} - 100vw)`}
      isOpen={isModalOpen}
      onRequestClose={closeModal}
      shouldSkipAnimation
    >
      <ModalHeader>
        <Header
          onRequestClose={closeModal}
          isMobileReferralShown={false}
          onMobileReferralClick={() => {}}
          css={css`
            background-color: transparent;
          `}
          horizontalLineProps={{
            color: ToucanColors.gray[200],
          }}
        />
      </ModalHeader>
      <ModalContent py={4}>
        <Image src="/images/extra-practice.svg" alt={t('extraPractice')} width="384px" height="240px" />
        <Box pt={4} textAlign="center">
          <Typography color={ToucanColors.gray[600]} font="secondary" variant="md">
            {t('getSomeExtraPractice')}
          </Typography>
        </Box>
        <Box pt={1} textAlign="center">
          <Typography>{t('learnGrammar')}</Typography>
        </Box>
        <Box pt={2} display="flex" justifyContent="center" gap={0.5}>
          {sites.map(({ iconUrl, name, url }) => (
            <SiteOption
              handleClick={() => {
                closeModal();
                closeSidebar();
              }}
              key={name}
              siteIconUrl={iconUrl}
              siteName={name}
              siteUrl={url}
            />
          ))}
        </Box>
      </ModalContent>
    </Modal>
  );
};

interface ExtraPracticeExplanationTriggerProps {
  triggerText: string;
}

export const ExtraPracticeExplanationTrigger = ({ triggerText }: ExtraPracticeExplanationTriggerProps) => {
  const [, setIsModalOpen] = useModalStorage(StorageKey.IsExtraPracticeExplanationModalOpen, false);
  const theme = useTheme();

  return (
    <Typography variant="xs">
      <Button type="text" onClick={() => setIsModalOpen(true)}>
        {triggerText}
        <ToucanIcon
          icon="link-out"
          css={css`
            color: inherit;
            width: 12px;
            height: 12px;
            margin-left: ${theme.spacing(1)};
          `}
        />
      </Button>
    </Typography>
  );
};
