import { useState, useEffect } from 'react';

import { browserVendor } from '~/configs/config';
import { FeatureName } from '~/constants/features';
import { GLOBAL_PERMISSION } from '~/constants/permissions';
import { DensityValuesPrimitive } from '~/constants/settings';
import { StorageKey } from '~/constants/storage';
import { useDomainEnabledEffect } from '~/hooks/useDomainEnabledEffect';
import { useHostPermissions } from '~/hooks/useHostPermissions/useHostPermissions';
import { useStorage } from '~/hooks/useStorage';
import { useUserInfo } from '~/hooks/useUserInfo';
import { VoiceNumber } from '~/libs/TextToSpeech';
import { useFeatureEnabled } from '~/libs/features/client';
import contentMessengers from '~/messengers/contentMessengers';
import {
  SnoozeDuration,
  useGetSettingsQuery,
  useChangeLevelMutation,
  useChangeGloballyEnabledStateMutation,
  useChangeGloballyDisabledDurationMutation,
  useRemoveDomainFromBanlistMutation,
  useAddDomainFromBanlistMutation,
  useChangeHighlightToTranslateEnabledMutation,
  useChangeDailyGoalsEnabledStateMutation,
  useLogoutMutation,
  useUpdateDataConsentMutation,
  useUpdateDifficultyTierMutation,
  useSetUserAudioPreferencesMutation,
} from '~/schema/background';
import { DifficultyTier } from '~/schema/server';
import { BrowserVendors } from '~/types/browsers';
import { SlideName } from '~/types/sidebar';

import { useCurrentDomain } from '../../../../hooks/useCurrentDomain';
import { useSidebarContext } from '../../SidebarContext';

interface SettingsSnapshot {
  isDomainEnabled: boolean;
  isExtensionGloballyEnabled: boolean;
  densityLevel: number;
}

interface SettingsOptions {
  isInPopup?: boolean;
}

export const useSettings = ({ isInPopup }: SettingsOptions) => {
  const { href, currentDomain: domain } = useCurrentDomain();
  const { onNavigateSidebar } = useSidebarContext();
  const prefix = href.match(/\/www\./g) ? 'www.' : '';
  const domainPattern = `*://${prefix}${domain}/*`;
  const [isEnabled] = useStorage<boolean>(StorageKey.GlobalEnableState, true);
  const [isPronunciationEnabled, setIsPronunciationEnabled] = useStorage(StorageKey.PronunciationEnabled, true);

  const { data, refetch, loading, error } = useGetSettingsQuery({
    variables: { domain, origin: href },
    skip: !href,
  });

  const [changeLevel] = useChangeLevelMutation();
  const [changeGlobalEnabledState] = useChangeGloballyEnabledStateMutation();
  const [changeDisabledDuration] = useChangeGloballyDisabledDurationMutation();
  const [removeDomainFromBanlist] = useRemoveDomainFromBanlistMutation();
  const [addDomainToBanlist] = useAddDomainFromBanlistMutation();
  const [updateDataConsent] = useUpdateDataConsentMutation();
  const [updateDifficultyTier] = useUpdateDifficultyTierMutation();
  const [changeAudioPreferences, { loading: changeAudioPreferenceLoading }] = useSetUserAudioPreferencesMutation();
  const { requestPermissions: requestDomainPermissions } = useHostPermissions({
    hosts: [domainPattern],
  });
  const { requestPermissions: requestGlobalPermissions, canRequestPermissions, hasPermissions } = useHostPermissions({
    hosts: [GLOBAL_PERMISSION],
    globalSubstitute: browserVendor === BrowserVendors.Safari,
  });
  const [changeHighlightToTranslateEnabled] = useChangeHighlightToTranslateEnabledMutation();
  const [changeDailyGoalsModalEnabled] = useChangeDailyGoalsEnabledStateMutation();
  const [logout] = useLogoutMutation();
  const isDifficultyEnabled = useFeatureEnabled(FeatureName.DifficultySidebar) ?? false;
  const { isUserRegistered } = useUserInfo();

  // State
  const [isSurveyDisplayed] = useState<boolean | null>(null);
  const [initialValues, setInitialValues] = useState<SettingsSnapshot | null>(null);

  // Derived data
  const isChromePermissionGranted = hasPermissions ?? true;
  const densityLevel = data?.settings.densityLevel ?? 1;
  const isDomainBanned = data?.settings.domain?.isBanned ?? null;
  const isDomainEnabled = !isDomainBanned;
  const isExtensionGloballyEnabled = data?.settings.isGloballyEnabled ?? true;
  const disabledDuration = data?.settings.disabledDuration ?? null;
  const unbannable = !!data?.settings.domain?.unbannable;
  const needsRefresh =
    (initialValues &&
      (initialValues.isDomainEnabled !== isDomainEnabled ||
        initialValues.isExtensionGloballyEnabled !== isExtensionGloballyEnabled ||
        (isInPopup && initialValues.densityLevel !== densityLevel))) ??
    false;
  const hasDomainPermissions = !!data?.browser?.permissions;
  const isUserAccountPremium = !!data?.me?.isPremiumSubscribed;
  const selectedComingSoonLanguageId = data?.settings?.selectedComingSoonLanguageId ?? null;
  const isHighlightToTranslateEnabled = data?.settings.highlightToTranslateEnabled ?? true;
  const isDailyGoalsModalEnabled = data?.settings.isDailyGoalsModalEnabled ?? true;
  const hasDataConsent = data?.settings.isAllowingTrackingAnalytics ?? false;
  const difficultyTier = data?.me?.difficultyTier ?? DifficultyTier.Beginner;
  const playbackRate = data?.me?.audioPreferences?.playbackRate ?? 1;
  const voiceNumber = (data?.me?.audioPreferences?.voiceNumber ?? 0) as VoiceNumber;

  // TODO: this is some complex logic,
  // test are needed for this.
  const isLoaded = !loading;

  // Setters
  const setDifficultyLevel = async (level: number) => {
    await changeLevel({ level });
    await refetch();
  };

  const setIsExtensionGloballyEnabled = async (enabled: boolean | null) => {
    await changeGlobalEnabledState({ enabled: enabled ?? false });
    await refetch();
  };

  const setDisabledDuration = async (duration: SnoozeDuration) => {
    await changeDisabledDuration({ duration });
    await refetch();
  };

  const setUserAudioPreferences = async (voice: number, playback: number) => {
    if (changeAudioPreferenceLoading) {
      return;
    }
    await changeAudioPreferences({ voiceNumber: voice, playbackRate: playback });
    await refetch();
  };

  const onAddDomainToBanlist = async () => {
    await addDomainToBanlist({ domain });
    await refetch();
  };

  const onRemoveDomainFromBanlist = async () => {
    await removeDomainFromBanlist({ domain });
    await refetch();
  };

  const onRequestGlobalPermissions = async () => {
    await requestGlobalPermissions();
    await refetch();
  };

  const onRequestDomainPermissions = async () => {
    await requestDomainPermissions();
    await refetch();
  };

  const onToggleHighlightToTranslateEnabled = async (enabled: boolean) => {
    await changeHighlightToTranslateEnabled({ enabled });
    await refetch();
  };

  const onToggleDailyGoalsModalEnabled = async (enabled: boolean) => {
    await changeDailyGoalsModalEnabled({ enabled });
    await refetch();
  };

  const onTogglePronunciation = async (enabled: boolean) => {
    await setIsPronunciationEnabled(enabled);
    await refetch();
  };

  const openSignup = () => {
    onNavigateSidebar({ slide: SlideName.Authenticate });
  };

  const onChangeDataConsent = async (isAllowingTrackingAnalytics: boolean) => {
    await updateDataConsent({ optIn: isAllowingTrackingAnalytics });
    await refetch();
  };

  const onDifficultyTierChange = async (passedTier: DifficultyTier) => {
    await updateDifficultyTier({ difficultyTier: passedTier });
    await refetch();
    contentMessengers.trackEvent({
      eventName: 'Difficulty Tier Updated',
      properties: {
        difficultyTier: passedTier,
      },
    });
  };

  useEffect(() => {
    if (initialValues || !isLoaded) {
      return;
    }
    setInitialValues({
      isDomainEnabled,
      isExtensionGloballyEnabled,
      densityLevel,
    });
  }, [domain, isDomainEnabled, isExtensionGloballyEnabled, initialValues, densityLevel]);

  useEffect(() => {
    refetch();
  }, [isEnabled]);

  useDomainEnabledEffect(async () => {
    // If the domain enabled state has changed update
    // this can happen if the user uses shortcuts or the pause button
    await refetch();
  }, [refetch]);

  return {
    domain,
    isLoaded,
    error,
    isSurveyDisplayed,
    isChromePermissionGranted,
    hasDataConsent,
    onChangeDataConsent,
    densityLevel: densityLevel as DensityValuesPrimitive,
    difficultyTier: difficultyTier as DifficultyTier,
    isDifficultyEnabled,
    isDomainBanned,
    isExtensionGloballyEnabled,
    isHighlightToTranslateEnabled,
    isDailyGoalsModalEnabled,
    disabledDuration,
    isUserRegistered,
    unbannable,
    needsRefresh,
    hasDomainPermissions,
    isUserAccountPremium,
    selectedComingSoonLanguageId,
    canRequestPermissions,
    setDifficultyLevel,
    setIsExtensionGloballyEnabled,
    setDisabledDuration,
    openSignup,
    onDifficultyTierChange,
    onAddDomainToBanlist,
    onRemoveDomainFromBanlist,
    onRequestGlobalPermissions,
    onRequestDomainPermissions,
    onToggleHighlightToTranslateEnabled,
    onToggleDailyGoalsModalEnabled,
    isPronunciationEnabled,
    onTogglePronunciation,
    logout,
    playbackRate,
    voiceNumber,
    setUserAudioPreferences,
    refetchAll: async () => {
      await refetch();
    },
  };
};
