import React, { useState, useMemo } from 'react';

import useDeepCompareEffect, { useDeepCompareMemoize } from 'use-deep-compare-effect';

import { Box } from '~/components/Box';
import { ReferralCta } from '~/components/Referral/ReferralCta';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { Events } from '~/constants/events';
import { FeatureName } from '~/constants/features';
import { Pinchpoints } from '~/constants/pinchpoints';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { filterNull } from '~/helpers/utilityHelpers';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useReferral } from '~/hooks/useReferral';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useFeatureEnabled } from '~/libs/features/client';
import contentMessengers from '~/messengers/contentMessengers';
import { SavedWordsCalloutStatus, useGetSavedWordsListQuery, WordListSort } from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

import { NoSavedWords } from './NoSavedWords';
import { SavedWordCallout } from './SavedWordCallout';
import { RenderingList } from './types';

export const useSavedWords = ({
  highlightedTranslation,
  openQuiz,
  openSubscribePage,
}: {
  highlightedTranslation?: string;
  openQuiz: (translations: Array<{ translationId: string; sourcePhrase: string; targetPhrase: string }>) => void;
  openSubscribePage: (options: { pinchpoint: Pinchpoints }) => void;
}) => {
  const [wordsToPractice, setWordsToPractice] = useState<Array<string>>([]);
  const [sort, setSort] = useState<WordListSort>(WordListSort.LastSeen);
  const { data, loading, error, refetch } = useGetSavedWordsListQuery({
    variables: { input: { sort, highlightedTranslation } },
  });
  const { toucanRewardOfferedPerReferral, referralCTAText } = useReferral();

  const wordList = data?.screens.savedWordList.translations ?? [];
  const callout = data?.screens.savedWordList.callout;
  const amountToPracticed = data?.screens.savedWordList.amountPracticed ?? 0;
  const isReferralEnabled = useFeatureEnabled(FeatureName.ReferralsV2);
  const { onNavigateSidebar } = useSidebarContext();
  const { isPremiumSubscribed } = useUserInfo();
  const { getFeaturePaywallStatus } = usePremiumExperiment();

  const isSaveWordPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.SaveWord) === PremiumFeaturePaywallStatus.Paid;
  const isSaveWordFeatureBehindPaywall = isSaveWordPremiumFeature && !isPremiumSubscribed;

  const isReviewQuizPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.ReviewQuiz) === PremiumFeaturePaywallStatus.Paid;
  const isReviewQuizFeatureBehindPaywall = isReviewQuizPremiumFeature && !isPremiumSubscribed;

  const onClickReferral = () => {
    contentMessengers.trackEvent({
      eventName: Events.ReferralCtaClicked,
      properties: {
        pinchpointLocation: Pinchpoints.SavedWordsReferralCta,
      },
    });
    onNavigateSidebar({ panel: PanelName.Referral, slide: SlideName.MainReferral });
  };

  /**
   * wordListSections is a list of words from the word list or a object
   * with a `children` property. This is to render these items to the screen
   * inside of the virtual list. These items are rendered in a react-virtualized List component,
   * and dynamically sized in a way that allows us to render anything to this virtualized list.
   */
  const wordListSections: RenderingList = useMemo(
    () =>
      [
        // This configures the callout
        callout && !isSaveWordFeatureBehindPaywall
          ? {
              children: (
                <Box p={2} pb={0} width="100%" data-testid="saved-word-callout">
                  <SavedWordCallout
                    {...callout}
                    onClick={() => {
                      if (
                        callout.status === SavedWordsCalloutStatus.Practice ||
                        callout.status === SavedWordsCalloutStatus.AllPracticed
                      ) {
                        openQuiz(wordList.filter(word => wordsToPractice.includes(word.translationId)));
                      }
                      if (callout.status === SavedWordsCalloutStatus.Locked) {
                        // open subscribe page
                        if (isReviewQuizFeatureBehindPaywall) {
                          openSubscribePage({ pinchpoint: Pinchpoints.SavedWordListReviewQuizCta });
                          return;
                        }
                        openSubscribePage({ pinchpoint: Pinchpoints.SavedWordsPractice });
                      }
                    }}
                  />
                </Box>
              ),
            }
          : null,
        // Display link to Referral panel once any saved word has been practiced within the last day
        amountToPracticed > 0 && toucanRewardOfferedPerReferral && isReferralEnabled
          ? {
              children: (
                <ReferralCta
                  display="flex"
                  justifyContent="center"
                  pt={2}
                  onClickReferral={onClickReferral}
                  referralCTAText={referralCTAText}
                />
              ),
            }
          : null,
        // This add the words in the word list
        ...wordList,
        // This configures the empty state
        wordList.length === 0
          ? {
              children: (
                <Box p={2} width="100%">
                  <NoSavedWords isDisabled={isSaveWordFeatureBehindPaywall} />
                </Box>
              ),
            }
          : null,
        // Padding at bottom of list
        wordList.length
          ? {
              children: <Box pb={3} width="100%" />,
            }
          : null,
      ].filter(filterNull),
    useDeepCompareMemoize([wordList, callout, wordsToPractice]),
  );

  useDeepCompareEffect(() => {
    if (wordList.length > 0) {
      const sorted = wordList.sort((a, b) => a.lastPracticedAt - b.lastPracticedAt);
      setWordsToPractice(sorted.slice(0, 10).map(word => word.translationId));
    }
  }, [wordList]);

  return {
    amountToPracticed,
    wordsToPractice,
    wordList,
    wordListSections,
    loading,
    error,
    refetch,
    sort,
    setSort,
    callout,
  };
};
