import React, { FC } from 'react';

import { css } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';

import { Box, BoxProps } from '~/components/Box';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { browserVendor } from '~/configs/config';
import { getFacebookShareUrl, getTwitterShareUrl } from '~/helpers/share';
import { useTranslation } from '~/libs/i18n';
import { Share } from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';
import { PanelName, SlideName } from '~/types/sidebar';

import { LinkCopy } from './LinkCopy';
import { OrLine } from './OrLine';
import { ShareOption } from './ShareOption';
import { shortenUrl } from './helpers';

const { Typography, useColorScheme } = ToucanComponents;

enum ReferralTypeId {
  Email = 1,
  Facebook = 2,
  Twitter = 3,
}

interface SharePremiumProps extends BoxProps {
  linkUrl: string;
  onShareToFacebook: (share: Share) => void;
  onShareToTwitter: (share: Share) => void;
}

export const SharePremium: FC<SharePremiumProps> = ({ linkUrl, onShareToFacebook, onShareToTwitter, ...boxProps }) => {
  const { t } = useTranslation();
  const { onNavigateSidebar } = useSidebarContext();
  const { isDarkMode } = useColorScheme();

  const shareText = t('beenLearningaNew');

  return (
    <Box {...boxProps}>
      <LinkCopy>{linkUrl}</LinkCopy>
      <OrLine mt={1} />
      <Box mt={2} display="flex">
        <ShareOption
          icon="envelope"
          onClick={() => onNavigateSidebar({ panel: PanelName.Referral, slide: SlideName.EmailReferral })}
          flex={1}
        >
          {t('email')}
        </ShareOption>
        <ShareOption
          icon="twitter"
          onClick={() => {
            const redirectUrl = `${shortenUrl(linkUrl)}?r=${ReferralTypeId.Twitter}`;
            onShareToTwitter({
              text: shareText,
              url: redirectUrl,
              source: 'referral-panel',
            });
            // Safari iOS won't launch a new tab from the background, so we need to launch it from here
            if (browserVendor === BrowserVendors.SafariiOS) {
              const twitterShareUrl = getTwitterShareUrl({ url: redirectUrl, text: shareText });
              window.open(twitterShareUrl, '_blank');
            }
          }}
          flex={1}
        >
          {t('twitter')}
        </ShareOption>
        <ShareOption
          icon="facebook"
          onClick={() => {
            const redirectUrl = `${linkUrl}?r=${ReferralTypeId.Facebook}`;
            onShareToFacebook({
              url: redirectUrl,
              source: 'referral-panel',
            });
            // Safari iOS won't launch a new tab from the background, so we need to launch it from here
            if (browserVendor === BrowserVendors.SafariiOS) {
              const facebookShareUrl = getFacebookShareUrl({ url: redirectUrl });
              window.open(facebookShareUrl, '_blank');
            }
          }}
          flex={1}
        >
          {t('facebook')}
        </ShareOption>
      </Box>
      <Box
        mt={3}
        display="flex"
        justifyContent="center"
        css={css`
          & h6 {
            text-align: center;
          }
        `}
      >
        <Typography variant="2xs" color={isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400]}>
          {t('onlyReedemableFromDesktop')}
        </Typography>
      </Box>
    </Box>
  );
};
