import React, { FC } from 'react';

import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { PremiumUpsell } from '~/components/Sidebar/PremiumUpsell';
import { SidebarItem } from '~/components/Sidebar/SidebarItem';
import { PremiumLockedBadge } from '~/components/shared/PremiumLockedBadge';
import { SubscriptionRedirectContainer } from '~/components/shared/SubscriptionRedirectContainer';
import { Pinchpoints } from '~/constants/pinchpoints';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { DensityValuesPrimitive } from '~/constants/settings';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useSubscription } from '~/hooks/useSubscription';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import { DifficultyTier } from '~/schema/server';

import { ButtonOption } from './ButtonOption';
import { DifficultySelectorOld } from './DensitySelectorOld';
import { DifficultyDescription } from './DifficultyDescription';
import { Slider } from './Slider';

const { Typography, useColorScheme } = ToucanComponents;

interface TranslationDifficultySelectorProps {
  onDifficultyTierChange: (tier: DifficultyTier) => Promise<void>;
  difficultyTier: DifficultyTier;
  densityLevel: DensityValuesPrimitive;
  onChangeDensityLevel: (level: DensityValuesPrimitive) => Promise<void>;
  isUserAccountPremium: boolean;
  disableDifficulty?: boolean;
}

export const DifficultySelector: FC<TranslationDifficultySelectorProps> = ({
  densityLevel,
  onChangeDensityLevel,
  difficultyTier,
  onDifficultyTierChange,
  disableDifficulty = false,
  isUserAccountPremium,
}) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { getFeaturePaywallStatus } = usePremiumExperiment();
  const { textLevel1 } = useColorScheme();
  const { isPremiumSubscribed } = useUserInfo();
  const { openSubscribePage } = useSubscription();

  const isBehindPaywall =
    getFeaturePaywallStatus(PremiumFeatureName.DifficultySettings) === PremiumFeaturePaywallStatus.Paid &&
    !isPremiumSubscribed;

  if (!disableDifficulty) {
    return (
      <SidebarItem>
        <Box mb={2} display="flex" align-item="center" gap={1}>
          <Typography variant="sm" isBold color={textLevel1}>
            {t('learningLevel')}
          </Typography>
          {isBehindPaywall && <PremiumLockedBadge data-testid="difficulty-density-selector-premium-badge" />}
        </Box>
        {isBehindPaywall && (
          <PremiumUpsell
            imageUrl="/images/sparkle.svg"
            imageAlt={t('sparkle')}
            linkText={t('startPremiumFreeTrial')}
            onClickTextButton={() => openSubscribePage({ pinchpoint: Pinchpoints.LearningLevelPremiumUpsell })}
            marginBottom={1}
          >
            {t('takeControlHowManyTranslationsDifficultWordsAre')}
          </PremiumUpsell>
        )}
        <SubscriptionRedirectContainer
          shouldRedirect={isBehindPaywall}
          pinchpoint={Pinchpoints.LearningLevelPremiumUpsell}
        >
          <Box role="radiogroup" display="flex" flexDirection="row" gap={theme.spacing(1)} pt={1}>
            {Object.values(DifficultyTier).map(tier => (
              <ButtonOption
                key={tier}
                isChecked={tier === difficultyTier}
                onClick={() => {
                  onDifficultyTierChange(tier);
                }}
                isDisabled={isBehindPaywall}
                data-testid="difficulty-selector"
              >
                {t(tier.toLowerCase() as 'beginner' | 'intermediate' | 'advanced')}
              </ButtonOption>
            ))}
          </Box>
          <Slider
            step={1}
            max={3}
            min={0}
            value={densityLevel}
            onChange={onChangeDensityLevel}
            paddingTop={2}
            isDisabled={isBehindPaywall}
            data-testid="density-selector"
          />
          <DifficultyDescription
            pt={1}
            difficultyTier={difficultyTier}
            densityLevel={densityLevel}
            isDisabled={isBehindPaywall}
          />
        </SubscriptionRedirectContainer>
      </SidebarItem>
    );
  }

  return (
    <DifficultySelectorOld
      isUserAccountPremium={isUserAccountPremium}
      densityLevel={densityLevel}
      onChangeDensityLevel={onChangeDensityLevel}
    />
  );
};
