import React from 'react';

import { css } from '@emotion/react';

import { Slides } from '~/components/Sidebar/Slides';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { Slide } from '~/components/shared/Slide';
import { SlideName } from '~/types/sidebar';

import { LearningShortcut } from './LearningShortcut';
import { LearningShortcutTopic } from './LearningShortcutTopic';
import { TopicWordList } from './TopicWordList';

interface FocusManagementContainerProps {
  isInPopup: boolean;
}

export const FocusManagementContainer = ({ isInPopup }: FocusManagementContainerProps) => {
  const {
    initialState: { hydration },
  } = useSidebar();
  const { learningShortcutTopicId, learningShortcutId } = hydration ?? {};

  return (
    <>
      <Slides display="flex" flex="1">
        <Slide
          slideName={SlideName.ProgressOverview}
          css={css`
            flex: 1;
          `}
        >
          <LearningShortcut />
        </Slide>

        <Slide slideName={SlideName.LearningShortcutTopic}>
          <LearningShortcutTopic isInPopup={isInPopup} learningShortcutId={learningShortcutId} />
        </Slide>

        <Slide
          slideName={SlideName.TopicWordList}
          css={css`
            flex: 1;
          `}
        >
          <TopicWordList isInPopup={isInPopup} topicId={learningShortcutTopicId} />
        </Slide>
      </Slides>
    </>
  );
};
