import React, { useEffect, useRef } from 'react';

import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { FadeIn } from '~/components/shared/FadeIn';
import { SidebarErrorMessage } from '~/components/shared/SidebarErrorMessage';
import { Events, EventSources } from '~/constants/events';
import { StorageKey } from '~/constants/storage';
import { getErrorId } from '~/helpers/errors';
import { useStorage } from '~/hooks/useStorage';
import ContentMessengers from '~/messengers/contentMessengers';
import { useGetLearningShortcutsOverviewQuery } from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

import { Box } from '../../../Box';

import { LearningShortcutBanner } from './LearningShortcutBanner';
import { LearningShortcutBody } from './LearningShortcutBody';
import { LearningShortcutHeader } from './LearningShortcutHeader';
import { LearningShortcutMore } from './LearningShortcutMore';
import { LearningShortcutSlideHeader } from './LearningShortcutSlideHeader';
import { Loading } from './Loading';

interface ViewTrackerProps {
  activeShortcut?: string;
}

const ViewTracker = ({ activeShortcut }: ViewTrackerProps) => {
  const [viewCount, setViewCount, loading] = useStorage(StorageKey.ShortcutOptionsViewCount, 0);
  const hasTracked = useRef(false);
  useEffect(() => {
    if (!loading && !hasTracked.current) {
      const updatedViewCount = viewCount + 1;
      ContentMessengers.trackEvent({
        eventName: Events.ShortcutOptionsViewed,
        properties: {
          activeShortcut,
          location: EventSources.Sidebar,
          viewCount: updatedViewCount,
        },
        userOptions: { includePremiumSubscriptionStatus: true },
      });
      hasTracked.current = true;
      setViewCount(updatedViewCount);
    }
  });
  return null;
};

export const LearningShortcutContainer = () => {
  const { data, error, loading = true } = useGetLearningShortcutsOverviewQuery();
  const { learningShortcutItem, moreLearningShortcuts } = data?.screens?.learningShortcutsOverview ?? {};
  const { openSidebar } = useSidebar();
  const { onNavigateSidebar } = useSidebarContext();
  const hasTracked = useRef(false);
  useEffect(() => {
    if (!loading && !hasTracked.current && (learningShortcutItem || moreLearningShortcuts)) {
      ContentMessengers.trackEvent({
        eventName: Events.ShortcutsSidebarPanelSeen,
        properties: {
          activeShortcut: learningShortcutItem?.title ?? undefined,
        },
      });
      hasTracked.current = true;
    }
  });

  if (loading) {
    return <Loading />;
  }

  const isShortcutAvailable = learningShortcutItem !== null;

  const onNavigateToDetails = (topicId: string) => {
    // Stores data
    openSidebar({
      panelName: PanelName.Progress,
      slideName: SlideName.LearningShortcutTopic,
      hydration: {
        learningShortcutId: topicId,
      },
    });
    onNavigateSidebar({
      panel: PanelName.Progress,
      slide: SlideName.LearningShortcutTopic,
      hydration: {
        learningShortcutId: topicId,
      },
    });
  };

  const onNavigateToTopic = (topicId: string) => {
    // Stores data
    openSidebar({
      panelName: PanelName.Progress,
      slideName: SlideName.TopicWordList,
      hydration: {
        learningShortcutTopicId: topicId,
      },
    });
    onNavigateSidebar({
      panel: PanelName.Progress,
      slide: SlideName.TopicWordList,
      hydration: {
        learningShortcutTopicId: topicId,
      },
    });
  };

  return (
    <Box>
      <LearningShortcutSlideHeader />
      <FadeIn show>
        <LearningShortcutBanner
          isShortcutAvailable={isShortcutAvailable}
          heroImage={learningShortcutItem?.heroImage.heroImageUrl ?? ''}
          badgeImage={learningShortcutItem?.badgeImage.badgeImageUrl ?? ''}
        />
        <Box
          sx={{
            mx: 4,
            mb: 3,
          }}
        >
          {(() => {
            if (error) {
              const errorId = getErrorId(error);
              return <SidebarErrorMessage message={error.message} errorId={errorId} />;
            }
            return (
              <>
                {learningShortcutItem && (
                  <>
                    <LearningShortcutHeader
                      onNavigateToDetails={onNavigateToDetails}
                      title={learningShortcutItem.title}
                      percentCompleted={learningShortcutItem.percentCompleted}
                      numberOfWordsLearnedToday={learningShortcutItem.numberOfWordsLearnedToday}
                      learningShortcutId={learningShortcutItem.learningShortcutId}
                      completedLearningShortcutPhraseCount={learningShortcutItem.completedLearningShortcutPhraseCount}
                      possibleLearningShortcutPhraseCount={learningShortcutItem.possibleLearningShortcutPhraseCount}
                    />
                    <LearningShortcutBody
                      onNavigateToDetails={onNavigateToDetails}
                      onNavigateToTopic={onNavigateToTopic}
                      currentFocusLearningShortcutId={learningShortcutItem.currentFocus.learningShortcutId ?? undefined}
                      currentFocusName={learningShortcutItem.currentFocus.name ?? undefined}
                      whatYouKnow={learningShortcutItem.whatYouKnow}
                    />
                  </>
                )}
                <ViewTracker activeShortcut={learningShortcutItem?.title} />
                <LearningShortcutMore
                  moreLearningShortcuts={moreLearningShortcuts}
                  isShortcutAvailable={isShortcutAvailable}
                  onNavigateToDetails={onNavigateToDetails}
                />
              </>
            );
          })()}
        </Box>
      </FadeIn>
    </Box>
  );
};
