/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Tabs as MUITabs, TabsProps, Tab as MUITab, TabProps, useTheme } from '@mui/material';

const { useColorScheme } = ToucanComponents;

export const Tabs: FC<TabsProps & { inverse?: boolean }> = ({ inverse, ...props }) => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  return (
    <MUITabs
      css={css`
        border-top: ${inverse ? 'none' : `1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]}`};
        border-bottom: ${inverse
          ? `1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]}`
          : 'none'};
        padding-left: ${theme.spacing(2)};
        padding-right: ${theme.spacing(2)};
        & .MuiTabs-indicator {
          background-color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
          height: 4px;
          bottom: ${inverse ? 'auto' : 0};
          top: ${inverse ? 0 : 'auto'};
        }
      `}
      {...props}
    />
  );
};

export const Tab: FC<TabProps> = props => {
  const { isDarkMode } = useColorScheme();
  return (
    <MUITab
      css={css`
        transition: color 0.2s ease-in;
        flex: 1;
        color: ${isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400]};
        font-size: 14px;
        font-weight: 700;
        text-transform: none;
        & .Mui-selected {
          & * {
            color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
          }
        }
      `}
      {...props}
    />
  );
};
