import React, { FC } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { GraphQLFormattedError } from 'graphql';

import { Box, BoxProps } from '~/components/Box';
import Configs from '~/configs/config';
import { useTranslation } from '~/libs/i18n';

const { Typography, useColorScheme } = ToucanComponents;

interface EmailReferralErrorProps extends BoxProps {
  error: Error | GraphQLFormattedError<Record<string, any>>;
  errorId?: string;
}

export const EmailReferralError: FC<EmailReferralErrorProps> = ({ error, errorId, ...boxProps }) => {
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();

  let errorMessage = error?.message ?? t('birdGotLoose');
  if (Configs.env === 'production') {
    errorMessage = t('birdGotLoose');
  }

  return (
    <Box
      css={css`
        h5 {
          color: ${isDarkMode ? ToucanColors.red[200] : ToucanColors.red[300]};
        }
      `}
      {...boxProps}
    >
      <Typography variant="xs">{errorMessage}</Typography>
      {errorId && <Typography variant="xs">{t('errorId', { id: errorId })}</Typography>}
    </Box>
  );
};
