/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme, alpha } from '@mui/material';

import { Image } from '~/components/shared/Image';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';

const { Typography, ToucanIcon } = ToucanComponents;

interface HelpfulTipsProps {
  difficultyLevel: number;
  isGloballyEnabled: boolean;
  hasGlobalPermissions: boolean;
  onRequestPermissions: () => Promise<boolean>;
  onEnableGlobally: () => void;
  onChangeDifficulty: () => void;
}

interface TipItemProps {
  onClick: () => void;
  title: string;
  image: string;
  hasBorder?: boolean;
}

export const HelpfulTips: FC<HelpfulTipsProps> = ({
  difficultyLevel,
  isGloballyEnabled,
  hasGlobalPermissions,
  onChangeDifficulty,
  onEnableGlobally,
  onRequestPermissions,
}) => {
  const { t } = useTranslation();
  const { targetLanguageInfo } = useLanguageInfo();

  const canUpgradeDifficulty = difficultyLevel !== 3 && isGloballyEnabled;
  const hasBestSettings = isGloballyEnabled && hasGlobalPermissions && !canUpgradeDifficulty;

  return (
    <Box pt={2}>
      <Box pb={2}>
        <Typography
          variant="sm"
          isBold
          css={css`
            color: ${ToucanColors.white};
          `}
        >
          {t('yourProgress', { language: targetLanguageInfo?.language ?? '' })}
        </Typography>
      </Box>
      <Box pb={2}>
        <Typography
          variant="xs"
          css={css`
            color: ${ToucanColors.white};
          `}
        >
          {t('youDidntHaveActivityYesterday')}
        </Typography>
      </Box>
      <Box>
        {canUpgradeDifficulty && (
          <TipItem
            onClick={() => {
              onChangeDifficulty();
            }}
            title={t('changeHowManyWordsYouSee')}
            image="images/browser-window.svg"
          />
        )}
        {!hasGlobalPermissions && (
          <TipItem
            hasBorder={canUpgradeDifficulty}
            onClick={async () => {
              await onRequestPermissions();
            }}
            title={t('enableToucanOnAllSites')}
            image="images/lamp-with-laptop.svg"
          />
        )}
        {!isGloballyEnabled && (
          <TipItem
            hasBorder={!hasGlobalPermissions}
            onClick={async () => {
              onEnableGlobally();
            }}
            title={t('turnToucanBackOnIfPaused')}
            image="images/progress/alarm.svg"
          />
        )}
        {hasBestSettings && (
          <Typography
            variant="xs"
            css={css`
              color: ${ToucanColors.white};
            `}
          >
            {t('youWillSeeMoreTranslatedWordsByBrowsing')}
          </Typography>
        )}
      </Box>
    </Box>
  );
};

const TipItem: FC<TipItemProps> = ({ onClick, title, image, hasBorder }) => {
  const theme = useTheme();
  return (
    <Box
      onClick={onClick}
      onKeyPress={onClick}
      tabIndex={0}
      css={css`
        margin-left: -${theme.spacing(1)};
        margin-right: -${theme.spacing(1)};
        border-radius: 8px;
        cursor: pointer;
        transition: all 0.2s ease;
        box-shadow: inset 0 1px 0 -1px ${alpha(ToucanColors.white, 0.3)};
        :hover {
          box-shadow: inset 0 0 0 2px ${alpha(ToucanColors.white, 0.3)};
        }
        :hover hr {
          opacity: 0;
        }
        :focus {
          box-shadow: inset 0 0 0 2px ${alpha(ToucanColors.blue[100], 0.3)};
          outline: none;
        }
      `}
    >
      {hasBorder && (
        <Box
          component="hr"
          mb={0}
          mr={1}
          ml={1}
          css={css`
            transition: opacity 0.2s ease;
            margin-top: -1px;
            background: ${ToucanColors.white};
            opacity: 0.2;
            border: none;
            height: 1px;
          `}
        />
      )}
      <Box display="flex" alignItems="center" padding={1}>
        <Image
          css={css`
            flex: 0;
          `}
          src={image}
          alt={title}
          width="20px"
          height="20px"
        />
        <Box flex={1} pl={2} pr={2}>
          <Typography
            variant="xs"
            isBold
            css={css`
              color: ${ToucanColors.white};
            `}
          >
            {title}
          </Typography>
        </Box>
        <Box flex={0}>
          <ToucanIcon
            css={css`
              color: ${ToucanColors.white};
              width: 12px;
              height: 12px;
            `}
            icon="chevron-right"
          />
        </Box>
      </Box>
    </Box>
  );
};
