import React, { FC } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import Configs from '~/configs/config';
import { useTranslation } from '~/libs/i18n';

import { Box, BoxProps } from '../Box';

import { Image } from './Image';

const { Typography, useColorScheme } = ToucanComponents;

interface ErrorMessageProps extends BoxProps {
  message?: Array<string> | string | undefined;
  errorId?: string;
}

export const SidebarErrorMessage: FC<ErrorMessageProps> = ({ message, errorId, ...boxProps }) => {
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();

  if (!message || !message.length) {
    return null;
  }

  const messages = Array.isArray(message) ? message : [message];
  let errorMessage = messages[0];
  if (errorMessage === 'We ran into an issue sending you an email') {
    errorMessage = t('wereExperiencingIssues');
  } else if (errorMessage === 'Authentication failed') {
    errorMessage = t('sorryTryAgain');
  } else if (Configs.env === 'production') {
    errorMessage = t('birdGotLoose');
  }

  return (
    <Box
      width="100%"
      textAlign="center"
      display="flex"
      alignItems="center"
      flexDirection="column"
      {...boxProps}
      css={css`
        p,
        h5 {
          color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
        }
      `}
    >
      <Image src="./images/toucan-shrug.svg" alt={t('toucanInCircle')} mb={3} width="120px" />
      <Typography variant="sm" isBold>
        {errorMessage}
      </Typography>
      {errorId ? <Typography variant="xs">{t('errorId', { id: errorId })}</Typography> : null}
    </Box>
  );
};
