import React, { memo } from 'react';

import { css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { SlideName } from '~/types/sidebar';

const { Typography, Link, Button, ToucanIcon } = ToucanComponents;

interface CTAProps {
  children?: string | null;
  ctaURL?: string | null;
  slide?: string | null;
}

export const CTA = memo(({ children, ctaURL, slide }: CTAProps) => {
  const theme = useTheme();
  const { onNavigateSidebar } = useSidebarContext();

  const navigateToSlide = (slideName: SlideName) => {
    onNavigateSidebar({ slide: slideName });
  };

  return (
    <Typography variant="xs">
      {slide ? (
        <Button
          type="text"
          onClick={() => navigateToSlide(slide as SlideName)}
          css={css`
            display: flex;
            align-items: center;
          `}
        >
          {children}
          <ToucanIcon
            icon="chevron-right"
            css={css`
              width: 10px;
              color: inherit;
              margin-left: ${theme.spacing(0.5)};
            `}
          />
        </Button>
      ) : (
        <>
          <Link
            href={ctaURL}
            css={css`
              display: flex;
              align-items: center;
            `}
          >
            {children}
            <ToucanIcon
              icon="chevron-right"
              css={css`
                width: 10px;
                color: inherit;
                margin-left: ${theme.spacing(0.5)};
              `}
            />
          </Link>
        </>
      )}
    </Typography>
  );
});
