import React, { FC, ReactNode } from 'react';

import { ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { PremiumUpsell } from '~/components/Sidebar/PremiumUpsell';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { PremiumLockedBadge } from '~/components/shared/PremiumLockedBadge';
import { Pinchpoints } from '~/constants/pinchpoints';
import { useSubscription } from '~/hooks/useSubscription';
import { useTranslation } from '~/libs/i18n';
import { WordListSort as WordListSortType } from '~/schema/background';

import { WordListSort } from '../Progress/layout/WordListSort';

const { useColorScheme } = ToucanComponents;

interface SavedWordsHeaderProps {
  title: ReactNode;
  onChangeSort: (sort: WordListSortType) => void;
  sort: WordListSortType;
  isPremiumLocked?: boolean;
}

export const SavedWordsHeader: FC<SavedWordsHeaderProps> = ({ title, sort, onChangeSort, isPremiumLocked = false }) => {
  const { backgroundBase } = useColorScheme();
  const { t } = useTranslation();
  const { openSubscribePage } = useSubscription();

  return (
    <Box zIndex={2} position="sticky" top="0" py={2} px={4} bgcolor={backgroundBase} mb={1}>
      <SlideHeader
        bottomContent={!isPremiumLocked && <WordListSort sort={sort} onChange={onChangeSort} />}
        isBackButtonVisible={false}
      >
        <Box display="flex" alignItems="center" gap={1}>
          <span>{title}</span>
          {isPremiumLocked && <PremiumLockedBadge data-testid="saved-words-panel-premium-badge" />}
        </Box>
        {isPremiumLocked && (
          <PremiumUpsell
            imageUrl="/images/paper.svg"
            imageAlt={t('notepad')}
            linkText={t('startFreeTrialFirstLetterCapitalized')}
            onClickTextButton={() => openSubscribePage({ pinchpoint: Pinchpoints.SavedWordsPremiumUpsell })}
          >
            {t('joinToucanPremiumSaveWordsUnlockPracticeSessions')}
          </PremiumUpsell>
        )}
      </SlideHeader>
    </Box>
  );
};
