import React, { useState } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { CircularProgress, css } from '@mui/material';

import { useMountedRef } from '~/hooks/useMountedRef';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { useRequestAccountDeletionMutation } from '~/schema/background';
import { SlideName } from '~/types/sidebar';

import { Box } from '../Box';
import { useSidebarContext } from '../Sidebar/SidebarContext';
import { SidebarSection } from '../Sidebar/SidebarSection';
import { SlideHeader } from '../Sidebar/SlideHeader';
import { Image } from '../shared/Image';

const { Button, Typography, useColorScheme } = ToucanComponents;

export const AccountDeletionConfirm = () => {
  const [loading, setLoading] = useState<boolean>(false);
  const { t } = useTranslation();
  const { onNavigateSidebar, onNavigateBack } = useSidebarContext();
  const [requestAccountDeletion] = useRequestAccountDeletionMutation();
  const { isDarkMode } = useColorScheme();
  const isMounted = useMountedRef();

  return (
    <>
      <SidebarSection>
        <SlideHeader>{t('deleteAccount')}</SlideHeader>
      </SidebarSection>
      <SidebarSection
        hasBottomLine={false}
        display="flex"
        flexDirection="column"
        alignItems="center"
        justifyContent="center"
        textAlign="center"
      >
        <Image mt={2} src="/images/toucan-sad.svg" alt={t('toucanLogo')} />
        <Box pt={2} pb={1}>
          <Typography variant="sm" isBold color={isDarkMode ? ToucanColors.white : ToucanColors.gray[500]}>
            {t('justAHeadsUp')}
          </Typography>
        </Box>
        <Typography variant="sm" color={isDarkMode ? ToucanColors.white : ToucanColors.gray[500]}>
          {t('ifYouDeleteYourAccount')}
        </Typography>

        <Box pt={2} pb={1} width="100%" display="flex" flexDirection="column">
          <Button
            type="secondary"
            isReversed={isDarkMode}
            onClick={() => {
              onNavigateBack();
            }}
          >
            {t('nevermind')}
          </Button>
        </Box>
        <Box
          width="100%"
          display="flex"
          flexDirection="column"
          css={css`
            & svg {
              color: ${ToucanColors.white};
            }
          `}
        >
          <Button
            type="urgent"
            onClick={async () => {
              setLoading(true);
              try {
                await requestAccountDeletion({});
                await contentMessengers.trackEvent({
                  eventName: 'Account Deletion Requested',
                });
              } catch (e) {
                // Ignore
              } finally {
                if (isMounted()) {
                  onNavigateSidebar({ slide: SlideName.AccountDeletionConfirmation });
                  setLoading(false);
                }
              }
            }}
          >
            {loading ? <CircularProgress size={16} thickness={3} /> : t('deleteMyAccount')}
          </Button>
        </Box>
      </SidebarSection>
    </>
  );
};
