/** @jsx jsx */
import React, { useState, useEffect, FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { useForm } from 'react-hook-form';

import { ErrorMessage } from '~/components/shared/ErrorMessage';
import { email as emailRegex } from '~/constants/regex';
import { resolveErrors } from '~/helpers/errors';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useForgotPasswordMutation } from '~/schema/background';

import { useAnonymousUserSettings } from '../AnonymousUserExtensionSettingsContext';

interface ForgotPasswordInputs {
  email: string;
}

const { Typography, Button, ToucanIcon, TextField } = ToucanComponents;

export const ForgotPassword: FC = () => {
  const [hasResetEmailBeenSent, setHasResetEmailBeenSent] = useState(false);
  const [isButtonVisibleAfterSend, setIsButtonVisibleAfterSend] = useState(false);
  const [isUserEmailDomainGmail, setIsUserEmailDomainGmail] = useState(false);
  const theme = useTheme();
  const {
    register,
    handleSubmit,
    formState: { errors: clientErrors },
  } = useForm<ForgotPasswordInputs>();
  const { isDarkModeEnabled, closeAccountCreationWidget } = useAnonymousUserSettings();
  const [forgotPassword, { error }] = useForgotPasswordMutation();
  const { t } = useTranslation();

  const errors = resolveErrors<{
    email?: string;
  }>(clientErrors, error);

  useEffect(() => {
    ContentMessengers.trackEvent({
      eventName: 'Forgot Password Screen Viewed',
      properties: {
        isInGuidedOnboarding: false,
      },
    });
  }, []);

  useEffect(() => {
    if (isUserEmailDomainGmail) {
      setIsButtonVisibleAfterSend(true);
    }
  }, [isUserEmailDomainGmail]);

  const updateUserEmailDomain = (email: string) => {
    const emailDomain = email.split('@')[1];
    if (emailDomain === 'gmail.com') {
      setIsUserEmailDomainGmail(true);
    }
  };

  const onPasswordReset = async (inputs: ForgotPasswordInputs) => {
    const resp = await forgotPassword({ auth: inputs });

    if (resp.data?.forgotPassword) {
      ContentMessengers.trackEvent({
        eventName: 'Forgot Password Success',
        properties: {
          isInGuidedOnboarding: false,
        },
      });

      setHasResetEmailBeenSent(true);
      updateUserEmailDomain(inputs.email);
    }
  };

  const openGmail = () => {
    window.open('https://mail.google.com/', '_blank');
    closeAccountCreationWidget();
  };

  const getButtonAction = (inputs: ForgotPasswordInputs) => {
    if (hasResetEmailBeenSent) {
      if (isUserEmailDomainGmail) {
        return openGmail();
      }
      return closeAccountCreationWidget();
    }
    return onPasswordReset(inputs);
  };

  const getButtonText = () => {
    if (hasResetEmailBeenSent) {
      if (isUserEmailDomainGmail) {
        return t('openGmail');
      }
      return t('close');
    }
    return t('send');
  };

  return (
    <div>
      <form
        css={css`
          margin-bottom: ${theme.spacing(hasResetEmailBeenSent ? 0 : 2)};
        `}
        onSubmit={handleSubmit(getButtonAction)}
      >
        {!hasResetEmailBeenSent && (
          <React.Fragment>
            <TextField
              label={t('email')}
              hasError={!!errors.email}
              helperText={errors.email?.message}
              type="email"
              {...register('email', {
                required: `${t('pleaseEnterValidEmail')}`,
                pattern: {
                  value: emailRegex,
                  message: t('pleaseEnterValidEmail'),
                },
              })}
              isDarkMode={isDarkModeEnabled}
            />
            <ErrorMessage
              message={errors.message}
              mt={1}
              mb={1}
              css={css`
                margin-bottom: ${theme.spacing(2)};
              `}
              frameless
            />
          </React.Fragment>
        )}
      </form>
      {hasResetEmailBeenSent && (
        <div
          css={css`
            display: flex;
            align-items: center;
            margin-bottom: ${theme.spacing(2)};
          `}
        >
          <ToucanIcon
            icon="check"
            css={css`
              color: ${theme.palette.success.main};
              width: 12px;
              height: auto;
              margin-right: ${theme.spacing(1)};
            `}
          />
          <Typography
            variant="sm"
            isBold
            css={css`
              color: ${theme.palette.success.main};
            `}
          >
            {t('passwordResetHasBeenSent')}
          </Typography>
        </div>
      )}

      {(!hasResetEmailBeenSent || (hasResetEmailBeenSent && isButtonVisibleAfterSend)) && (
        <Button
          size="medium"
          type="utility"
          onClick={handleSubmit(getButtonAction)}
          css={css`
            width: 100%;
          `}
        >
          {getButtonText()}
        </Button>
      )}
    </div>
  );
};
