/** @jsx jsx */
import { useState, useEffect } from 'react';

import { jsx } from '@emotion/react';
import { motion, AnimatePresence } from 'framer-motion';

import { useSidebarContext } from '~/components/Sidebar/SidebarContext';

import { AnonymousUserExtensionSettings } from './AnonymousUserExtensionSettings';
import { UnregisteredUserExtensionSettings } from './UnregisteredUserExtensionSettings';

interface LoginSignupProps {
  isAccountCreationWidgetShown: boolean;
  onAccountCreationWidgetClose: () => void;
  isInPopup: boolean;
  refetch: () => Promise<void>;
  shouldRequireRegistration?: boolean;
  isUserInfoLoading?: boolean;
}

export const LoginSignup = ({
  isAccountCreationWidgetShown,
  onAccountCreationWidgetClose,
  isInPopup,
  refetch,
  shouldRequireRegistration = false,
  isUserInfoLoading,
}: LoginSignupProps) => {
  const [isAccountWidgetShown, setIsAccountWidgetShown] = useState(false);
  const { setDisableClickAway } = useSidebarContext();

  useEffect(() => {
    if (isAccountCreationWidgetShown) {
      setIsAccountWidgetShown(true);
    }
  }, [isAccountCreationWidgetShown]);

  useEffect(() => {
    setDisableClickAway(true);

    return () => {
      setDisableClickAway(false);
    };
  }, []);

  return (
    <AnimatePresence onExitComplete={onAccountCreationWidgetClose}>
      {isAccountWidgetShown && (
        <motion.div
          key="login-signup"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0, ease: 'easeIn', duration: 0.3 }}
          exit={{ opacity: 0, transition: { ease: 'easeOut', duration: 0.3 } }}
        >
          {shouldRequireRegistration ? (
            <UnregisteredUserExtensionSettings
              closeAccountCreationWidget={() => setIsAccountWidgetShown(false)}
              isInPopup={isInPopup}
              refetch={refetch}
              isUserInfoLoading={isUserInfoLoading}
            />
          ) : (
            <AnonymousUserExtensionSettings
              closeAccountCreationWidget={() => setIsAccountWidgetShown(false)}
              isInPopup={isInPopup}
              refetch={refetch}
            />
          )}
        </motion.div>
      )}
    </AnimatePresence>
  );
};
