/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { useTheme } from '@mui/material';

import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';
import { SnoozeDuration, ComingSoonLanguageId } from '~/schema/background';

import { EnableExtension } from './Settings/EnableExtension';

interface SnoozeProps {
  setDisabledDuration: (duration: SnoozeDuration) => Promise<void>;
  domain: string;
  disabledDuration: SnoozeDuration | null;
  selectedComingSoonLanguageId?: ComingSoonLanguageId | null;
}

export const Snooze: FC<SnoozeProps> = ({
  domain,
  disabledDuration,
  selectedComingSoonLanguageId,
  setDisabledDuration,
}) => {
  const { t } = useTranslation();
  const theme = useTheme();

  return (
    <div
      css={css`
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: ${theme.spacing(1)} ${theme.spacing(4)};
      `}
    >
      <Image src="/images/toucan-sleep.svg" alt={t('pausingToucan')} width="314px" />
      <EnableExtension
        domain={domain}
        disabledDuration={disabledDuration}
        onDisabledDurationChange={setDisabledDuration}
        selectedComingSoonLanguageId={selectedComingSoonLanguageId}
      />
    </div>
  );
};
