import React, { useEffect } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { CircularProgress } from '@mui/material';

import { Box } from '~/components/Box';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { ExtraPracticeExplanationTrigger } from '~/components/Sidebar/content/ExtraPracticeExplanation';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { FadeIn } from '~/components/shared/FadeIn';
import { Image } from '~/components/shared/Image';
import { SidebarErrorMessage } from '~/components/shared/SidebarErrorMessage';
import { Events, EventSources } from '~/constants/events';
import { FeatureName } from '~/constants/features';
import { getErrorId } from '~/helpers/errors';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useActivateLearningShortcutMutation, useGetLearningShortcutDetailsQuery } from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

import { Header } from '../TopicWordList/Header';

import { Placeholder } from './Placeholder';
import { TopicItem } from './TopicItem';

const { Typography, useColorScheme, Button } = ToucanComponents;

interface LearningShortcutTopicContainerProps {
  learningShortcutId?: string;
  isInPopup: boolean;
}

export const LearningShortcutTopicContainer = ({
  isInPopup,
  learningShortcutId,
}: LearningShortcutTopicContainerProps) => {
  const { data, loading, error, refetch } = useGetLearningShortcutDetailsQuery({
    skip: !learningShortcutId,
    variables: {
      input: {
        learningShortcutId: learningShortcutId ?? '',
      },
    },
  });
  const [activateShortcut, { loading: activatingShortcut }] = useActivateLearningShortcutMutation();
  const { openSidebar } = useSidebar();
  const { onNavigateSidebar } = useSidebarContext();
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const isModalEnabled = useFeatureEnabled(FeatureName.ExtraPracticeExplanationModal);

  const imageUrl = data?.screens.learningShortcutDetails.heroImage.heroImageUrl ?? '';
  const imageAlt = data?.screens.learningShortcutDetails.heroImage.heroImageAltText ?? '';
  const name = data?.screens.learningShortcutDetails.name ?? '';
  const title = data?.screens.learningShortcutDetails.title ?? '';
  const details = data?.screens.learningShortcutDetails.details ?? '';
  const topics = data?.screens.learningShortcutDetails.learningShortcutTopics ?? [];
  const isActive = data?.screens.learningShortcutDetails.isActive ?? false;

  useEffect(() => {
    if (name === '') {
      return;
    }
    // TODO: include active shortcut but that requires updating the graphql schema
    ContentMessengers.trackEvent({
      eventName: 'Shortcut Topics Seen',
      properties: {
        viewedShortcut: name,
      },
    });
  }, [name]);

  const onNavigateToTopic = (topicId: string) => {
    // Stores data
    openSidebar({
      panelName: PanelName.Progress,
      slideName: SlideName.TopicWordList,
      hydration: {
        learningShortcutTopicId: topicId,
        learningShortcutId,
      },
    });
    onNavigateSidebar({
      panel: PanelName.Progress,
      slide: SlideName.TopicWordList,
      hydration: {
        learningShortcutTopicId: topicId,
        learningShortcutId,
      },
    });
  };

  const onActivateShortcut = async () => {
    if (activatingShortcut) {
      return;
    }
    await activateShortcut({
      input: {
        learningShortcutId: learningShortcutId ?? '',
      },
    });
    await refetch();

    ContentMessengers.trackEvent({
      eventName: Events.ShortcutSelectionSet,
      properties: {
        activeShortcut: name,
        location: EventSources.Sidebar,
      },
      userOptions: { includePremiumSubscriptionStatus: true },
    });

    onNavigateSidebar({
      panel: PanelName.Progress,
      slide: SlideName.ProgressOverview,
    });
  };

  return (
    <Box
      display="flex"
      height="100%"
      flexDirection="column"
      css={css`
        height: 100%;
        p,
        h5 {
          color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
        }
      `}
    >
      <Box flex="1">
        <Header title={name} loading={loading} error={!!error} subtitle="" />
        {(() => {
          if (loading) {
            return <Placeholder />;
          }
          if (error) {
            const errorId = getErrorId(error);
            return (
              <Box pt={5} px={3}>
                <SidebarErrorMessage message={error.message} errorId={errorId} />
              </Box>
            );
          }

          return (
            <>
              <Box
                css={css`
                  height: 104px;
                  width: 100%;
                  overflow: hidden;
                `}
              >
                <Image
                  src={imageUrl}
                  alt={imageAlt}
                  css={css`
                    object-fit: cover;
                  `}
                />
              </Box>
              <SidebarSection display="flex" flexDirection="column" gap={1}>
                <Typography variant="sm" isBold>
                  {title}
                </Typography>
                <Typography variant="sm">{details}</Typography>
                {isModalEnabled && !isInPopup && (
                  <ExtraPracticeExplanationTrigger triggerText={t('learnAboutExtraPractice')} />
                )}
              </SidebarSection>
              {topics.map(({ topicId, name: topicName, possibleTopicPhraseCount, completedTopicPhraseCount }) => (
                <TopicItem
                  key={topicId}
                  topicId={topicId}
                  label={topicName}
                  asideLabel={`${completedTopicPhraseCount}/${possibleTopicPhraseCount}`}
                  onClick={() => onNavigateToTopic(topicId)}
                />
              ))}
            </>
          );
        })()}
      </Box>
      {!loading && !error && (
        <FadeIn
          show
          p={2}
          textAlign="center"
          boxShadow="0px -4px 12px rgba(22, 22, 22, 0.04);"
          css={css`
            position: sticky;
            bottom: 0;
            background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
          `}
        >
          {isActive ? (
            <Typography variant="xs" isBold>
              {t('thisIsCurrentFocus')}
            </Typography>
          ) : (
            <Button
              type="utility"
              css={css`
                width: 100%;
              `}
              onClick={onActivateShortcut}
            >
              {activatingShortcut ? (
                <CircularProgress sx={{ color: ToucanColors.white }} size={16} />
              ) : (
                t('takeThisShortcut')
              )}
            </Button>
          )}
        </FadeIn>
      )}
    </Box>
  );
};
