/** @jsx jsx */
import React, { FC, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import browser from 'webextension-polyfill';

import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { Image } from '~/components/shared/Image';
import { MessageKey } from '~/constants/messages';
import { EmailAuthType } from '~/constants/user';
import { useExternalAuth } from '~/hooks/useExternalAuth';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import { AuthFlow } from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

import Config from '../../../configs/config';
import { useAnonymousUserSettings } from '../AnonymousUserExtensionSettingsContext';
import { ForgotPassword } from '../forms/ForgotPassword';
import { Login } from '../forms/Login';
import { SignUp } from '../forms/SignUp';

import { ButtonSelectionFooter } from './ButtonSelectionFooter';

const { Button, useColorScheme } = ToucanComponents;

interface GoogleAuthButtonProps {
  authFlow: AuthFlow;
  children: React.ReactNode;
}
const GoogleAuthButton = ({ children, authFlow, ...props }: GoogleAuthButtonProps) => {
  const { signInWithGoogle } = useExternalAuth();
  const theme = useTheme();
  return (
    <Button
      {...props}
      type="primary"
      size="medium"
      onClick={() => {
        browser.runtime.sendMessage({ msg: MessageKey.ClearUserCache });
        signInWithGoogle(authFlow);
      }}
      css={css`
        width: inherit;
        margin-bottom: ${theme.spacing(1)};
      `}
    >
      <Image
        src="/images/google.svg"
        alt="google logo"
        width="14px"
        css={css`
          margin-right: ${theme.spacing(2)};
        `}
      />
      {children}
    </Button>
  );
};

interface AppleAuthButtonProps {
  authFlow: AuthFlow;
  children: React.ReactNode;
}
const AppleAuthButton = ({ children, authFlow, ...props }: AppleAuthButtonProps) => {
  const { signInWithApple } = useExternalAuth();
  const theme = useTheme();
  return (
    <Button
      {...props}
      type="primary"
      size="medium"
      onClick={() => {
        browser.runtime.sendMessage({ msg: MessageKey.ClearUserCache });
        signInWithApple(authFlow);
      }}
      css={css`
        width: inherit;
        margin-bottom: ${theme.spacing(1)};
        background-color: ${ToucanColors.gray[600]};
        &:hover {
          background-color: ${ToucanColors.gray[400]};
        }
        &:active {
          background-color: ${ToucanColors.gray[600]};
          box-shadow: ${ToucanColors.gray[300]} 0px 0px 0px 2px inset;
        }
      `}
    >
      <Image
        src="/images/apple.svg"
        alt="apple logo"
        width="14px"
        css={css`
          margin-right: ${theme.spacing(2)};
        `}
      />
      {children}
    </Button>
  );
};

interface ButtonsProps {
  onEmailClick: (type: EmailAuthType) => void;
}

const ButtonsSignUp: FC<ButtonsProps> = ({ onEmailClick }) => {
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();

  return (
    <div
      css={css`
        width: 100%;
      `}
    >
      <GoogleAuthButton data-testid="sign-up-with-google-button" authFlow={AuthFlow.SignUpFlow}>
        {t('continueWithGoogle')}
      </GoogleAuthButton>
      <AppleAuthButton data-testid="sign-up-with-apple-button" authFlow={AuthFlow.SignUpFlow}>
        {t('continueWithApple')}
      </AppleAuthButton>
      <Button
        data-testid="sign-up-with-email-button"
        type="secondary"
        size="medium"
        isReversed={isDarkMode}
        onClick={() => onEmailClick(EmailAuthType.SignUp)}
        css={css`
          width: inherit;
        `}
      >
        {t('continueWithEmail')}
      </Button>
      <ButtonSelectionFooter />
    </div>
  );
};

const ButtonsLogIn: FC<ButtonsProps> = ({ onEmailClick }) => {
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();

  return (
    <div
      css={css`
        width: 100%;
      `}
    >
      <GoogleAuthButton data-testid="log-in-with-google-button" authFlow={AuthFlow.LoginFlow}>
        {t('logInWithGoogle')}
      </GoogleAuthButton>
      <AppleAuthButton data-testid="log-in-with-apple-button" authFlow={AuthFlow.LoginFlow}>
        {t('logInWithApple')}
      </AppleAuthButton>
      <Button
        data-testid="log-in-with-email-button"
        type="secondary"
        size="medium"
        isReversed={isDarkMode}
        onClick={() => onEmailClick(EmailAuthType.LogIn)}
        css={css`
          width: inherit;
        `}
      >
        {t('logInWithEmail')}
      </Button>
      <ButtonSelectionFooter />
    </div>
  );
};

interface SignUpSignInButtonsProps {
  refetch: () => Promise<void>;
}

export const SignUpSignInButtons = ({ refetch }: SignUpSignInButtonsProps) => {
  const { isNewSignUp, isEmailSignupSelected, isResetSelected } = useAnonymousUserSettings();
  const { email } = useUserInfo();
  const { onNavigateSidebar } = useSidebarContext();

  const onEmailClick = (type: EmailAuthType) => {
    // NOTE: in lieu of processing users that select email sign-up/log-in within the sidebar we will now have them complete this
    // process directly on the website. we are using query params to help go beyond the initial sign-up/log-in flow where they are
    // asked what method to sign-in/log-in with (email, google, apple) and directly into inputting fields. if users were to sign-up/log-in
    // directly within the sidebar, they would become anonymous the moment they redirect to the website when selecting a subscription plan.
    window.open(`${Config.siteURL}/${type}?method=email`, '_blank');
  };

  const redirectUser = async () => {
    await refetch();
    onNavigateSidebar({ panel: PanelName.Settings, slide: SlideName.MainSettings });
  };

  useEffect(() => {
    // If email is registered, redirect to settings
    if (email) {
      redirectUser();
    }
  }, [email]);

  switch (isEmailSignupSelected) {
    case false:
      return isNewSignUp ? <ButtonsSignUp onEmailClick={onEmailClick} /> : <ButtonsLogIn onEmailClick={onEmailClick} />;
    default:
      if (isNewSignUp && !isResetSelected) {
        return (
          <div
            css={css`
              width: 100%;
            `}
          >
            <SignUp />
          </div>
        );
      }
      if (!isNewSignUp && !isResetSelected) {
        return (
          <div
            css={css`
              width: 100%;
            `}
          >
            <Login />
          </div>
        );
      }
      return (
        <div
          css={css`
            width: 100%;
          `}
        >
          <ForgotPassword />
        </div>
      );
  }
};
