/** @jsx jsx */
import React from 'react';

import { css, jsx } from '@emotion/react';
import { useTheme } from '@mui/material';

import { SectionItemSeparator } from '~/components/Sidebar/SectionItemSeparator';
import { Title } from '~/components/Sidebar/Title';
import { useTranslation } from '~/libs/i18n';
import { useIsNotificationFeedbackShownQuery } from '~/schema/background';

import { Feedback } from './Feedback';

export const Header = () => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { data } = useIsNotificationFeedbackShownQuery();
  const isFeedbackShown = data?.settings?.isNotificationFeedbackShown ?? false;

  return (
    <React.Fragment>
      <div
        data-testid="notification-header"
        css={css`
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding-left: ${theme.spacing(2)};
          padding-right: ${theme.spacing(2)};
        `}
      >
        <Title>{t('toucanTips')}</Title>
      </div>
      {isFeedbackShown && (
        <React.Fragment>
          <SectionItemSeparator />
          <Feedback />
        </React.Fragment>
      )}
    </React.Fragment>
  );
};
