import React, { useEffect } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { Language } from '~/constants/language';
import { GLOBAL_PERMISSION } from '~/constants/permissions';
import { useHostPermissions } from '~/hooks/useHostPermissions';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import {
  ProgressionPeriod,
  ProgressionType,
  useChangeGloballyEnabledStateMutation,
  useGetProgressQuery,
} from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

import { ProgressOverview } from './ProgressOverview';

interface ProgressOverviewContainerProps {
  period: ProgressionPeriod;
  targetLanguage: Language;
  onItemClicked: (progressionType: ProgressionType) => void;
}

const { Typography } = ToucanComponents;

export const ProgressOverviewContainer: React.FC<ProgressOverviewContainerProps> = ({
  period,
  targetLanguage,
  onItemClicked,
}) => {
  const { data, error, loading, refetch } = useGetProgressQuery({ variables: { period } });
  const { canRequestPermissions, requestPermissions, hasPermissions } = useHostPermissions({
    hosts: [GLOBAL_PERMISSION],
  });
  const [changeEnabledState] = useChangeGloballyEnabledStateMutation();
  const { onNavigateSidebar } = useSidebarContext();
  const { t } = useTranslation();

  const backgroundUrl = browser.runtime.getURL(`images/progress/landmarkBG-${targetLanguage}.svg`);
  const topPercent = data?.screens?.progressionOverview?.topPercent ?? 100;
  const progressionItems = (data?.screens?.progressionOverview?.progressionItems ?? [])
    .filter(({ amount }) => amount > 0)
    .map(progressionItem => ({ ...progressionItem, progressionType: progressionItem.type }));
  const densityLevel = data?.settings?.densityLevel ?? 0;
  const isGloballyEnabled = data?.settings?.isGloballyEnabled ?? false;
  const errorId = (error && 'extensions' in error && error.extensions?.errorId) ?? 'toucan-41834534';

  // This logic is tricky here because if the users has the ability to request permissions
  // we show this, if not we want to hide it.
  const hasGlobalPermissions = canRequestPermissions ? hasPermissions ?? false : true;

  useEffect(() => {
    contentMessengers.trackEvent({ eventName: 'Progress overview viewed', properties: { timeSpan: period } });
  }, [period]);

  return (
    <Box
      m={2}
      flex="1"
      bgcolor={ToucanColors.gray[400]}
      borderRadius="8px"
      color={ToucanColors.white}
      css={css`
        background-image: url(${backgroundUrl});
        background-size: cover;
        background-repeat: no-repeat;
        background-position: bottom center;
      `}
    >
      {(() => {
        if (error) {
          return (
            <Box p={2}>
              <Box p={2} color={ToucanColors.red[100]}>
                <Typography variant="xs" color={ToucanColors.red[400]}>
                  {error.message}
                </Typography>
                <Typography variant="xs" color={ToucanColors.red[400]}>
                  {t('errorId', { errorId })}
                </Typography>
              </Box>
            </Box>
          );
        }
        if (loading) {
          return <Box p={2} />;
        }
        return (
          <Box p={3}>
            <ProgressOverview
              key={period}
              period={period}
              topPercentage={topPercent}
              targetLanguage={targetLanguage}
              progressionItems={progressionItems}
              difficultyLevel={densityLevel}
              isGloballyEnabled={isGloballyEnabled}
              hasGlobalPermissions={hasGlobalPermissions}
              onRequestPermissions={async () => {
                const isGranted = await requestPermissions();
                contentMessengers.trackEvent({
                  eventName: `Progression Permission ${isGranted ? 'Granted' : 'Denied'}`,
                  properties: {},
                });
                await refetch();
                return isGranted;
              }}
              onEnableGlobally={async () => {
                await changeEnabledState({ enabled: true });
                contentMessengers.trackEvent({
                  eventName: `Progression Enable Globally`,
                  properties: {},
                });
                await refetch();
              }}
              onChangeDifficulty={async () => {
                contentMessengers.trackEvent({
                  eventName: 'Progression Change Difficulty',
                  properties: {},
                });
                onNavigateSidebar({
                  panel: PanelName.Settings,
                  slide: SlideName.MainSettings,
                  overlay: null,
                });
              }}
              onItemClicked={onItemClicked}
            />
          </Box>
        );
      })()}
    </Box>
  );
};
