/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';

const { useColorScheme, Typography, ToucanIcon } = ToucanComponents;

interface NoSavedWordsProps {
  isDisabled?: boolean;
}

export const NoSavedWords = ({ isDisabled }: NoSavedWordsProps) => {
  const { scheme, textLevel1, textLevel2 } = useColorScheme();
  const { t } = useTranslation();

  return (
    <Box
      display="flex"
      height="100%"
      overflow="hidden"
      flexDirection="column"
      p={3}
      textAlign="center"
      alignItems="center"
      data-testid="no-saved-words"
    >
      <Image
        src={`/images/sidebar/savedWords/emptyNest${isDisabled ? `-disabled-${scheme}` : ''}.svg`}
        alt={t('toucanLookingAtMap')}
        width="120px"
        height="120px"
      />
      <Box pt={3} pb={0.5}>
        <Typography variant="sm" isBold color={textLevel1}>
          {t('thisNestLooksEmpty')}
        </Typography>
      </Box>
      {isDisabled ? (
        <Typography variant="xs" color={textLevel2}>
          {t('youDontHaveSavedWordsYet')}
        </Typography>
      ) : (
        <Box
          maxWidth="266px"
          css={css`
            svg {
              width: 14px;
              height: 14px;
              vertical-align: middle;
            }
            path {
              fill: ${textLevel2};
            }
          `}
        >
          <Typography variant="xs" color={textLevel2}>
            <Trans i18nKey="clickTheBookmarkIconOnWords" components={[<ToucanIcon icon="bookmark-outline" />]} />
          </Typography>
        </Box>
      )}
    </Box>
  );
};
