/** @jsx jsx */
import { FC, useEffect, useLayoutEffect, useRef, useState } from 'react';

import { jsx, css } from '@emotion/react';
import { CellMeasurer, CellMeasurerCache, List, ListRowRenderer } from 'react-virtualized';

import { Box } from '~/components/Box';
import { Overlay } from '~/components/Sidebar/Overlay';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { ErrorMessage } from '~/components/shared/ErrorMessage';
import { FadeIn } from '~/components/shared/FadeIn';
import { Events, EventSources } from '~/constants/events';
import { Language } from '~/constants/language';
import { Pinchpoints } from '~/constants/pinchpoints';
import { getErrorId } from '~/helpers/errors';
import { useAddDictionaryEntry } from '~/hooks/useDictionary';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { usePrevious } from '~/hooks/usePrevious';
import { useQuiz } from '~/hooks/useQuiz';
import { useSubscription } from '~/hooks/useSubscription';
import contentMessengers from '~/messengers/contentMessengers';
import {
  ProgressionPeriod,
  ProgressionTranslation,
  ProgressionType,
  QuizType,
  useGetWordListScreenQuery,
  WordListSort,
} from '~/schema/background';
import { OverlayName } from '~/types/sidebar';

import { NoActivity } from './NoActivity';
import { WordListCard } from './WordListCard';
import { WordListPlaceholder } from './WordListPlaceholder';
import { WordQuiz } from './WordQuiz';

interface WordListContainerProps {
  progressionType: ProgressionType;
  sort: WordListSort;
  period: ProgressionPeriod;
  quizInfo: { translationIds: Array<string>; quizType: QuizType } | null;
  onProgressionTypeChange: (progressionType: ProgressionType) => void;
  onQuizEnter: (translationId: string, quizType: QuizType) => void;
  onQuizExit: () => void;
}

type WordList = Pick<
  ProgressionTranslation,
  | 'translationId'
  | 'sourcePhrase'
  | 'targetPhrase'
  | 'isSaved'
  | 'isKnown'
  | 'isFluent'
  | 'progressionScore'
  | 'targetLanguage'
  | 'practicedToday'
  | 'fluentAt'
  | 'difficultyLevel'
>[];

const cache = new CellMeasurerCache({
  // 138 card + 16 margin
  defaultHeight: 154,
  fixedWidth: true,
});

const wordListRenderer: (options: {
  wordList: WordList;
  refetch: () => Promise<void>;
  onQuizEnter: (translationId: string, quizType: QuizType) => void;
  openSubscribePage: (options: { pinchpoint: Pinchpoints }) => void;
  isPremiumSubscribed: boolean;
  closeSidebar: () => void;
  languageKey: Language | undefined;
}) => ListRowRenderer = ({ wordList, refetch, onQuizEnter, openSubscribePage, isPremiumSubscribed, languageKey }) => ({
  index,
  style,
  key,
  parent,
}) => (
  <CellMeasurer cache={cache} columnIndex={0} key={key} parent={parent} rowIndex={index}>
    <WordListCard
      refetch={refetch}
      style={style}
      onQuizEnter={onQuizEnter}
      isPremiumSubscribed={isPremiumSubscribed}
      openSubscribePage={openSubscribePage}
      {...wordList[index]}
      key={wordList[index].translationId}
      languageKey={languageKey}
    />
  </CellMeasurer>
);

export const WordListContainer: FC<WordListContainerProps> = ({
  progressionType,
  period,
  onQuizEnter,
  quizInfo,
  onQuizExit,
  sort,
}) => {
  const { onNavigateSidebar } = useSidebarContext();
  const { data, loading, error, refetch } = useGetWordListScreenQuery({
    variables: { input: { type: progressionType, period, sort } },
  });
  const addTranslations = useAddDictionaryEntry();
  const { openSubscribePage, isPremiumSubscribed } = useSubscription();
  const parentRef = useRef<HTMLDivElement>(null);
  const [rect, setRect] = useState<DOMRect | null>(null);
  const pastQuiz = usePrevious(quizInfo);
  const { currentQuiz } = useQuiz();
  const { closeSidebar } = useSidebar();
  const { targetLanguageInfo } = useLanguageInfo();
  const languageKey = targetLanguageInfo?.key;

  useEffect(() => {
    contentMessengers.trackEvent({
      eventName: Events.WordListViewed,
      properties: { timeSpan: period, source: EventSources.Sidebar },
    });
  }, []);

  const openQuiz = (translationId: string, quizType: QuizType) => {
    const entry = data?.screens.wordList?.translations.find(({ translationId: id }) => id === translationId);

    if (entry) {
      // Needed for Quizzes to work
      // base on usage of `useDictionary`
      addTranslations({
        [translationId]: {
          translationId,
          sourcePhrase: entry.sourcePhrase,
          targetPhrase: entry.targetPhrase,
        },
      });
    }

    onQuizEnter(translationId, quizType);
    onNavigateSidebar({ overlay: OverlayName.WordQuiz });
  };

  useEffect(() => {
    // Refetch navigate when there was a quiz but is not longer one.
    if (pastQuiz && !quizInfo) {
      onNavigateSidebar({ overlay: null });
      refetch();
    }
  }, [quizInfo, pastQuiz]);

  useLayoutEffect(() => {
    if (parentRef.current) {
      const parentRect = parentRef.current.getBoundingClientRect();
      setRect(parentRect);
    }
  }, []);

  useEffect(
    () => () => {
      cache.clearAll();
    },
    [],
  );

  return (
    <Box width="100%" flex="1" ref={parentRef} height="100%">
      {(() => {
        // Render null here to avoid extended height from loader
        if (!rect) {
          return <Box data-testid="wordList-loading" />;
        }
        if (loading) {
          return <WordListPlaceholder />;
        }
        if (error) {
          const errorId = getErrorId(error);
          return (
            <Box p={2}>
              <ErrorMessage message={error.message} errorId={errorId} />
            </Box>
          );
        }

        const wordList = data?.screens.wordList?.translations ?? [];

        if (!wordList.length) {
          return <NoActivity />;
        }

        return (
          <FadeIn
            show
            css={css`
              height: 100%;
              flex: 1;
            `}
          >
            <List
              deferredMeasurementCache={cache}
              height={rect.height || 300}
              width={rect.width || 300}
              rowHeight={cache.rowHeight}
              rowCount={wordList.length}
              rowRenderer={wordListRenderer({
                wordList,
                refetch,
                onQuizEnter: openQuiz,
                isPremiumSubscribed,
                openSubscribePage,
                closeSidebar,
                languageKey,
              })}
              scrollToAlignment="start"
            />
          </FadeIn>
        );
      })()}
      <Overlay overlayName={OverlayName.WordQuiz}>
        {quizInfo && (
          <WordQuiz
            key={quizInfo.translationIds.join(':')}
            onQuizExit={onQuizExit}
            quizStatus={currentQuiz.status}
            {...quizInfo}
          />
        )}
      </Overlay>
    </Box>
  );
};
