/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';

import { Box, BoxProps } from '~/components/Box';

const { useColorScheme } = ToucanComponents;

interface LoadingBoxProps extends BoxProps {
  width?: number | string;
  height?: number | string;
}

export const LoadingBox: FC<LoadingBoxProps> = ({ width, height, ...boxProps }) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();

  return (
    <Box width={width} height={height} {...boxProps}>
      <Skeleton
        variant="rectangular"
        width={width}
        height={height}
        css={css`
          border-radius: ${theme.spacing(0.5)};
          &.MuiSkeleton-root {
            background-color: ${isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[100]};
          }
        `}
      />
    </Box>
  );
};
