import React, { FC } from 'react';

import { css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import Configs from '~/configs/config';
import { EventSources } from '~/constants/events';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';

import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, Switch, useColorScheme, Link, ToucanIcon } = ToucanComponents;

interface DataConsentProps {
  hasDataConsent: boolean;
  onChangeDataConsent: (value: boolean) => Promise<void>;
}

export const DataConsent: FC<DataConsentProps> = ({ onChangeDataConsent, hasDataConsent }) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { textLevel1 } = useColorScheme();

  return (
    <SidebarItemSeparated
      alignContent="center"
      rightContentAlignItems="center"
      leftContent={
        <Box
          css={css`
            p,
            h5 {
              color: ${textLevel1};
            }
            svg {
              color: inherit;
              width: 12px;
              height: 12px;
              margin-left: ${theme.spacing(1)};
            }
          `}
        >
          <Typography variant="sm" isBold>
            {t('helpImproveToucan')}
          </Typography>
          <Typography variant="xs">{t('shareDeviceAndUsage')}</Typography>
          <Link href={`${Configs.siteURL}/privacy`} target="_blank">
            {t('privacyPolicy')} <ToucanIcon icon="link-out" />
          </Link>
        </Box>
      }
      rightContent={
        <Box
          display="flex"
          flex="1"
          justifyContent="center"
          css={css`
            label > span {
              margin-right: 0px;
            }
          `}
        >
          <Switch
            value="switch"
            isChecked={hasDataConsent}
            onChange={async () => {
              await onChangeDataConsent(!hasDataConsent);
              ContentMessengers.trackEvent({
                eventName: 'Data consent toggle',
                properties: {
                  enabled: !hasDataConsent,
                  source: EventSources.Sidebar,
                },
              });
            }}
          />
        </Box>
      }
    />
  );
};
