/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { Language } from '~/constants/language';
import { getLanguageInfo } from '~/helpers/languageHelpers';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';

import { useAnonymousUserSettings } from '../AnonymousUserExtensionSettingsContext';

const { Typography, Button, useColorScheme } = ToucanComponents;

interface HeadingProps {
  isNewSignUp: boolean;
  isResetSelected: boolean;
  isEmailSignupSelected: boolean;
}

const HeadingSelection = ({ isNewSignUp, isResetSelected, isEmailSignupSelected }: HeadingProps) => {
  const { t } = useTranslation();
  if (isNewSignUp && !isResetSelected && !isEmailSignupSelected) {
    return <span>{t('turnOnToucan')}</span>;
  }
  if (isNewSignUp && isEmailSignupSelected) {
    return <span data-testid="sign-up-email-header">{t('turnOnToucan')}</span>;
  }
  if (!isNewSignUp && !isResetSelected && !isEmailSignupSelected) {
    return <span data-testid="welcome-header">{t('welcomeBack')}</span>;
  }
  if (!isNewSignUp && isEmailSignupSelected) {
    return <span>{t('logInWithEmail')}</span>;
  }
  return (
    <span data-testid="reset-password-header">
      <Trans i18nKey="resetYourPassword" components={[<br />]} />
    </span>
  );
};

const SubHeadingSelection = ({
  isNewSignUp,
  isResetSelected,
  onSignInClick,
  isEmailSignupSelected,
}: HeadingProps & { onSignInClick: () => void }) => {
  const { t } = useTranslation();
  const languageInfo = getLanguageInfo(t);
  const { targetLanguageInfo } = useLanguageInfo();

  if (isNewSignUp && !isResetSelected && !isEmailSignupSelected) {
    return (
      <span>
        {t('youNeedToCreateAnAccountToContinue', {
          language: (targetLanguageInfo ?? languageInfo[Language.Spanish]).language,
        })}
      </span>
    );
  }
  if (isNewSignUp && isEmailSignupSelected) {
    return (
      <span>
        {t('youNeedToCreateAnAccountToContinue', {
          language: (targetLanguageInfo ?? languageInfo[Language.Spanish]).language,
        })}
      </span>
    );
  }
  if (!isNewSignUp && !isResetSelected) {
    return <span>{t('logIntoYourAccountToContinue')}</span>;
  }
  return (
    <span>
      <Trans
        i18nKey="pleaseEnterYourEmailAndWellSend"
        components={[
          <br />,
          <br />,
          <Button
            type="text"
            onClick={onSignInClick}
            css={css`
              text-decoration: none !important;
              display: initial;
              vertical-align: initial;
              font-size: 14px;
            `}
          >
            a
          </Button>,
        ]}
      />
    </span>
  );
};

export const UnregisteredUserHeading = () => {
  const theme = useTheme();
  const {
    isNewSignUp,
    isResetSelected,
    isEmailSignupSelected,
    setIsResetSelected,
    setIsNewSignUp,
    setIsEmailSignupSelected,
  } = useAnonymousUserSettings();
  const { textLevel1, textLevel2 } = useColorScheme();

  const onSignInClick = () => {
    setIsNewSignUp(true);
    setIsResetSelected(false);
    setIsEmailSignupSelected(false);
  };

  return (
    <Box display="flex" flexDirection="column" alignItems="center" mb={4} textAlign="center">
      <Typography
        variant="lg"
        font="secondary"
        css={css`
          overflow: visible;
          margin-bottom: ${theme.spacing(2)};
        `}
        color={textLevel1}
      >
        <HeadingSelection
          isNewSignUp={isNewSignUp}
          isResetSelected={isResetSelected}
          isEmailSignupSelected={isEmailSignupSelected}
        />
      </Typography>
      <Typography
        variant="sm"
        css={css`
          overflow: visible;
          text-align: center;
        `}
        color={textLevel2}
      >
        <SubHeadingSelection
          onSignInClick={onSignInClick}
          isNewSignUp={isNewSignUp}
          isResetSelected={isResetSelected}
          isEmailSignupSelected={isEmailSignupSelected}
        />
      </Typography>
    </Box>
  );
};
