import React, { FC } from 'react';

import { SettingName } from '~/types/sidebar';

import { useSidebarContext } from './SidebarContext';

interface SettingProps {
  name: SettingName;
  children: React.ReactNode;
}

/**
 * Container for setting content
 * It is meant to show content which can potentially be shown for that setting
 * @param props
 * @param props.name String that is used to determine if the setting should be visible
 */
export const Setting: FC<SettingProps> = ({ children, name }) => {
  const { supportedSettings } = useSidebarContext();
  return supportedSettings.includes(name) ? <>{children}</> : null;
};
