/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { get } from 'lodash';
import { useForm, useFieldArray } from 'react-hook-form';

import { Box, BoxProps } from '~/components/Box';
import { email as emailRegex } from '~/constants/regex';
import { useTranslation } from '~/libs/i18n';

const { useColorScheme, Button, TextField, ToucanIcon, TransparentButton } = ToucanComponents;

interface EmailReferralFormProps extends BoxProps {
  onSubmitForm: (emails: string[]) => Promise<void>;
}

type FormValues = {
  emails: { email: string }[];
};

export const EmailReferralForm: FC<EmailReferralFormProps> = ({ onSubmitForm }) => {
  const { t } = useTranslation();
  const {
    register,
    handleSubmit,
    control,
    formState: { errors },
    reset,
  } = useForm<FormValues>({
    defaultValues: {
      emails: [{}],
    },
  });

  const { fields, append, remove } = useFieldArray({
    control,
    name: 'emails',
  });
  const { isDarkMode } = useColorScheme();

  const onSubmit = async (inputs: FormValues) => {
    const emailsToSubmit = inputs.emails.map(({ email }) => email).filter(email => email.length > 0);
    await onSubmitForm(emailsToSubmit);

    // Reset the form after the emails were sent successfully
    reset();
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      {fields.map((field, index) => {
        const errorMessage = get(errors, `emails[${index}].email.message`, null);
        const hasError = !!errorMessage;

        return (
          <Box key={`emails.${index}`} mt={index !== 0 ? 1 : 0}>
            <Box display="flex" alignItems="center">
              <TextField
                label={t('emailAddress')}
                hasError={hasError}
                helperText={errorMessage}
                type="email"
                inputProps={{
                  tabIndex: index + 1,
                }}
                {...register(`emails.${index}.email`, {
                  required: index === 0 ? `${t('pleaseEnterValidEmail')}` : false,
                  pattern: {
                    value: emailRegex,
                    message: t('emailDoesntLookRight'),
                  },
                })}
                isDarkMode={isDarkMode}
              />
              {fields.length > 1 && (
                <TransparentButton onClick={() => remove(index)}>
                  <Box
                    component={ToucanIcon}
                    // @ts-ignore: icon comes from ToucanIcon component
                    icon="times"
                    color={isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[300]}
                    width={14}
                    height={14}
                    ml={2}
                  />
                </TransparentButton>
              )}
            </Box>
          </Box>
        );
      })}

      {fields.length < 5 && (
        <Box mt={2}>
          <Button type="text" onClick={() => append({})}>
            + {t('addAnother')}
          </Button>
        </Box>
      )}
      <Box mt={2}>
        <Button
          size="medium"
          type="utility"
          onClick={handleSubmit(onSubmit)}
          css={css`
            width: 100%;
          `}
        >
          {t('done')}
        </Button>
      </Box>
    </form>
  );
};
