import * as queryString from 'query-string';

import Config, { browserVendor } from '~/configs/config';
import { AuthFlow } from '~/schema/background';
import { ExternalAuthProvider } from '~/schema/server';
import { BrowserVendors } from '~/types/browsers';

const externalAuthInfo = {
  [ExternalAuthProvider.Google]: {
    redirectUri: Config.GOOGLE_AUTH_REDIRECT_URI,
  },
  [ExternalAuthProvider.Apple]: {
    redirectUri: Config.APPLE_AUTH_REDIRECT_URI,
  },
};

export const useExternalAuth = () => {
  const signInWithGoogle = (authFlow: AuthFlow) => {
    const params = {
      client_id: Config.GOOGLE_AUTH_CLIENT_ID,
      redirect_uri: externalAuthInfo[ExternalAuthProvider.Google].redirectUri,
      scope: [
        'https://www.googleapis.com/auth/userinfo.email',
        'https://www.googleapis.com/auth/userinfo.profile',
        'openid',
      ].join(' '),
      response_type: 'code',
      access_type: 'offline',
      prompt: 'consent',
      state: queryString.stringify({
        isAuthFromExtension: true,
        authFlow,
      }),
    };

    const loginUrl = `https://accounts.google.com/o/oauth2/v2/auth?${queryString.stringify(params)}`;
    window.open(loginUrl, '_blank', `scrollbars=yes, width=520, height=570`);
  };

  const signInWithApple = (authFlow: AuthFlow) => {
    const params = {
      client_id: Config.APPLE_AUTH_CLIENT_ID,
      redirect_uri: `${externalAuthInfo[ExternalAuthProvider.Apple].redirectUri}`,
      scope: 'name email',
      response_type: 'code',
      response_mode: 'form_post',
      state: queryString.stringify({
        isAuthFromExtension: true,
        authFlow,
      }),
    };

    let loginUrl = `https://appleid.apple.com/auth/authorize?${queryString.stringify(params)}`;

    // Apple sign in with face id doesn't hit the redirect_uri so we can't authenticate
    // Temporarily open the sign in page on the website instead
    // TODO: Fix in https://jointoucan.atlassian.net/browse/GROWTH-1396
    if (browserVendor === BrowserVendors.SafariiOS) {
      loginUrl = `${Config.siteURL}/${authFlow === AuthFlow.LoginFlow ? 'log-in' : 'sign-up'}`;
    }

    window.open(loginUrl, '_blank', `scrollbars=yes, width=520, height=570`);
  };

  return {
    signInWithGoogle,
    signInWithApple,
  };
};
