/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';

import { useAnonymousUserSettings } from '../AnonymousUserExtensionSettingsContext';

const { Typography, Button } = ToucanComponents;

interface HeadingProps {
  isNewSignUp: boolean;
  isResetSelected: boolean;
  isEmailSignupSelected: boolean;
}

const HeadingSelection: FC<HeadingProps> = ({ isNewSignUp, isResetSelected, isEmailSignupSelected }) => {
  const { t } = useTranslation();
  if (isNewSignUp && !isResetSelected && !isEmailSignupSelected) {
    return (
      <span data-testid="sign-up-header">
        <Trans i18nKey="startLearningForFree" components={[<br />]} />
      </span>
    );
  }
  if (isNewSignUp && isEmailSignupSelected) {
    return <span data-testid="sign-up-email-header">{t('sharedGetMoreForFree')}</span>;
  }
  if (!isNewSignUp && !isResetSelected) {
    return <span data-testid="welcome-header">{t('welcomeBack')}</span>;
  }
  return (
    <span data-testid="reset-password-header">
      <Trans i18nKey="resetYourPassword" components={[<br />]} />
    </span>
  );
};

const SubHeadingSelection: FC<HeadingProps & { onSignInClick: () => void }> = ({
  isNewSignUp,
  isResetSelected,
  onSignInClick,
  isEmailSignupSelected,
}) => {
  const { t } = useTranslation();
  if (isNewSignUp && !isResetSelected && !isEmailSignupSelected) {
    return (
      <span>
        <Trans i18nKey="createAnAccountToUnlock" components={[<strong>a</strong>]} />
      </span>
    );
  }
  if (isNewSignUp && isEmailSignupSelected) {
    return (
      <span>
        <Trans i18nKey="unlockMoreAndPersonalize" components={[<strong>a</strong>]} />
      </span>
    );
  }
  if (!isNewSignUp && !isResetSelected) {
    return <span>{t('logIntoYourAccountToContinue')}</span>;
  }
  return (
    <span>
      <Trans
        i18nKey="forgotPasswordEnterEmail"
        components={[
          <br />,
          <br />,
          <Button
            type="text"
            onClick={onSignInClick}
            css={css`
              text-decoration: none !important;
              display: initial;
              vertical-align: initial;
              font-size: 14px;
            `}
          >
            a
          </Button>,
        ]}
      />
    </span>
  );
};

export const AccountValueProps = () => {
  const theme = useTheme();
  const { t } = useTranslation();
  const {
    isDarkModeEnabled,
    isNewSignUp,
    isResetSelected,
    isEmailSignupSelected,
    setIsResetSelected,
    setIsNewSignUp,
    setIsEmailSignupSelected,
  } = useAnonymousUserSettings();

  const onSignInClick = () => {
    setIsNewSignUp(true);
    setIsResetSelected(false);
    setIsEmailSignupSelected(false);
  };

  return (
    <div
      css={css`
        margin-bottom: ${theme.spacing(4)};
      `}
    >
      <Image
        src="/images/lamp-with-laptop.svg"
        alt={t('lampWithLaptop')}
        css={css`
          width: 56px;
          margin-bottom: ${theme.spacing(2)};
        `}
      />
      <Typography
        variant="lg"
        font="secondary"
        css={css`
          color: ${isDarkModeEnabled ? ToucanColors.white : ToucanColors.gray[500]};
          overflow: visible;
          margin-bottom: ${theme.spacing(2)};
        `}
      >
        <HeadingSelection
          isNewSignUp={isNewSignUp}
          isResetSelected={isResetSelected}
          isEmailSignupSelected={isEmailSignupSelected}
        />
      </Typography>
      <Typography
        variant="sm"
        css={css`
          color: ${isDarkModeEnabled ? ToucanColors.gray[100] : ToucanColors.gray[400]};
          overflow: visible;
        `}
      >
        <SubHeadingSelection
          onSignInClick={onSignInClick}
          isNewSignUp={isNewSignUp}
          isResetSelected={isResetSelected}
          isEmailSignupSelected={isEmailSignupSelected}
        />
      </Typography>
    </div>
  );
};
