import { useState } from 'react';

import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { browserVendor } from '~/configs/config';
import { GLOBAL_PERMISSION } from '~/constants/permissions';
import { Pinchpoints } from '~/constants/pinchpoints';
import { useHostPermissions } from '~/hooks/useHostPermissions';
import { useSubscription } from '~/hooks/useSubscription';
import { ChecklistOnClick } from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';
import { PanelName, SlideName } from '~/types/sidebar';

export const useOnboarding = (refetch: () => void) => {
  const { onNavigateSidebar } = useSidebarContext();
  const { openSubscribePage } = useSubscription();
  const { requestPermissions } = useHostPermissions({
    hosts: [GLOBAL_PERMISSION],
    globalSubstitute: browserVendor === BrowserVendors.Safari,
  });
  const [isExpanded, setIsExpanded] = useState(false);

  const onOpenSidebarSignUpClick = () => {
    onNavigateSidebar({ panel: PanelName.Settings, slide: SlideName.Authenticate });
  };

  const onRequestHostPermissionsClick = async () => {
    await requestPermissions();
    refetch();
  };

  const onOpenSubscriptionPageClick = () => {
    openSubscribePage({ pinchpoint: Pinchpoints.OnboardingChecklistUpdated });
  };

  const onShowLottieAnimationClick = () => {
    setIsExpanded(prevState => !prevState);
  };

  const onClickFunction = ({ onClickType: clickType }: { onClickType: ChecklistOnClick }) => {
    switch (clickType) {
      case ChecklistOnClick.OpenSubscriptionPage:
        onOpenSubscriptionPageClick();
        break;
      case ChecklistOnClick.RequestHostPermission:
        onRequestHostPermissionsClick();
        break;
      case ChecklistOnClick.OpenSidebarSignUp:
        onOpenSidebarSignUpClick();
        break;
      case ChecklistOnClick.ShowLottieAnimation:
      default:
        onShowLottieAnimationClick();
        break;
    }
  };

  return {
    isExpanded,
    onClickFunction,
  };
};
