import React, { FC } from 'react';

import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { useTranslation } from '~/libs/i18n';
import { FeedNotificationLayout } from '~/schema/server';
import { SlideName } from '~/types/sidebar';

import { CTA } from '../Feed/CTA';
import { Description } from '../Feed/Description';
import { Image } from '../Feed/Image';
import { Title } from '../Feed/Title';

interface ChecklistNotificationProps {
  amountCompleted: number;
}

export const ChecklistNotification: FC<ChecklistNotificationProps> = ({ amountCompleted }) => {
  const { t } = useTranslation();
  const isCompleted = amountCompleted === 100;

  return (
    <SidebarSection>
      <Image
        imageURL={`https://public.toucanapi.com/img-checklist-${isCompleted ? 'done' : 'inprogress'}.svg`}
        layout={FeedNotificationLayout.Large}
      />
      <Title>{t('getToKnowToucan')}</Title>
      <Description>{t('exploreKeyFeature')}</Description>
      <CTA slide={SlideName.OnboardingChecklist}>{t('exploreToucanTips')}</CTA>
    </SidebarSection>
  );
};
