/** @jsx jsx */
import React, { useState, useEffect, ChangeEvent } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { useForm, Controller } from 'react-hook-form';

import { Box } from '~/components/Box';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import Config from '~/configs/config';
import { email as emailRegex } from '~/constants/regex';
import { ContentMessageProducer } from '~/contentMessageProducer';
import { resolveErrors } from '~/helpers/errors';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';
import ContentMessengers from '~/messengers/contentMessengers';

import { useAnonymousUserSettings } from '../AnonymousUserExtensionSettingsContext';

import { FormFooter } from './FormFooter';

const { Typography, useColorScheme, Button, TextField, ToucanIcon, Link, Checkbox } = ToucanComponents;

interface SignupInput {
  name: string;
  email: string;
  password: string;
  emailOptIn?: boolean;
}

export const SignUp = () => {
  const [isEmailTaken, setIsEmailTaken] = useState(false);
  const theme = useTheme();
  const {
    register,
    handleSubmit,
    getValues,
    formState: { errors: localErrors },
    control,
  } = useForm<SignupInput>();
  const formValues = getValues();
  const { isDarkModeEnabled, isInPopup } = useAnonymousUserSettings();
  const { t } = useTranslation();
  const { textLevel2, textLevel3 } = useColorScheme();
  const { closeSidebar } = useSidebar();

  let errors = resolveErrors<{
    name?: string;
    email?: string;
    password?: string;
  }>(localErrors);

  useEffect(() => {
    ContentMessengers.trackEvent({
      eventName: 'Registration Screen Viewed',
      properties: {
        isInGuidedOnboarding: false,
      },
    });
  }, []);

  const onSignUp = async (inputs: SignupInput) => {
    setIsEmailTaken(false);
    const message = await ContentMessageProducer.signUpUser(inputs);
    const { data, error: serverError } = message;

    if (serverError) {
      errors = resolveErrors(serverError);
    }

    const messages = Array.isArray(errors.message) ? errors.message : [errors.message];
    if (messages[0] === 'Email was already taken by another user') {
      setIsEmailTaken(true);
    }

    if (data.email && !serverError) {
      ContentMessengers.trackEvent({
        eventName: 'Registration Success',
        properties: {
          isInGuidedOnboarding: false,
        },
      });
      if (isInPopup) {
        window.close();
        // reload page in order to display personalization widget
        ContentMessengers.reloadCurrentPage();
      } else {
        closeSidebar();
      }
    }
  };

  const getPasswordHelperText = () => {
    if (errors.password) {
      return errors.password.message;
    }

    if (formValues.password && formValues.password.length >= 8) {
      return (
        <div
          css={css`
            display: flex;
            align-items: center;
          `}
        >
          <ToucanIcon
            icon="check"
            css={css`
              color: ${theme.palette.success.main};
              width: 12px;
              margin-right: ${theme.spacing(1)};
            `}
          />
          <Typography
            variant="xs"
            css={css`
              color: ${theme.palette.success.main};
            `}
          >
            {t('eightCharsMin')}
          </Typography>
        </div>
      );
    }
    return t('eightCharsMin');
  };

  return (
    <div
      css={css`
        width: 100%;
      `}
    >
      <form
        data-testid="sign-up-form"
        css={css`
          margin-bottom: ${theme.spacing(2)};
        `}
        onSubmit={handleSubmit(onSignUp)}
      >
        <TextField
          label={t('name')}
          hasError={!!errors.name}
          helperText={errors.name && errors.name.message}
          css={css`
            margin-bottom: ${theme.spacing(1)};
          `}
          inputProps={{
            maxlength: 60,
          }}
          {...register('name', {
            required: `${t('pleaseIncludeName')}`,
          })}
          isDarkMode={isDarkModeEnabled}
        />
        <TextField
          label={t('email')}
          hasError={!!(errors.email || isEmailTaken)}
          helperText={errors.email ? errors.email.message : isEmailTaken && t('emailAlreadyInUse')}
          type="email"
          css={css`
            margin-bottom: ${theme.spacing(1)};
          `}
          {...register('email', {
            required: `${t('pleaseEnterValidEmail')}`,
            pattern: {
              value: emailRegex,
              message: t('pleaseEnterValidEmail'),
            },
          })}
          isDarkMode={isDarkModeEnabled}
        />
        <TextField
          type="password"
          label={t('createAPassword')}
          canPasswordBeToggled
          hasError={!!errors.password}
          helperText={getPasswordHelperText()}
          {...register('password', {
            required: t('passwordMustBeEightChars'),
            minLength: {
              value: 8,
              message: t('passwordMustBeEightChars'),
            },
          })}
          css={css`
            & .MuiInputBase-root {
              box-sizing: border-box;
              height: 70px;
            }
          `}
          showCta={t('show')}
          hideCta={t('hide')}
          isDarkMode={isDarkModeEnabled}
          inputProps={{
            autoCapitalize: 'off',
          }}
        />
        <Box mt={2}>
          <Controller
            name="emailOptIn"
            control={control}
            render={({ field }) => (
              <Checkbox
                css={css`
                  align-self: flex-start;
                `}
                label={
                  <React.Fragment>
                    <Typography variant="sm" color={textLevel2}>
                      {t('emailOptInAgreement')}{' '}
                      <Link href={`${Config.siteURL}/privacy`} target="_blank">
                        {t('privacyPolicy')}
                      </Link>
                    </Typography>
                  </React.Fragment>
                }
                onChange={(event: ChangeEvent<HTMLInputElement>) => field.onChange(event.target.checked)}
                checked={field.value}
              />
            )}
          />
        </Box>
      </form>
      <Typography
        variant="xs"
        color={textLevel3}
        css={css`
          margin-bottom: ${theme.spacing(2)};
        `}
      >
        <Trans
          i18nKey="byClickingSignUp"
          components={[
            <Link href={`${Config.siteURL}/terms`} target="_blank" />,
            <Link href={`${Config.siteURL}/privacy`} target="_blank" />,
          ]}
        />
      </Typography>
      <Button
        data-testid="sign-up-form-submit-button"
        size="medium"
        type="utility"
        onClick={handleSubmit(onSignUp)}
        css={css`
          width: 100%;
          margin-bottom: ${theme.spacing(3)};
        `}
      >
        {t('signUp')}
      </Button>
      <FormFooter />
    </div>
  );
};
