import React, { FC } from 'react';

import { ToucanComponents } from '@jointoucan/toucan-design';

import { Box, BoxProps } from '~/components/Box';
import { DensityValuesPrimitive } from '~/constants/settings';
import { useTranslation } from '~/libs/i18n';
import { TranslationKeys } from '~/libs/i18n/types';
import { DifficultyTier } from '~/schema/server';

const { Typography, useColorScheme } = ToucanComponents;

const DescriptionKeyMap: Record<DensityValuesPrimitive, TranslationKeys | Record<DifficultyTier, TranslationKeys>> = {
  0: 'aFewTranslatedWords',
  1: 'aHandfulOfTranslations',
  2: {
    [DifficultyTier.Beginner]: 'aFewTranslatedWords',
    [DifficultyTier.Intermediate]: 'aMixOfIntermediate',
    [DifficultyTier.Advanced]: 'aMixOfAdvancedTranslations',
  },
  3: {
    [DifficultyTier.Beginner]: 'lotsOfTranslations',
    [DifficultyTier.Intermediate]: 'lotsOfIntermediateWords',
    [DifficultyTier.Advanced]: 'lotsOfAdvancedWords',
  },
};

interface DifficultyDescriptionProps extends Omit<BoxProps, 'css'> {
  difficultyTier: DifficultyTier;
  densityLevel: DensityValuesPrimitive;
  isDisabled?: boolean;
}

export const DifficultyDescription: FC<DifficultyDescriptionProps> = ({
  difficultyTier,
  densityLevel,
  isDisabled,
  ...boxProps
}) => {
  const { t } = useTranslation();
  const { textLevel2, textLevel3 } = useColorScheme();

  let translationKey = DescriptionKeyMap[densityLevel];
  if (typeof translationKey === 'object') {
    translationKey = translationKey[difficultyTier];
  }

  return (
    <Box {...boxProps}>
      <Typography variant="xs" color={isDisabled ? textLevel3 : textLevel2}>
        {t(translationKey)}
      </Typography>
    </Box>
  );
};
