/** @jsx jsx */
import React, { FC, useState, useEffect, useMemo } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme, CircularProgress } from '@mui/material';
import { isUndefined } from 'lodash';

import { SectionItemSeparator } from '~/components/Sidebar/SectionItemSeparator';
import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { SidebarItem } from '~/components/Sidebar/SidebarItem';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { FadeIn } from '~/components/shared/FadeIn';
import { browserVendor } from '~/configs/config';
import {
  LanguageSupportMatrix,
  SupportedSourceLanguages,
  supportedSourceLanguageIds,
  popularTargetLanguagesBySourceLanguage,
} from '~/constants/language';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';
import { useChangeLanguageMutation, useForceDatasetDownloadMutation } from '~/schema/background';
import { LanguageId } from '~/schema/server';
import { BrowserVendors } from '~/types/browsers';
import { SlideName } from '~/types/sidebar';

import { LanguageButtonsGrid } from './LanguageButtonsGrid';
import { SourceLanguageSwitcher } from './SourceLanguageSwitcher';

const isMobileSafari = browserVendor === BrowserVendors.SafariiOS;

const { Typography, useColorScheme, Button } = ToucanComponents;

export const ChangeLanguage: FC = () => {
  const { t, i18n } = useTranslation();
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const [forceDatasetDownload] = useForceDatasetDownloadMutation();
  const { sourceLanguageInfo, targetLanguageInfo } = useLanguageInfo();
  const [changeLanguage] = useChangeLanguageMutation();
  const [selectedSourceLanguageId, setSelectedSourceLanguageId] = useState<SupportedSourceLanguages>();
  const [selectedTargetLanguageId, setSelectedTargetLanguageId] = useState<LanguageId | null>();
  const [isLoadingNewDataSet, setIsLoadingNewDataSet] = useState(false);
  const [isNewDataSetLoaded, setIsNewDataSetLoaded] = useState(false);
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const { onNavigateSidebar } = useSidebarContext();
  const { sourceLanguage, targetLanguage } = useUserInfo();

  const isLoadingLastSavedLanguages = !targetLanguage;

  useEffect(() => {
    if (!isLoadingLastSavedLanguages && sourceLanguageInfo && isUndefined(selectedSourceLanguageId)) {
      setSelectedSourceLanguageId(sourceLanguage);
    }

    if (!isLoadingLastSavedLanguages && targetLanguageInfo && isUndefined(selectedTargetLanguageId)) {
      setSelectedTargetLanguageId(targetLanguageInfo.languageId);
    }
  }, [sourceLanguageInfo?.languageId, targetLanguageInfo?.languageId, isLoadingLastSavedLanguages]);

  const sortedTargetLanguagesForSourceLanguage = useMemo(() => {
    if (!selectedSourceLanguageId) {
      return [];
    }
    const mostPopularTargetLanguagesForSourceLanguage =
      popularTargetLanguagesBySourceLanguage[selectedSourceLanguageId];
    return [...LanguageSupportMatrix[selectedSourceLanguageId]].sort((prev, current) => {
      if (!mostPopularTargetLanguagesForSourceLanguage) {
        return 0;
      }

      if (!mostPopularTargetLanguagesForSourceLanguage.includes((current as unknown) as LanguageId)) {
        return 1;
      }
      return (
        mostPopularTargetLanguagesForSourceLanguage.indexOf((prev as unknown) as LanguageId) -
        mostPopularTargetLanguagesForSourceLanguage.indexOf((current as unknown) as LanguageId)
      );
    });
  }, [selectedSourceLanguageId]);

  if (isUndefined(selectedSourceLanguageId) || isUndefined(selectedTargetLanguageId) || isLoadingLastSavedLanguages) {
    return null;
  }

  const onClickSaveChangesButton = async () => {
    if (selectedSourceLanguageId && selectedTargetLanguageId) {
      setIsNewDataSetLoaded(false);
      setIsLoadingNewDataSet(true);
      await i18n.changeLanguage(selectedSourceLanguageId);
      const { errors } = await changeLanguage({
        sourceLanguage: selectedSourceLanguageId,
        targetLanguage: selectedTargetLanguageId,
      });

      if (errors && errors.length > 0) {
        setErrorMessage(errors[0].message);
        setIsLoadingNewDataSet(false);
        return;
      }

      // NOTE: This messages the document that a change has happened with the users language.
      window.document.dispatchEvent(new Event('toucan-change-language'));
      // Note this downloads the dataset a second time.
      await forceDatasetDownload({});
      setIsLoadingNewDataSet(false);
      if (isMobileSafari) {
        setIsNewDataSetLoaded(true);
        return;
      }
      onNavigateSidebar({ slide: SlideName.MainSettings });
    }
  };

  return (
    <React.Fragment>
      <SidebarSection>
        <SlideHeader>{t('changeLanguage')}</SlideHeader>
      </SidebarSection>
      <SidebarSection hasBottomLine={false}>
        <SidebarItem>
          <Trans
            i18nKey="iSpeakLangAndLearn"
            components={[
              <Typography
                variant="sm"
                color={isDarkMode ? ToucanColors.white : ToucanColors.gray[600]}
                isBold
                css={css`
                  display: inline;
                `}
              >
                x
              </Typography>,
              <SourceLanguageSwitcher
                supportedLanguageIds={supportedSourceLanguageIds}
                sourceLanguageId={selectedSourceLanguageId || sourceLanguageInfo?.languageId}
                onChangeSourceLanguage={languageId => {
                  setSelectedSourceLanguageId(languageId as SupportedSourceLanguages);
                  setSelectedTargetLanguageId(null);
                }}
                mx={0.5}
              />,
              <Typography
                variant="sm"
                color={isDarkMode ? ToucanColors.white : ToucanColors.gray[600]}
                isBold
                css={css`
                  display: inline;
                `}
              >
                x
              </Typography>,
            ]}
          />
          <LanguageButtonsGrid
            css={css`
              margin-top: ${theme.spacing(2)};
            `}
            dataSource={sortedTargetLanguagesForSourceLanguage}
            onClickButton={languageId => setSelectedTargetLanguageId(languageId as LanguageId)}
            selectedLanguageId={selectedTargetLanguageId as string}
            testIdBase="target-language-option"
            isReducedSize={sourceLanguage === LanguageId.De}
          />
        </SidebarItem>
        <SectionItemSeparator />
      </SidebarSection>
      <Box flex="0" width="100%" zIndex="3" position="sticky" bottom={0}>
        <SidebarSection
          hasBottomLine={false}
          css={css`
            border-top: 1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]};
            background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
          `}
        >
          <FadeIn show={!!errorMessage}>
            <Typography
              variant="xs"
              css={css`
                margin-bottom: ${theme.spacing(1)};
                color: ${isDarkMode ? ToucanColors.red[200] : ToucanColors.red[300]};
              `}
            >
              {errorMessage}
            </Typography>
          </FadeIn>
          <Button
            type="utility"
            onClick={onClickSaveChangesButton}
            data-testid="save-changes-button"
            css={css`
              width: 100%;
            `}
            isDisabled={
              !selectedSourceLanguageId ||
              !selectedTargetLanguageId ||
              (((sourceLanguageInfo?.languageId as unknown) as LanguageId) === selectedSourceLanguageId &&
                targetLanguageInfo?.languageId === selectedTargetLanguageId)
            }
          >
            {t('saveChanges')}
          </Button>

          <FadeIn show={isLoadingNewDataSet || isNewDataSetLoaded}>
            {isLoadingNewDataSet ? (
              <Box mt={1} display="flex" justifyContent="center" alignItems="center">
                <CircularProgress size={theme.spacing(2)} thickness={5} />
                <Typography
                  variant="xs"
                  color={isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}
                  css={css`
                    margin-left: ${theme.spacing(2)};
                  `}
                >
                  {t('loadingExtensionLibrary')}
                </Typography>
              </Box>
            ) : (
              <Box mt={1} textAlign="center">
                <Typography variant="xs" isBold color={isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}>
                  {selectedTargetLanguageId &&
                    t('yourLanguageHasChangedToLang', {
                      lang: targetLanguageInfo?.language || '',
                    })}
                </Typography>
                <Typography variant="xs" color={isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}>
                  {t('tryRefreshingThePageToUpdate')}
                </Typography>
              </Box>
            )}
          </FadeIn>
        </SidebarSection>
      </Box>
    </React.Fragment>
  );
};
