import React from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Image } from '~/components/shared/Image';
import { Events } from '~/constants/events';
import { StorageKey } from '~/constants/storage';
import { useStorage } from '~/hooks/useStorage';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { MoreLearningShortcuts } from '~/schema/background';

import { Box } from '../../../Box';

const { useColorScheme, Typography, ToucanIcon, Tooltip } = ToucanComponents;

interface LearningShortcutMoreProps {
  moreLearningShortcuts: MoreLearningShortcuts[];
  isShortcutAvailable: boolean;
  onNavigateToDetails: (learningShortcutId: string) => void;
}

export const LearningShortcutMore = ({
  moreLearningShortcuts,
  isShortcutAvailable,
  onNavigateToDetails,
}: LearningShortcutMoreProps) => {
  const theme = useTheme();
  const { backgroundBase, isDarkMode, textLevel1, textLevel2, uiLink, uiLinkHover } = useColorScheme();
  const { t } = useTranslation();
  const [clickCounts, setClickCounts] = useStorage<Record<string, number>>(StorageKey.ShortcutClickCounts, {});

  return (
    <Box>
      {isShortcutAvailable ? (
        <Box display="flex" alignItems="flex-start">
          <Typography variant="sm" isBold color={textLevel1}>
            {t('moreShortcuts')}
          </Typography>
        </Box>
      ) : (
        <Box display="flex" flexDirection="column" alignItems="center" data-testid="no-main-shortcut">
          <Box display="flex" alignItems="center">
            <Typography
              variant="sm"
              isBold
              color={textLevel1}
              css={css`
                margin-right: ${theme.spacing(1)};
              `}
            >
              {t('wantToTakeAShortcut')}
            </Typography>
            <Tooltip title={t('toucanWillPrioritizeTeaching')} placement="top-end" width={200}>
              <ToucanIcon
                icon="info-circle"
                css={css`
                  width: 14px;
                  height: 14px;
                  color: ${uiLink};
                  :hover {
                    color: ${uiLinkHover};
                  }
                  cursor: pointer;
                `}
              />
            </Tooltip>
          </Box>
          <Typography
            variant="xs"
            color={textLevel2}
            css={css`
              margin-top: ${theme.spacing(1)};
            `}
          >
            {t('selectOneBelow')}
          </Typography>
        </Box>
      )}
      {moreLearningShortcuts.length > 0 && (
        <Box
          sx={{
            marginTop: theme.spacing(2),
            display: 'grid',
            gridTemplateColumns: '1fr 1fr',
            columnGap: theme.spacing(1),
            rowGap: theme.spacing(1),
          }}
        >
          {moreLearningShortcuts.map(moreLearningShortcut => (
            <Box
              typeof="button"
              key={moreLearningShortcut.name}
              sx={{
                height: '150px',
                display: 'flex',
                flexDirection: 'column',
                backgroundColor: backgroundBase,
                cursor: 'pointer',
                '&:hover': {
                  backgroundColor: isDarkMode ? ToucanColors.teal[500] : ToucanColors.blue[100],
                  '.hover-effect': {
                    boxShadow: `inset 0 0 0 1px ${isDarkMode ? ToucanColors.teal[400] : ToucanColors.blue[300]}`,
                  },
                },
                '&:active': {
                  '.hover-effect': {
                    boxShadow: `inset 0 0 0 4px ${isDarkMode ? ToucanColors.teal[400] : ToucanColors.blue[300]}`,
                  },
                },
                '.hover-effect': {
                  boxShadow: `inset 0 0 0 1px ${ToucanColors.gray[isDarkMode ? 600 : 200]}`,
                  borderRadius: theme.spacing(0.5),
                },
              }}
              position="relative"
              onClick={() => {
                const prevCount = clickCounts[moreLearningShortcut.learningShortcutId] || 0;
                const updatedCount = prevCount + 1;
                ContentMessengers.trackEvent({
                  eventName: Events.ShortcutClicked,
                  properties: {
                    clickCount: updatedCount,
                    shortcutName: moreLearningShortcut.name,
                  },
                  userOptions: { includePremiumSubscriptionStatus: true },
                });
                setClickCounts({
                  ...clickCounts,
                  [moreLearningShortcut.learningShortcutId]: updatedCount,
                });
                onNavigateToDetails(moreLearningShortcut.learningShortcutId);
              }}
              overflow="hidden"
            >
              <Image
                src={moreLearningShortcut.tileImage.tileImageUrl}
                alt={moreLearningShortcut.tileImage.tileImageAltText}
                css={css`
                  width: 'fit-content';
                  border-top-left-radius: ${theme.spacing(0.5)};
                  border-top-right-radius: ${theme.spacing(0.5)};
                `}
              />
              <Box
                sx={{
                  height: '8px',
                  width: '100%',
                  backgroundColor: isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100],
                }}
              >
                <Box
                  height="8px"
                  width={
                    moreLearningShortcut.percentCompleted > 15 ? `${moreLearningShortcut.percentCompleted}%` : '15%'
                  }
                  borderRadius={moreLearningShortcut.percentCompleted === 100 ? '0px' : '0px 24px 24px 0px'}
                  sx={{
                    backgroundColor: ToucanColors.blue[200],
                  }}
                />
              </Box>
              <Typography
                variant="xs"
                color={textLevel2}
                isBold
                css={css`
                  margin: ${theme.spacing(1)};
                `}
              >
                {moreLearningShortcut.name}
              </Typography>
              <Box
                className="hover-effect"
                sx={{
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  width: '100%',
                  height: '100%',
                }}
              />
            </Box>
          ))}
        </Box>
      )}
    </Box>
  );
};
