/** @jsx jsx */
import React, { FC, memo } from 'react';

import { css, jsx } from '@emotion/react';
import { useTheme } from '@mui/material';

import { useTranslation } from '~/libs/i18n';
import { FeedNotificationLayout } from '~/schema/server';

interface ImageProps {
  imageURL?: string | null;
  layout: string;
}

export const Image: FC<ImageProps> = memo(({ imageURL, layout }) => {
  const theme = useTheme();
  const { t } = useTranslation();

  return (
    <React.Fragment>
      {imageURL && (
        <img
          src={imageURL}
          alt={
            layout === FeedNotificationLayout.Large
              ? t('notificationsLargeNotification')
              : t('notificationsMediumNotification')
          }
          width={layout === FeedNotificationLayout.Large ? '314px' : '80px'}
          height={layout === FeedNotificationLayout.Large ? '176px' : '80px'}
          css={css`
            margin-top: ${theme.spacing(layout === FeedNotificationLayout.Large ? 1 : 0)};
            margin-bottom: ${theme.spacing(layout === FeedNotificationLayout.Large ? 2 : 0)};
          `}
        />
      )}
    </React.Fragment>
  );
});
