import React, { FC } from 'react';

import { ToucanComponents } from '@jointoucan/toucan-design';

const { Typography, useColorScheme } = ToucanComponents;

export const Title: FC = ({ children }) => {
  const { textLevel1 } = useColorScheme();

  return (
    <Typography variant="md" font="secondary" color={textLevel1}>
      {children}
    </Typography>
  );
};
