/** @jsx jsx */
import React, { useRef } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { SidebarItem } from '~/components/Sidebar/SidebarItem';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { SlideHeader } from '~/components/Sidebar/SlideHeader';
import { StorageKey } from '~/constants/storage';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useStorage } from '~/hooks/useStorage';
import { useToast } from '~/hooks/useToast';
import { useGetExperimentsQuery } from '~/schema/background';

const { Typography, useColorScheme, Button } = ToucanComponents;

export const ExperimentInformation = () => {
  const { isDarkMode, textLevel1, textLevel3, textLevel4 } = useColorScheme();
  const { data } = useGetExperimentsQuery({});
  const { experimentName, variation, config } = usePremiumExperiment();
  const [, setCustomPremiumExperimentConfig] = useStorage<string | null>(
    StorageKey.CustomPremiumExperimentConfig,
    null,
  );
  const { showToast, removeToast } = useToast();
  const editableContainerRef = useRef<HTMLDivElement>(null);

  return (
    <React.Fragment>
      <SidebarSection>
        <SlideHeader>Experiment Information</SlideHeader>
      </SidebarSection>
      <SidebarSection hasBottomLine={false}>
        <Box
          bgcolor={isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}
          p={2}
          pl={4}
          mb={2}
          ml={-4}
          mt={-2}
          mr={-4}
        >
          <Typography variant="sm" isBold color={textLevel4}>
            Configuration applied
          </Typography>
        </Box>
        <SidebarItem>
          <Typography variant="sm" isBold color={textLevel1}>
            Experiment Name
          </Typography>
          <Typography variant="sm" color={textLevel3}>
            {experimentName || 'Custom config being applied'}
          </Typography>
        </SidebarItem>

        <SidebarItem>
          <Typography variant="sm" isBold color={textLevel1}>
            Variation
          </Typography>
          <Typography variant="sm" color={textLevel3}>
            {variation || 'Custom config being applied'}
          </Typography>
        </SidebarItem>

        <SidebarItem>
          <Typography variant="sm" isBold color={textLevel1}>
            Config
          </Typography>
          <div
            id="editable-config"
            css={css`
              border: 1px solid #ccc;
              overflow-x: hidden;
              overflow-y: auto;
            `}
            contentEditable
            ref={editableContainerRef}
          >
            <pre
              css={css`
                background-color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]};
                padding: 16px;
                font-size: 14px;
                overflow-x: auto;
                color: ${textLevel4};
                margin-top: 0;
                margin-bottom: 0;
              `}
            >
              {JSON.stringify(config, null, 2)}
            </pre>
          </div>
          <Box mt={2} display="flex" gap={2}>
            <Button
              size="small"
              css={css`
                padding-left: 24px;
                padding-right: 24px;
              `}
              onClick={() => {
                const updatedConfigText = editableContainerRef.current?.textContent;
                if (updatedConfigText) {
                  try {
                    // Parse the text content to ensure that it is formatted correctly
                    JSON.parse(updatedConfigText);
                  } catch (error) {
                    const toastId = 'updated-experiment-config-error';
                    showToast({
                      id: toastId,
                      title: 'The experiment config is not formatted correctly',
                      subtitle: 'Please check the config and try again',
                      image: '/images/toucan-sad.svg',
                      onClick: () => {
                        removeToast(toastId);
                      },
                      onClose: () => {
                        removeToast(toastId);
                      },
                      timeout: 3000,
                    });
                    return;
                  }

                  setCustomPremiumExperimentConfig(updatedConfigText);
                  const toastId = 'updated-experiment-config';
                  showToast({
                    id: toastId,
                    title: 'The experiment config has been updated',
                    subtitle: 'Please refresh to see the changes',
                    image: '/images/toucan-circle.svg',
                    onClick: () => {
                      removeToast(toastId);
                    },
                    onClose: () => {
                      removeToast(toastId);
                    },
                    timeout: 3000,
                  });
                }
              }}
            >
              Save config
            </Button>
            <Button
              size="small"
              type="secondary"
              css={css`
                padding-left: 24px;
                padding-right: 24px;
              `}
              onClick={() => {
                setCustomPremiumExperimentConfig(null);
                const toastId = 'reset-experiment-config';
                showToast({
                  id: toastId,
                  title: 'The experiment config has been reset',
                  subtitle: 'Please refresh to see the changes',
                  image: '/images/toucan-circle.svg',
                  onClick: () => {
                    removeToast(toastId);
                  },
                  onClose: () => {
                    removeToast(toastId);
                  },
                  timeout: 3000,
                });
              }}
            >
              Reset config
            </Button>
          </Box>
        </SidebarItem>
      </SidebarSection>

      <SidebarSection hasBottomLine={false}>
        <Box
          bgcolor={isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}
          p={2}
          pl={4}
          mb={2}
          ml={-4}
          mt={-2}
          mr={-4}
        >
          <Typography variant="sm" isBold color={textLevel4}>
            Amplitude experiments
          </Typography>
        </Box>
        {data?.experiments.map(experiment => (
          <SidebarItem key={experiment.experiment}>
            <Typography variant="sm" isBold color={textLevel1}>
              {experiment.experiment}
            </Typography>
            <Typography variant="sm" color={textLevel3}>
              {experiment.variation}
            </Typography>
          </SidebarItem>
        ))}
      </SidebarSection>
    </React.Fragment>
  );
};
