import React, { FC } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { FadeIn } from '~/components/shared/FadeIn';
import { Language } from '~/constants/language';
import { useTranslation } from '~/libs/i18n';
import { TranslationKeys } from '~/libs/i18n/types';
import { ProgressionPeriod, ProgressionType } from '~/schema/background';

import { HelpfulTips } from './HelpfulTips';
import { ProgressionItem } from './ProgressionItem';
import { TopPercent } from './TopPercent';

interface ProgressOverviewProps {
  topPercentage: number;
  targetLanguage: Language;
  period: ProgressionPeriod;
  progressionItems: Array<{
    amount: number;
    displayName: string;
    icon: string;
    iconColor: string;
    iconFillColor: string;
    targetPhrases: Array<string>;
    progressionType: ProgressionType;
  }>;
  // info for helpers
  difficultyLevel: number;
  isGloballyEnabled: boolean;
  hasGlobalPermissions: boolean;
  onRequestPermissions: () => Promise<boolean>;
  onEnableGlobally: () => void;
  onChangeDifficulty: () => void;
  onItemClicked: (progressionType: ProgressionType) => void;
}

const { Typography } = ToucanComponents;

export const ProgressOverview: FC<ProgressOverviewProps> = ({
  topPercentage,
  targetLanguage,
  progressionItems,
  period,
  difficultyLevel,
  isGloballyEnabled,
  hasGlobalPermissions,
  onRequestPermissions,
  onEnableGlobally,
  onChangeDifficulty,
  onItemClicked,
}) => {
  const { t, i18n } = useTranslation();
  const showProgressionItems = progressionItems.length > 0;

  return (
    <FadeIn show>
      <TopPercent percent={topPercentage} targetLanguage={targetLanguage} period={period} />
      {showProgressionItems ? (
        <>
          <Box py={2}>
            <Typography variant="sm" isBold color={ToucanColors.white}>
              {t('yourRecentWords')}
            </Typography>
          </Box>{' '}
          {progressionItems.map(
            ({ amount, displayName, targetPhrases, icon, iconFillColor, iconColor, progressionType }) => (
              <ProgressionItem
                key={displayName}
                title={t(displayName as TranslationKeys, { amount: amount.toLocaleString(i18n.sourceLanguage) })}
                subTitle={targetPhrases.join(', ')}
                icon={icon}
                iconColor={iconColor}
                iconFillColor={iconFillColor}
                onItemClicked={onItemClicked}
                progressionType={progressionType}
              />
            ),
          )}
        </>
      ) : (
        <HelpfulTips
          difficultyLevel={difficultyLevel}
          isGloballyEnabled={isGloballyEnabled}
          hasGlobalPermissions={hasGlobalPermissions}
          onRequestPermissions={onRequestPermissions}
          onEnableGlobally={onEnableGlobally}
          onChangeDifficulty={onChangeDifficulty}
        />
      )}
    </FadeIn>
  );
};
