import React, { FC } from 'react';

import { SerializedStyles } from '@emotion/react';
import { BoxProps } from '@mui/material';

import { Box } from '~/components/Box';

import { useSidebarContext } from '../../SidebarContext';

interface SlidesProps extends Omit<BoxProps, 'css' | 'ref'> {
  css?: SerializedStyles;
}

/**
 * Container for slides
 * It sets up an animation container to be used to transition slides
 */
export const ProgressSlides: FC<SlidesProps> = ({ children, ...boxProps }) => {
  const { activeSlideName } = useSidebarContext();
  const childrenWithActiveSlideName = React.Children.toArray(children).map(child => {
    if (React.isValidElement(child)) {
      return React.cloneElement(child, { ...child.props, activeSlideName });
    }
    return child;
  });

  return <Box {...boxProps}>{childrenWithActiveSlideName}</Box>;
};
