/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Language } from '~/constants/language';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';

import { Box } from '../../../Box';

const { useColorScheme, Typography, Tooltip, ToucanIcon, ProgressBar, Button } = ToucanComponents;

interface LearningShortcutHeaderProps {
  onNavigateToDetails: (learningShortcutId: string) => void;
  title: string;
  percentCompleted: number;
  numberOfWordsLearnedToday: number;
  learningShortcutId: string;
  completedLearningShortcutPhraseCount: number;
  possibleLearningShortcutPhraseCount: number;
}

export const LearningShortcutHeader: FC<LearningShortcutHeaderProps> = ({
  onNavigateToDetails,
  title,
  percentCompleted,
  numberOfWordsLearnedToday,
  learningShortcutId,
  completedLearningShortcutPhraseCount,
  possibleLearningShortcutPhraseCount,
}) => {
  const theme = useTheme();
  const { textLevel1, textLevel2, textLevel3, uiLinkActive, uiLink, uiLinkHover } = useColorScheme();
  const { t } = useTranslation();
  const { targetLanguageInfo } = useLanguageInfo();

  return (
    <Box marginBottom={theme.spacing(1.5)} data-testid="shortcut-selected">
      <Box display="flex" flexDirection="column" alignItems="center">
        <Box display="flex" alignItems="center">
          <Typography
            variant="xs"
            color={textLevel3}
            css={css`
              margin-right: ${theme.spacing(1)};
            `}
          >
            {/* @ts-ignore TODO: values need to be casted */}
            {t('yourShortcut', { language: t(targetLanguageInfo?.language ?? Language.Spanish) })}
          </Typography>
          <Tooltip title={t('toucanWillPrioritizeTeaching')} placement="top-end" width={200}>
            <ToucanIcon
              icon="info-circle"
              css={css`
                width: 14px;
                height: 14px;
                color: ${uiLink};
                :hover {
                  color: ${uiLinkHover};
                }
                cursor: pointer;
              `}
            />
          </Tooltip>
        </Box>
        <Typography
          variant="md"
          isBold
          font="secondary"
          color={textLevel1}
          css={css`
            margin-top: ${theme.spacing(1)};
            text-align: center;
          `}
        >
          {title}
        </Typography>
        {!!numberOfWordsLearnedToday && (
          <Box
            borderRadius="16px"
            mt={1}
            sx={{
              backgroundColor: ToucanColors.green[100],
            }}
          >
            <Typography
              variant="2xs"
              css={css`
                color: ${ToucanColors.green[500]};
                margin-right: ${theme.spacing(2)};
                margin-left: ${theme.spacing(2)};
                margin-top: ${theme.spacing(0.5)};
                margin-bottom: ${theme.spacing(0.5)};
              `}
            >
              {t('niceWorkToday', {
                value: numberOfWordsLearnedToday,
                word: numberOfWordsLearnedToday === 1 ? t('word') : t('words'),
              })}
            </Typography>
          </Box>
        )}
      </Box>
      <ProgressBar
        beginPercentage={0}
        endPercentage={percentCompleted > 15 ? percentCompleted : 15}
        barColor={ToucanColors.blue[200]}
        barCompleteTransitionColor={ToucanColors.blue[200]}
        css={css`
          margin-top: ${theme.spacing(2)};
          height: 24px;
        `}
      />
      <Box display="flex" alignItems="center" justifyContent="space-between" mt={2}>
        <Box display="flex">
          <Typography
            variant="xs"
            isBold
            css={css`
              color: ${textLevel2};
            `}
          >
            {completedLearningShortcutPhraseCount}&nbsp;
          </Typography>
          <Typography
            variant="xs"
            css={css`
              color: ${textLevel3};
            `}
          >
            {t('wordsAndPhrases', { totalWords: possibleLearningShortcutPhraseCount })}
          </Typography>
        </Box>
        <Box
          css={css`
            svg {
              margin-left: ${theme.spacing(0.5)};
              vertical-align: middle;
              height: 10px;
            }
            path {
              fill: ${uiLink};
            }
            :hover {
              path {
                fill: ${uiLinkHover};
              }
            }
            :active {
              path {
                fill: ${uiLinkActive};
              }
            }
          `}
        >
          <Typography variant="xs" isBold>
            <Button
              type="text"
              css={css`
                display: flex;
                align-items: center;
              `}
              onClick={() => onNavigateToDetails(learningShortcutId)}
            >
              {t('viewAll')}
              <ToucanIcon icon="chevron-right" />
            </Button>
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};
