/** @jsx jsx */
import React, { ChangeEvent, FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { SlideName } from '~/types/sidebar';

import { useSidebarContext } from '../../SidebarContext';
import { SidebarItemSeparated } from '../../SidebarItem';

const { Typography, Switch, Button, useColorScheme, ToucanIcon } = ToucanComponents;

interface AllowOnSiteProps {
  onAddDomainToBanlist: (domain: string) => Promise<void>;
  onRemoveDomainFromBanlist: (domain: string) => Promise<void>;
  onRequestDomainPermissions: () => Promise<void>;
  isBanned: boolean;
  domain: string;
  unbannable: boolean;
  hasDomainPermissions: boolean;
}

export const AllowOnSite: FC<AllowOnSiteProps> = ({
  unbannable,
  isBanned,
  hasDomainPermissions,
  onAddDomainToBanlist,
  onRemoveDomainFromBanlist,
  onRequestDomainPermissions,
  domain,
}) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { textError, textLevel1 } = useColorScheme();
  const { onNavigateSidebar } = useSidebarContext();

  const navigateToBlockedSites = () => {
    onNavigateSidebar({ slide: SlideName.BlockedSites });
  };

  const onAllowOnSiteSwitchChange = async (event: ChangeEvent<HTMLInputElement>) => {
    const isSwitchOn = event.target.checked;
    event.persist();
    if (!hasDomainPermissions) {
      await onRequestDomainPermissions();
    }

    if (isSwitchOn) {
      await onRemoveDomainFromBanlist(domain);
    } else {
      await onAddDomainToBanlist(domain);
    }

    const eventName = isSwitchOn ? 'Translations Resumed on a Site Level' : 'Translations Paused on a Site Level';
    const properties = {
      URL: domain,
    };
    ContentMessengers.trackEvent({ eventName, properties });
  };

  const hasTranslations = !isBanned && hasDomainPermissions;

  return (
    <React.Fragment>
      <div data-testid="allow-site">
        <SidebarItemSeparated
          leftContent={
            <React.Fragment>
              <Typography
                variant="sm"
                isBold
                css={css`
                  color: ${textLevel1};
                `}
              >
                {t('allowOnSite', { site: domain || t('thisSite') })}
              </Typography>
              <Typography variant="xs">
                <Button type="text" onClick={navigateToBlockedSites}>
                  {t('seeBlockedSites')}
                </Button>
              </Typography>
              {unbannable && (
                <Box display="flex" mt={1} gap={`${theme.spacing(1)}`}>
                  <ToucanIcon
                    icon="exclamation-circle"
                    css={css`
                      color: ${textError};
                      margin-top: ${theme.spacing(0.5)};
                    `}
                  />
                  <Typography
                    variant="xs"
                    css={css`
                      color: ${textError};
                    `}
                  >
                    {t('sorryToucanNoWorkOnPageYet')}
                  </Typography>
                </Box>
              )}
            </React.Fragment>
          }
          rightContent={
            <Switch
              value="switch"
              isDisabled={unbannable}
              isChecked={hasTranslations}
              onChange={onAllowOnSiteSwitchChange}
              css={css`
                margin-right: 0;
              `}
            />
          }
        />
      </div>
    </React.Fragment>
  );
};
